<template>
  <ASelect :value="$props.modelValue" v-bind="selectProps">
    <ASelectOption v-for="contact of contacts" :value="contact.id" :key="contact.id">
      {{ contact.fullName }}
    </ASelectOption>
  </ASelect>
</template>

<script lang="ts">
import { ContactsService } from '@/services';
import { createDebounce } from '@/utils/functions';
import { computed, defineComponent, ref, watchEffect } from 'vue';

export default defineComponent({
  props: {
    modelValue: Number,
    style: [String, Object],
    placeholder: {
      type: String,
      default: 'Select Contact',
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    disabled: Boolean,
  },
  emits: ['update:modelValue', 'change'],
  inheritAttrs: false,
  setup(props, { emit }) {
    const debounce = createDebounce();
    const contacts = ref<any[]>([]);

    let defaultContacts: any[] = [];

    /** Search contacts */
    const searchContact = async (val = '', delay = 500) => {
      // Clear whitespaces
      val = val.trim();

      // Empty string, set default contacts to list
      if (!val && defaultContacts.length) {
        contacts.value = defaultContacts;
        return;
      }

      // Execute search
      debounce(async () => {
        const filter = [];

        if (val) {
          filter.push({
            field: 'fullName',
            value: val,
            condition: 'like',
          });
        }

        const results: any = await ContactsService.search({
          limit: 6,
          filter,
        });

        contacts.value = results;

        if (!defaultContacts.length) {
          defaultContacts = results;
        }
      }, delay);
    };

    const getContact = async (contactId: number) => {
      try {
        const [contact] = await ContactsService.search({
          limit: 1,
          filter: [
            {
              field: 'id',
              value: contactId,
            },
          ],
        });

        return contact;
      } catch (error: any) {
        console.error(error);
      }
    };

    /** Update parent value */
    const onChange = (value: string | number) => {
      emit('update:modelValue', value);
      emit('change', value);
    };

    const selectProps = computed(() => {
      const _props = {
        placeholder: props.placeholder,
        filterOption: false,
        style: props.style,
        disabled: props.disabled,
      } as any;

      if (props.readonly) {
        return {
          ..._props,
          showSearch: false,
          allowClear: false,
          open: false,
        };
      }

      return {
        ..._props,
        showSearch: true,
        allowClear: true,
        filterOption: false,
        onSearch: searchContact,
        onChange: onChange,
      };
    });

    // Load default values
    searchContact('', 0);

    watchEffect(async () => {
      if (!props.modelValue || !contacts.value.length) {
        return;
      }

      const contactId = props.modelValue;
      const exists = contacts.value.some(contact => contact.id === contactId);

      if (exists) return;
      // Load from backed
      const contact = await getContact(contactId);
      // Insert to options
      contacts.value.push(contact);
    });

    return { contacts, selectProps };
  },
});
</script>
