
import { computed, defineComponent, inject, nextTick, provide, reactive, ref, watch } from 'vue';
import { CalendarOutlined, SettingOutlined } from '@ant-design/icons-vue';
import { CalendarSettingsSymbol, ModalSettingsSymbol } from '@/utils/symbols';
import timezones from '@/utils/timezones.json';
import ManageCalendar from './ManageCalendar.vue';
import { ChangeCalendarData } from '@/types/calendar.type';
import { calendarService } from '@/services';
import { useCalendarSources } from '@/composables';
import { notification } from 'ant-design-vue';

export default defineComponent({
  components: { CalendarOutlined, SettingOutlined, ManageCalendar },
  props: {
    isDisplay: Boolean,
    timezoneAutofocus: Boolean,
  },
  emits: ['changeSource', 'createCalendar', 'removeCalendar'],
  setup(props, { emit }) {
    const { updateSource } = useCalendarSources();
    const settings = inject(CalendarSettingsSymbol)!;

    const tab = ref('general');

    provide(ModalSettingsSymbol, {
      isDisplay: computed(() => props.isDisplay),
      activeTab: tab,
    });

    const refCalendarForm = ref<any>({});
    const refTimezones = ref<any>(null);

    const calendar = reactive({
      name: '',
      color: '',
    });

    // Set autofocus to timezone select
    watch(
      () => props.isDisplay,
      isDisplay => {
        if (!props.timezoneAutofocus || !isDisplay) {
          return;
        }

        tab.value !== 'general' && (tab.value = 'general');

        nextTick(() => {
          refTimezones.value.focus();
        });
      },
      { immediate: true },
    );

    const onChangeCalendarData = async ({ data, source, oldSource }: ChangeCalendarData) => {
      try {
        // Update calendar in API
        await calendarService.updateCalendar(source.id, data);
        // Update source in local data
        updateSource(source.id, data);

        emit('changeSource', { source, oldSource });

        notification.success({
          message: 'Calendar updated!',
          placement: 'bottomRight',
        });
      } catch (error: any) {
        console.error(error);
      }
    };

    return {
      refCalendarForm,
      refTimezones,
      settings,
      timezones,
      calendar,
      tab,
      onChangeCalendarData,
      onCreateCalendar: () => emit('createCalendar'),
      onRemoveCalendar: (id: string) => emit('removeCalendar', id),
    };
  },
});
