
import { defineComponent, onMounted, ref } from 'vue';
import Calendar from '@/components/Calendar.vue';
import { primaApi } from './api';
import { PrimaHttpHeader } from './utils/enums/http.enum';
import { useCalendarSources, useGlobalProps, useModeFilter, useUsers } from './composables';
import { calendarService, ContactsService } from './services';
import { CalendarMode, CalendarOriginalSource } from './types/calendar.type';
import { CalendarColors } from './utils/enums/colors.enum';
import { ModalBackground } from './types/types';

export default defineComponent({
  name: 'App',
  components: {
    Calendar,
  },
  setup() {
    const { setWhoami, setUsers } = useUsers();
    const { isLoading, setAwaitLoading } = useGlobalProps();
    const { setSources } = useCalendarSources();
    const {
      activeModeFilter,
      setFilterData,
      setContactData,
      isModeFilter,
      contactData,
      setCasesInCache,
      setContactInCache,
    } = useModeFilter();

    const bgModal = ref<ModalBackground>('active');

    /** Params for list events */
    const showCalendar = ref(false);
    const hasError = ref(false);

    const hasUserCalendars = (userId: number, calendars: CalendarOriginalSource[]) => {
      return calendars.some(calendar => calendar.userId === userId);
    };

    const fetchData = async () => {
      try {
        setAwaitLoading(true);

        const { calendars, whoami, users } = await calendarService.getCalendars();
        setWhoami(whoami);
        setUsers(users);

        // Create default calendars
        if (!hasUserCalendars(whoami.id, calendars)) {
          const [personal, toDo, appointments] = await Promise.all([
            calendarService.createCalendar({
              name: 'Personal',
              isDefault: true,
              color: CalendarColors.Blush,
            }),

            calendarService.createCalendar({
              name: 'To Do',
              isDefault: false,
              color: CalendarColors.LapisLazuli,
            }),

            calendarService.createCalendar({
              name: 'Appointments',
              isDefault: false,
              color: CalendarColors.ForestGreenCrayola,
            }),
          ]);

          calendars.unshift(personal);
          calendars.push(toDo, appointments);
        }

        setSources(calendars, isModeFilter.value);
        showCalendar.value = true;
      } catch (error: any) {
        console.error(error);
        hasError.value = true;
      } finally {
        setAwaitLoading(false);
      }
    };

    const getParams = async (url: URL) => {
      const bgParam = url.searchParams.get('modal-bg') as ModalBackground;
      const modeParam = url.searchParams.get('mode') as CalendarMode;
      bgModal.value = !bgParam || !['active', 'blank'].includes(bgParam) ? 'active' : bgParam;

      if (modeParam === 'filter') {
        let contactId = url.searchParams.get('contact');
        let caseId = url.searchParams.get('case');

        if (!caseId && !contactId) {
          throw new Error(`[Filter Mode]: The params "contact" or "case" are required!`);
        }

        if (caseId && !contactId) {
          throw new Error(`[Filter Mode]: The param "contact" is required`);
        }

        if (isNaN(contactId as any) || (caseId && isNaN(caseId as any))) {
          throw new Error(`[Filter Mode]: The params "contact" and "case" should be a numbers!`);
        }

        activeModeFilter(true);

        const field = caseId ? 'caseId' : 'contactId';
        const value = field === 'caseId' ? caseId : contactId;

        setFilterData(field, Number(value));
        setContactData({ id: Number(contactId), caseId: Number(caseId) });
      }
    };

    const onCreated = async () => {
      const url = new URL(window.location.href);

      getParams(url);

      const baseURL = url.searchParams.get('base-url');
      const token = url.searchParams.get('token');
      // const token = localStorage.getItem('token');

      baseURL && primaApi.setBaseURL(baseURL);

      if (token) {
        primaApi.setToken({ [PrimaHttpHeader.TOKEN]: token });

        fetchData();
      } else {
        hasError.value = true;

        return;
      }

      if (!isModeFilter.value) {
        return;
      }

      const getCases = !!contactData.value!.caseId;

      const [result] = await ContactsService.search(
        { filter: [{ field: 'id', value: contactData.value!.id }] },
        getCases,
      );

      if (!result) {
        throw new Error(`Contact not found`);
      }

      const { cases = [], ...contact } = result;

      setCasesInCache(cases);
      setContactInCache(contact);
    };

    onMounted(() => {
      if (bgModal.value === 'blank') {
        document.body.classList.add('blank-modal');
      }
    });

    onCreated();

    return { showCalendar, isLoading, hasError };
  },
});
