import { time, timezone } from '@/composables/use-time';
import {
  Byweekday,
  CalendarDay,
  CalendarDayNumeric,
  CalendarEvent,
  CalendarEventDuration,
  CalendarEventDurationExtended,
  CalendarEventPosition,
  CalendarRecurringEvent,
  EventFrequencyType,
  FormattedEventDatesParams,
  GetRecurrenceNewMonthParams,
  GetRuleMatchesParams,
  UpdateDateFromDuration,
  WeekDayMinType,
} from '@/types/calendar.type';
import { DAYS, FORMAT_EVENT } from '@/utils/constants';
import {
  isArrayEmpty,
  cloneObject,
  isoDateWithoutTime,
  removeObjectProps,
  removeNullableProps,
  isArrayAndHasItems,
  isUndefined,
} from '@/utils/functions';
import moment, { Moment } from 'moment';
import { RRule } from 'rrule';

/** Format event date according "allDay" field value */
export const formatEventDayByAllDay = (date: string, allDay: boolean) => {
  if (allDay) {
    return moment.utc(date).format('YYYY-MM-DD');
  }

  return date;
};

/** Get new start month of recurrence (Monthly by day) */
export const getRecurrenceNewMonth = ({
  dateAbsulute,
  eventNewPosition,
  eventOldPosition,
}: GetRecurrenceNewMonthParams) => {
  const oldMonth = new Date(eventOldPosition).getUTCMonth();
  const newMonth = new Date(eventNewPosition).getUTCMonth();

  if (oldMonth === newMonth) {
    return new Date(dateAbsulute).getUTCMonth();
  }

  return moment
    .utc(dateAbsulute)
    .add(newMonth - oldMonth, 'months')
    .get('months');
};

export const isRecurrenceMonthlyByDay = (rrule: CalendarRecurringEvent) => {
  return rrule.byweekday !== undefined && rrule.freq === 'MONTHLY';
};

export const dayStringToNumeric = (weekday: WeekDayMinType) => {
  const day = DAYS.find(item => item.min === weekday)!;

  return day.numeric.rrule;
};

export const dayNumericToString = (weekday: number, type: keyof CalendarDayNumeric = 'rrule') => {
  const day = DAYS.find(item => item.numeric[type] === weekday)!;

  return day.min;
};

export const getFullDay = (weekday: WeekDayMinType): CalendarDay => {
  const day = DAYS.find(item => item.min === weekday)!;

  return { ...day };
};

export function byweekdayPropToStr(byweekday: Byweekday<number | WeekDayMinType>[]) {
  return byweekday.map((item: Byweekday<number | WeekDayMinType>) => {
    if (typeof item.weekday === 'string') {
      return item.weekday;
    }

    return dayNumericToString(item.weekday);
  });
}

/** Transform "byweekday" from FullCalendar to backend notation */
export const byweekdayFromUIToBackend = (rrule: CalendarRecurringEvent) => {
  let { byweekday } = rrule!;

  if (!byweekday) {
    return rrule;
  }

  (byweekday as any) = (rrule!.byweekday! as any[]).map(item => {
    // Set string literal
    if (rrule!.freq !== 'MONTHLY') {
      return { weekday: item };
    }

    // Transform 0 -> MO
    return { ...item, weekday: dayNumericToString(item.weekday) };
  });

  rrule!.byweekday = byweekday;

  return rrule;
};

/** Transform "byweekday" from backend notation to FullCalendar */
export const byweekdayFromBackendToUI = (rrule: CalendarRecurringEvent) => {
  let { byweekday } = rrule!;

  if (!byweekday || (Array.isArray(byweekday) && !byweekday.length)) {
    // if (!byweekday) {
    return rrule;
  }

  (byweekday as any) = (rrule!.byweekday! as Byweekday[]).map(item => {
    // Set string literal
    if (rrule!.freq !== 'MONTHLY') {
      return (item as Byweekday).weekday;
    }

    // Transform MO -> 0
    return { ...(item as Byweekday), weekday: dayStringToNumeric((item as Byweekday).weekday) };
  });

  rrule!.byweekday = byweekday;

  return rrule;
};

export function formatEventToFullCalendar(event: CalendarEvent) {
  // Recurring events can't be resized

  // All day event
  if (event.allDay) {
    return event;
  }

  if (!event.rrule) {
    event.start = moment.tz(event.start!, event.timezone).format(FORMAT_EVENT);
    event.end = moment.tz(event.end!, event.timezone).format(FORMAT_EVENT);
    return event;
  }

  // Format dates
  const newEvent = cloneObject(event);

  newEvent.rrule!.dtstart = moment.tz(newEvent.rrule!.dtstart, event.timezone).format(FORMAT_EVENT);

  if (newEvent.rrule!.until) {
    newEvent.rrule!.until = moment.tz(newEvent.rrule!.until, event.timezone).format(FORMAT_EVENT);
  }

  if (isArrayAndHasItems(newEvent.exdate)) {
    newEvent.exdate = newEvent.exdate!.map(d => moment.tz(d, event.timezone).format(FORMAT_EVENT));
  }

  return newEvent;
}

/** Transform backend event to fullcalendar event allowed */
export function formatBackendEvent(event: CalendarEvent): CalendarEvent {
  const isRecurringEvent = !!event.rrule;
  let propsToRemove: string[] = [];

  if (isRecurringEvent) {
    propsToRemove = ['start', 'end'];

    if (event.allDay) {
      event.exdate = event.exdate!.map(d => isoDateWithoutTime(d));
    }

    event.rrule = byweekdayFromBackendToUI(event.rrule!);
  } else {
    propsToRemove = ['duration', 'rrule', 'exdate'];
  }

  /** Event object according to his type */
  let eventObject = removeObjectProps(event, ...propsToRemove) as CalendarEvent;
  // Remove null properties
  eventObject = removeNullableProps(eventObject);

  // Sanitize "rrule" properties
  if (isRecurringEvent) {
    eventObject.rrule = removeNullableProps(eventObject.rrule);
  }

  if (!eventObject.allDay) {
    return eventObject;
  }

  return formatEventDates(eventObject);
}

export const getCurrentAvailableTimeRange = (now: Moment) => {
  let minutes = now.get('minutes');

  while (minutes % 5 !== 0) {
    minutes++;
  }

  now.set({
    minutes,
    seconds: 0,
    milliseconds: 0,
  });

  return {
    start: now,
    end: now.clone().add(1, 'hours'),
  };
};

/** Convert date string to ISO  */
export const convertOffsetTimeToUTC = (strDate: string): string => {
  return new Date(strDate).toISOString();
};

export function formatEventDates(event: CalendarEvent) {
  const isRecurringEvent = !!event.rrule;

  // Normal event
  if (!isRecurringEvent) {
    event.start = isoDateWithoutTime(event.start!);
    event.end = isoDateWithoutTime(event.end!);

    return event;
  }

  // Recurring event
  event.rrule!.dtstart = isoDateWithoutTime(event.rrule!.dtstart);

  if (event.rrule!.until) {
    event.rrule!.until = isoDateWithoutTime(event.rrule!.until);
  }

  return event;
}

/** Convert calendar frequency to RRule valid value **/
export const freqToRRuleFrequency = (frequency: EventFrequencyType) => {
  return RRule.FREQUENCIES.findIndex(freq => freq === frequency);
};

export const getRuleDates = (event: CalendarEvent, between?: { start: string; end: string }) => {
  if (!event.rrule) {
    throw new Error('Event is not recurring');
  }

  const { rrule } = event;
  const freq = freqToRRuleFrequency(rrule.freq as EventFrequencyType);

  const options: any = {
    count: rrule.count,
  };

  if (rrule.until && typeof rrule.count === 'undefined') {
    options.until = moment.utc(rrule.until).toDate();
  }

  if (rrule.byweekday && !isArrayEmpty(rrule.byweekday)) {
    const newRRule = byweekdayFromBackendToUI(cloneObject(event.rrule));

    options.byweekday = newRRule.byweekday;
  }

  const rule = new RRule({
    freq,
    interval: rrule.interval,
    dtstart: moment.utc(rrule.dtstart).toDate(),
    ...options,
  });

  let dates = [];

  if (between) {
    dates = rule.between(moment.utc(between.start).toDate(), moment.utc(between.end).toDate());
  } else {
    dates = rule.all();
  }

  return dates.map(d => d.toISOString());
};

export const getFormattedRuleDates = (params: FormattedEventDatesParams) => {
  const { realStart, offset, event } = cloneObject(params);

  const dates = getRuleDates(
    { ...event, rrule: { ...event.rrule!, dtstart: realStart } },
    params.between,
  );

  return dates.map(date => {
    const local = date.substr(0, 19);

    return new Date(`${local}${offset}`).toISOString();
  });
};

export const getDatesDaysDiff = (date1: string | Moment, date2: string | Moment) => {
  date1 = isoDateWithoutTime(date1);
  date2 = isoDateWithoutTime(date2);

  return moment(date2).diff(date1, 'days');
};

export const getRecurrenceDatesRange = (recurringEvent: CalendarEvent) => {
  if (!recurringEvent.rrule) {
    throw new Error(`Event should be recurring`);
  }

  const { until, dtstart } = recurringEvent.rrule;

  const betweenDates = {
    start: moment.utc(dtstart).subtract(1, 'days').toISOString(),
    end: until || '',
  };

  if (until) {
    betweenDates.end = moment.utc(until).add(1, 'days').toISOString();
  } else {
    betweenDates.end = moment.utc(dtstart).add(1, 'week').toISOString();
  }

  const matches = getRuleDates(recurringEvent, betweenDates);

  if (isArrayEmpty(matches)) {
    throw new Error(`Invalid recurring rule`);
  }

  const res = { first: matches[0], last: null as null | string };

  if (until) {
    res.last = matches[matches.length - 1];

    if (!recurringEvent.allDay) {
      const oldUntil = moment(until);
      const newUntil = moment(res.last).set({
        hours: oldUntil.get('hours'),
        minutes: oldUntil.get('minutes'),
        seconds: oldUntil.get('seconds'),
      });

      res.last = newUntil.toISOString();
    }
  }

  return res;
};

export const getRuleMatches = ({
  recurringEvent,
  betweenDates,
  exclude = true,
}: GetRuleMatchesParams) => {
  const event = cloneObject(recurringEvent);
  let matches: string[] = [];

  // Add and subtract one day for get all matches (? i don't know bro, is for the library)
  if (betweenDates) {
    betweenDates.start = moment.utc(betweenDates.start).subtract(1, 'days').toISOString();
    betweenDates.end = moment.utc(betweenDates.end).add(1, 'days').toISOString();
  }

  // Format dates to specific timezone
  if (!event.allDay && isArrayAndHasItems(event.rrule!.byweekday)) {
    let dtstart = time(event.rrule!.dtstart);
    let until: undefined | Moment | string;

    // Finish by "until" field
    if (event.rrule?.until && isUndefined(event.rrule!.count)) {
      until = time(event.rrule?.until);
    }

    let between: any = undefined as undefined;

    // Format range dates too
    if (betweenDates) {
      between = {};

      // Convert to current timezone
      between.start = time(betweenDates.start);
      between.end = time(betweenDates.end);
    }

    // Convert to event timezone
    if (event.timezone !== timezone.value) {
      dtstart = time(event.rrule!.dtstart, event.timezone);

      until && (until = time(until, event.timezone));

      if (betweenDates) {
        between.start = time(betweenDates.start, event.timezone);
        between.end = time(betweenDates.end, event.timezone);
      }
    }

    const offset = dtstart.clone().format('Z');

    // Fake UTC
    const realStart = dtstart.clone().format(FORMAT_EVENT) + '.000Z';

    // Fake until UTC
    if (until) {
      until = (until as Moment).clone().format(FORMAT_EVENT) + '.000Z';
      // Assing fake until
      event.rrule!.until = until;
    }

    if (between) {
      between.start = between.start.format(FORMAT_EVENT) + '.000Z';
      between.end = between.end.format(FORMAT_EVENT) + '.000Z';
    }

    matches = getFormattedRuleDates({
      realStart,
      offset,
      event,
      between,
    });
  } else {
    // Get in UTC
    matches = getRuleDates(recurringEvent, betweenDates);
  }

  if (!matches.length) {
    return [];
  }

  // Remove excluded dates
  if (exclude && isArrayAndHasItems(recurringEvent.exdate)) {
    matches = matches.filter(match =>
      recurringEvent.exdate!.every(exdate => !moment.utc(exdate).isSame(match)),
    );
  }

  return matches;
};

export const isFirstEventOfRecurrence = (
  dateToAnalize: string,
  betweenDates: CalendarEventPosition,
  recurringEvent: CalendarEvent,
) => {
  if (!recurringEvent.rrule) {
    throw new Error(`Event should be recurring`);
  }

  const matches = getRuleMatches({ recurringEvent, betweenDates });

  if (isArrayEmpty(matches)) {
    return false;
  }

  const firstDate = matches[0];
  return moment(firstDate).isSame(dateToAnalize);
};

export const isLastEventOfRecurrence = (
  dateToAnalize: string,
  betweenDates: CalendarEventPosition,
  recurringEvent: CalendarEvent,
) => {
  if (!recurringEvent.rrule) {
    throw new Error(`Event should be recurring`);
  }

  if (!recurringEvent.rrule.until) {
    return false;
  }

  if (new Date(betweenDates.end) < new Date(recurringEvent.rrule.until)) {
    return false;
  }

  const matches = getRuleMatches({ recurringEvent, betweenDates });

  if (isArrayEmpty(matches)) {
    return false;
  }

  const lastDate = matches[matches.length - 1];
  return moment(lastDate).isSame(dateToAnalize);
};

/** Know if the durations are different */
export const isDurationDifferent = (
  duration1: CalendarEventDuration,
  duration2: CalendarEventDuration,
) => {
  const keys = Object.keys(duration1) as (keyof CalendarEventDuration)[];

  return keys.some(key => duration1[key] !== duration2[key]);
};

export const getSelectedWeekdaysShort = (selectedDays: boolean[]) => {
  return selectedDays.reduce<string[]>((filtered, val, idx) => {
    if (!val) {
      return filtered;
    }

    return [...filtered, DAYS[idx].min];
  }, []);
};

// TODO:: remove floor property
export const generateEventDuration = (
  start: Moment | string,
  end: Moment | string,
  floor = false,
): CalendarEventDurationExtended => {
  /** Difference in minutes */
  const duration = moment(end).diff(start, 'minutes');

  let days = duration / 1440;
  let hours = (duration % 1440) / 60;
  let minutes = duration % 60;

  if (!floor) {
    days = parseInt(days.toString(), 10);
    hours = parseInt(hours.toString(), 10);
    minutes = parseInt(minutes.toString(), 10);
  } else {
    days = Math.floor(days);
    hours = Math.floor(hours);
    minutes = Math.floor(minutes);
  }

  return {
    days,
    hours,
    minutes,
    daysDiff: getDatesDaysDiff(start, end),
  };
};

export const isDurationEmpty = (duration: CalendarEventDuration) => {
  const keys = Object.keys(duration) as (keyof CalendarEventDuration)[];

  return keys.every(key => duration[key] === 0);
};

export const getDaysOfDuration = (duration: CalendarEventDuration) => {
  if (duration.days === 0) {
    return 0;
  }

  let days = duration.days;

  if (duration.hours > 0) {
    days++;
  }

  return days;
};

export const updateDateFromDuration = ({
  date,
  duration,
  allDay = true,
}: UpdateDateFromDuration): string => {
  if (isDurationEmpty(duration)) {
    return allDay ? isoDateWithoutTime(date) : date;
  }

  const newDate = moment
    .utc(date)
    .add(duration.days, 'days')
    .add(duration.hours, 'hours')
    .add(duration.minutes, 'minutes');

  return allDay ? isoDateWithoutTime(newDate) : newDate.toISOString();
};

export const subtractDate = ({ date, duration, allDay = true }: UpdateDateFromDuration): string => {
  if (isDurationEmpty(duration)) {
    return allDay ? isoDateWithoutTime(date) : date;
  }

  const newDate = moment
    .utc(date)
    .subtract(duration.days, 'days')
    .subtract(duration.hours, 'hours')
    .subtract(duration.minutes, 'minutes');

  return allDay ? isoDateWithoutTime(newDate) : newDate.toISOString();
};

/** Increment/reduce a weekday (SU, MO, TU...) in days */
export const updateWeekday = (currentDay: WeekDayMinType, addDaysCount: number) => {
  if (addDaysCount === 0) {
    return currentDay;
  }

  const index = DAYS.findIndex(day => day.min.toUpperCase() === currentDay.toUpperCase())!;

  const isSum = addDaysCount > 0;

  let newIndexDay = isSum ? index + addDaysCount : index - Math.abs(addDaysCount);

  // Add days
  if (isSum) {
    // Reduce it to less or equals than 6
    while (newIndexDay > 6) {
      newIndexDay -= 7;
    }
    // Substract days
  } else {
    // increment it to upper or equals than 0
    while (newIndexDay < 0) {
      newIndexDay += 7;
    }
  }

  return DAYS[newIndexDay].min;
};

export const updateByweekdayProp = (weekdays: WeekDayMinType[], days: number) => {
  return weekdays.map(weekday => {
    return updateWeekday(weekday, days);
  });
};

/** Get week number name in the current month */
export const getWeekNumberNameInMonth = (date: Moment) => {
  const prefixes = ['first', 'second', 'third', 'fourth', 'fifth'];
  const index = Math.floor(date.date() / 7);

  return prefixes[index];
};

export const splitEventExclusions = (exclusions: string[], until: string) => {
  return exclusions.reduce(
    (obj, date) => {
      if (moment(date).isSameOrBefore(until)) {
        return { ...obj, current: [...obj.current, date] };
      }

      return { ...obj, new: [...obj.new, date] };
    },
    { current: [] as string[], new: [] as string[] },
  );
};
