<template>
  <div
    class="ColorCircle"
    :class="{
      'ColorCircle--animate': isAnimated,
      'ColorCircle--disabled': disabled,
    }"
  >
    <CheckOutlined v-if="active" style="color: #fff; font-size: 15px" />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue';
import { CheckOutlined } from '@ant-design/icons-vue';

type ColorSize = 'default' | 'small' | 'large';

export default defineComponent({
  components: { CheckOutlined },
  props: {
    size: {
      type: String as PropType<ColorSize>,
      default: 'default',
      validator: (value: string) => {
        return ['default', 'small', 'large', 'extraSmall'].includes(value);
      },
    },
    color: {
      type: String,
      required: true,
    },
    active: {
      type: Boolean,
      default: false,
    },
    animate: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    /** Circle sizes in pixels */
    enum Sizes {
      large = 40,
      default = 30,
      small = 20,
      extraSmall = 14,
    }

    // enum CheckSizes {
    //   small = 20,
    // }

    const circleSize = `${Sizes[props.size]}px`;

    const isAnimated = computed(() => props.animate && !props.disabled);

    return {
      circleSize,
      isAnimated,
    };
  },
});
</script>

<style lang="scss" scoped>
.ColorCircle {
  width: v-bind('circleSize');
  height: v-bind('circleSize');
  border-radius: 50%;
  background-color: v-bind('color');
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba($color: #000, $alpha: 0.05);

  &--animate:hover {
    transform: scale(1.1);
    transition: transform ease-in-out 200ms;
  }

  &--disabled {
    cursor: not-allowed;
  }
}
</style>
