import { primaApi } from '@/api';
import { Case } from '@/types/cases.type';
import gql from 'graphql-tag';

export default class {
  public static async search(contactId?: number) {
    try {
      const { data } = await primaApi.graphql<{ cases: Case[] }>({
        query: gql`
          query getCases($contactId: Int) {
            cases(contactId: $contactId) {
              id
              name
              caseTypeName
              contactId
            }
          }
        `,
        variables: { contactId },
      });

      return Promise.resolve(data.data.cases);
    } catch (error: any) {
      return Promise.reject(error);
    }
  }
}
