import { User } from '@/types/user.type';
import { reactive, readonly, toRefs } from '@vue/reactivity';

const state = reactive({
  whoami: {} as User,
  users: [] as User[],
});

export default () => {
  const setWhoami = (user: User) => {
    state.whoami = { ...user, fullName: getUserFullName(user) };
  };

  const setUsers = (users: User[]) => {
    state.users = users
      .map(user => ({ ...user, fullName: getUserFullName(user) }))
      .filter(user => user.id !== state.whoami.id);
  };

  const getUserLocal = (id: number) => {
    if (id === state.whoami.id) {
      return { ...state.whoami };
    }

    const user = state.users.find(user => user.id === id)!;
    return { ...user };
  };

  const getUserFullName = (user: User) => {
    if (user.lastName) {
      return `${user.firstName} ${user.lastName}`;
    }

    if (user.middleName) {
      return `${user.firstName} ${user.middleName}`;
    }

    return user.firstName;
  };

  return {
    ...toRefs(readonly(state)),
    getUserLocal,
    getUserFullName,
    setWhoami,
    setUsers,
  };
};
