import moment, { Moment } from 'moment';
import { time, timezone } from '@/composables/use-time';
import { DAYS } from '@/utils/constants';
import {
  ByweekdayMonthly,
  GetDateByPositionOfMonthParams,
  WeekDayMinType,
} from '@/types/calendar.type';
import { dayNumericToString } from './calendar.helper';

export function getMomentInstance(date: string | Moment | Date) {
  const _moment = date instanceof moment ? (date as Moment) : moment(date);

  return _moment.clone();
}

/** Get total weeks number available of the current month */
export function getWeeksOfMonth(date: string | Moment): number {
  const current = getMomentInstance(date);

  return current.clone().endOf('month').week() - current.clone().startOf('month').week() + 1;
}

/** Get current week number of the month */
export function getCurrentWeekOfMonth(date: string | Moment): number {
  const current = getMomentInstance(date);

  return current.week() - current.clone().startOf('month').week() + 1;
}

export function getDayPositionOfMonth(date: string | Moment): ByweekdayMonthly {
  const currentDate = getMomentInstance(date);

  const startMonth = currentDate.clone().startOf('month');
  const endMonth = currentDate.clone().endOf('month');

  const monthDay = startMonth.clone();
  const match = { found: false, position: 0, day: '' as WeekDayMinType };

  let count = 0;

  // equals or less
  while (monthDay.isSameOrBefore(endMonth)) {
    if (currentDate.day() === monthDay.day()) {
      count++;

      if (!match.found && currentDate.isSame(monthDay, 'day')) {
        match.found = true;
        match.position = count;

        const dayString = dayNumericToString(monthDay.day(), 'fullcalendar');
        match.day = dayString;
      }
    }

    // Verify next day
    monthDay.add(1, 'day');
  }

  if (count === match.position) {
    // Indicates that is the last
    match.position = -1;
  }

  return { weekday: match.day, n: match.position };
}

export function getDateByPositionInMonth({
  month,
  year,
  byweekday,
}: GetDateByPositionOfMonthParams) {
  if (![1, 2, 3, 4, 5, -1].includes(byweekday.n)) {
    throw new Error(`Invalid "n" param, out of range -1, 1, 2, 3, 4 , 5`);
  }

  const date = moment().utc().set({ month, year }).startOf('month');
  const endMonth = date.clone().endOf('month');

  const fullDay = DAYS.find(day => day.min === byweekday.weekday)!;
  const ocurrences: string[] = [];

  while (date.isSameOrBefore(endMonth)) {
    if (fullDay.numeric.fullcalendar !== date.day()) {
      date.add(1, 'day');

      continue;
    }

    ocurrences.push(date.toISOString());

    date.add(1, 'day');
  }

  const { length } = ocurrences;
  const matchDate = byweekday.n === -1 ? ocurrences[length - 1] : ocurrences[byweekday.n - 1];

  return matchDate;
}

/** 2021-04-29T03:30:00.000Z -> 2021-04-28T22:30:00 (Bogotá) */
export function dateUTCToTimezone(date: string, addOffset = false) {
  return time(date).format(!addOffset ? 'YYYY-MM-DDTHH:mm:ss' : undefined);
}

/** Format from -05:00 to -5, +03:30 to +3:30 */
export function formatTimezoneHours(time: string) {
  const symb = time[0];
  time = time.substr(1);

  const nums = time
    .split(':')
    .map(numberStr => Number(numberStr))
    .filter(num => num > 0);

  return symb + nums.join(':');
}

export function generateTimezoneLabel(timezone: string) {
  /** Moment timezone value */
  const momentFormat = moment.tz(timezone).format('Z z');
  const [firstPart, lastPart] = momentFormat.split(' ');

  const isHourFormat = /^[+-]/.test(lastPart);
  const time = formatTimezoneHours(firstPart);
  const label = isHourFormat ? `GMT ${time}` : `${lastPart} (GMT ${time})`;

  return `${timezone} ${label}`;
}

export function getWeekday(date: string | Date | Moment) {
  const currentDate = getMomentInstance(date);

  const day = DAYS.find(day => day.numeric.fullcalendar === currentDate.day())!;

  return day;
}

/** Pass date of the current "eventApi" and get it formatted to default event timezone */
export function eventApiDateToEventZone(eventApiDate: string, eventTimezone: string): Moment {
  if (timezone.value === eventTimezone) {
    return time(eventApiDate);
  }

  return time(eventApiDate, timezone.value).tz(eventTimezone);
}

