
import { computed, defineComponent, onMounted, reactive, ref, watch, provide, nextTick } from 'vue';
import CalendarToolbar from '@/components/CalendarToolbar.vue';
import CalendarEventForm from '@/components/CalendarEventForm.vue';
import CalendarForm from '@/components/CalendarForm.vue';
import CalendarModalEventInfo from '@/components/CalendarModalEventInfo.vue';
import { Calendar, DateSelectArg, EventApi, EventDropArg } from '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listGridPlugin from '@fullcalendar/list';
import interactionPlugin, {
  EventDragStartArg,
  EventResizeDoneArg,
  EventResizeStartArg,
} from '@fullcalendar/interaction';
import rrulePlugin from '@fullcalendar/rrule';
import moment from 'moment-timezone';

import {
  useCalendar,
  useTime,
  useGlobalProps,
  useCalendarEvents,
  useCalendarSources,
  useModeFilter,
} from '@/composables';
import {
  capitalize,
  removeObjectProps,
  isoDateWithoutTime,
  cloneObject,
  getObjChangedProps,
  isArrayEmpty,
  isArrayAndHasItems,
  isUndefined,
  isObjectEmpty,
} from '@/utils/functions';
import {
  Byweekday,
  CalendarCreate,
  CalendarEvent,
  CalendarSettings,
  CalendarWriteShowFor,
  ChangeSource,
  EditRecurringType,
  FilterDeleteEvent,
  GenerateDateRangeParams,
  ReturnCalendarEvent,
  ToggleCalendarVisibilityParams,
  UpdateEvent,
} from '@/types/calendar.type';
import { notification } from 'ant-design-vue';
import {
  CalendarInstanceSymbol,
  CalendarSettingsSymbol,
  CalendarCurrentDataSymbol,
} from '@/utils/symbols';
import { calendarService } from '@/services';
import { calendarHelper, dateHelper } from '@/helpers';
import { AntNotificationType } from '@/types/ant-design.type';
import { FORMAT_EVENT, UNAUTHORIZED_ERROR } from '@/utils/constants';
import { EventFrequency, Permission } from '@/utils/enums/global.enum';
import { eventApiDateToEventZone } from '@/helpers/date.helper';
import { FetchEventsMap } from '@/types/services.type';

export default defineComponent({
  components: {
    CalendarToolbar,
    CalendarEventForm,
    CalendarForm,
    CalendarModalEventInfo,
  },
  setup() {
    let calendar = {} as Calendar;
    provide(CalendarInstanceSymbol, calendar);

    const { isModeFilter, getFilteredEvents, globalFilter } = useModeFilter();

    const {
      getRecurrenceChildren,
      getChildrenDataToUpdate,
      getEventFromSource,
      splitExdate,
      regenerateByweekday,
      renewLocalEvent,
    } = useCalendarEvents();

    const {
      isSourceDisplayed,
      getSourceById,
      sourcesToRender,
      isCalendarOwn,
      addNewEventInSource,
      deleteEventInSource,
      removeSource,
    } = useCalendarSources();

    const {
      sources,
      permission,
      setCalendarInstance,
      setDataOfCurrentEvent,
      setActiveDateRange,
      setCurrentCalendarId,
      setOldRecurringPosition,
      addNewCalendarSource,
      getCurrentFullEvent,
      getEventDate,
      getActiveDatesRange,
      selectedSource,
      defaultSource,
      isRecurringEvent,
      isChildEvent,
      currentData,
      currentEventApi,
    } = useCalendar();

    const { isLoading, setLoading, setAwaitLoading, awaitLoading } = useGlobalProps();

    const refFormEvent = ref();
    const eventToForm = ref<CalendarEvent | null>(null);

    const state = reactive({
      recurrenceLabel: '',
      eventDateLabel: '',
      newEventRange: { start: '', end: '' },
    });

    const settings: CalendarSettings = reactive({
      timezone: moment.tz.guess(),
    });

    const { time, setTimezone, timezoneLabel } = useTime(settings.timezone);

    const modal = reactive({
      visible: false,
      type: 'create' as 'create' | 'edit',
      calendar: false,
      /** Modal information visible */
      info: false,
      recurrence: false,
      moveRecurrence: false,
    });

    const editRecurringType = ref<EditRecurringType>('current');
    /** Know where the current action is running from **/
    const eventAction = ref<'modal' | 'dragAndDrop' | 'resize'>('modal');

    const showRecurrenceSelect = computed(() => {
      if (isChildEvent.value) {
        return false;
      }

      if (modal.type === 'create' || !isRecurringEvent.value) {
        return true;
      }

      return editRecurringType.value !== 'current';
    });

    const modalActionTitle = computed(() => {
      return `${capitalize(modal.type)} ${isRecurringEvent.value ? 'Recurring' : ''} Event`;
    });

    const formatEventDates = (oldTimezone?: string) => {
      const events = calendar.getEvents();

      for (const event of events) {
        /* Without old timezone and calendar timezone is equals to calendar's */
        const keepCurrentDate = !oldTimezone && settings.timezone === event.extendedProps.timezone;

        if (event.allDay || keepCurrentDate) {
          continue;
        }

        // Convert event date to old timezone selected or a his own's
        const start = moment.tz(event.startStr, oldTimezone || event.extendedProps.timezone);
        const end = moment.tz(event.endStr, oldTimezone || event.extendedProps.timezone);

        // Convert date to new timezone selected and update event date
        event.setStart(time(start).format(FORMAT_EVENT));
        event.setEnd(time(end).format(FORMAT_EVENT));
      }
    };

    const getVisibleRange = (calendars: FetchEventsMap[]) => {
      let foreverEvent = false;
      const events = calendars.map(calendar => calendar.events).flat();

      const range = events.reduce(
        (range, current) => {
          const start = current.start || current.rrule!.dtstart!;
          const end = current.end || current.rrule!.until!;

          if (!range.start && !range.end) {
            if (events.length === 1) {
              foreverEvent = !end && !foreverEvent;
            }

            return { start, end };
          }

          if (!end && !foreverEvent) {
            foreverEvent = true;
          }

          const temp: any = {};

          if (new Date(start) < new Date(range.start)) {
            temp.start = start;
          }

          if (end && range.end && new Date(end) > new Date(range.end)) {
            temp.end = end;
          }

          return { ...range, ...temp };
        },
        { start: '', end: '' },
      );

      if (foreverEvent) {
        range.end = moment(range.end || new Date())
          .add(13, 'months')
          .toISOString();
      }

      range.start = moment(range.start).subtract(10, 'days').toISOString();
      range.end = moment(range.end).add(10, 'days').toISOString();

      return range;
    };

    const fetchEvents = async () => {
      try {
        let results: FetchEventsMap[];
        const isFilter = isModeFilter.value && calendar.view.type === 'filter';
        let filterFrom: 'backend' | 'local' = 'backend';

        if (isFilter) {
          calendar.removeAllEvents();

          const { from, sourceEvents } = await getFilteredEvents(sources.value);
          filterFrom = from as 'backend' | 'local';

          const visibleRange = getVisibleRange(sourceEvents);
          calendar.setOption('visibleRange', visibleRange);

          results = sourceEvents;
        } else {
          const { start, end } = getActiveDatesRange();

          results = await calendarService.getEvents({ start, end }, globalFilter.value);
        }

        calendar.batchRendering(() => {
          for (const result of results) {
            const index = sources.value.findIndex(source => source.id === result.id);

            if (index === -1) {
              throw new Error(`Calendar with the ID "${result.id}" was not found`);
            }

            const source = sources.value[index];

            if (!isFilter || filterFrom === 'backend') {
              source.events = result.events;
            }

            if (source.show || isFilter) {
              const calendarSource = calendar.getEventSourceById(result.id)!;

              if (!calendarSource) {
                calendar.addEventSource({ ...source, events: result.fullCalendarEvents } as any);
              } else {
                calendarSource.remove();
                calendar.addEventSource({ ...source, events: result.fullCalendarEvents } as any);
              }
            }
          }

          formatEventDates();
        });
      } catch (error: any) {
        console.error(error);
      }
    };

    watch(awaitLoading, (val, oldVal) => {
      // Stop loading
      if (!val && oldVal) {
        setLoading(false);
      }
    });

    watch(
      () => currentData.activeDateRange,
      activeDateRange => {
        const hasActiveDateRange = 'start' in activeDateRange;

        hasActiveDateRange && fetchEvents();
      },
    );

    const showAlert = (message: string, type: AntNotificationType = 'success') => {
      notification[type]({
        message: message,
        placement: 'bottomRight',
      });
    };

    // Update timezone
    watch(
      () => settings.timezone,
      (timezone, oldTimezone) => {
        setTimezone(timezone);
        calendar.setOption('timeZone', timezone);

        calendar.batchRendering(() => formatEventDates(oldTimezone));

        showAlert(`Timezone changed to ${timezoneLabel.value}`, 'info');
      },
    );

    /** Hide item "Current" */
    const hideCurrentItem = computed(() => {
      if (!isRecurringEvent.value) {
        return false;
      }

      const currentEvent = getCurrentFullEvent();

      // Forever event
      if (!currentEvent.rrule!.until) {
        return false;
      }

      let until = '';

      if (currentEvent.allDay) {
        until = isoDateWithoutTime(currentEvent.rrule!.until);
      } else {
        until = calendarHelper.subtractDate({
          date: currentEvent.rrule!.until,
          duration: currentEvent.duration!,
          allDay: currentEvent.allDay,
        });
      }

      if (moment.utc(until).isSame(moment.utc(currentEvent.rrule!.dtstart))) {
        return true;
      }

      if (!isUndefined(currentEvent.rrule!.count) && currentEvent.rrule!.count === 1) {
        return true;
      }

      const dates = calendarHelper.getRuleMatches({ recurringEvent: currentEvent });
      return dates.length === 1;
    });

    const generateDateRange = ({ date, rrule, field }: GenerateDateRangeParams) => {
      // Base range
      const between = {
        start: date,
        end: date,
      };

      // Get method according field
      const methods = {
        start: 'subtract',
        end: 'add',
      };

      // Get invert field
      const invertField = {
        start: 'end',
        end: 'start',
      };

      // Get moment unit according to event frequency
      const frequency: any = {
        [EventFrequency['Daily']]: 'days',
        [EventFrequency['Weekly']]: 'weeks',
        [EventFrequency['Monthly']]: 'months',
        [EventFrequency['Yearly']]: 'years',
      };

      // Example: field = start

      // end
      const invert = invertField[field] as 'start' | 'end';
      // subtract
      const method = methods[field] as 'add' | 'subtract';

      // between.start
      between[field] = moment
        // between.end
        .utc(between[invert])
        // subtract                   // days | weeks | mponths | years
        [method](rrule!.interval * 2, frequency[rrule!.freq])
        .toISOString();

      return between;
    };

    /** Hide item "This and following events" **/
    const hideFollowingCheckItem = computed(() => {
      if (!currentData || !currentData.oldRecurringPosition || !isRecurringEvent.value) {
        return false;
      }

      const currentEvent = getCurrentFullEvent();
      const range = { ...currentData.oldRecurringPosition! };

      const rrule = currentEvent.rrule!;
      const recurrenceStart = moment.utc(rrule!.dtstart);

      // Current selected event date is the first
      if (recurrenceStart.isSame(range.start)) {
        return true;
      }

      let betweenDates = generateDateRange({
        date: recurrenceStart.toISOString(),
        rrule,
        field: 'end',
      });

      // Start: deep validation
      if (calendarHelper.isFirstEventOfRecurrence(range.start, betweenDates, currentEvent)) {
        return true;
      }

      // Wihout finish date, skip
      if (!rrule!.until) {
        return false;
      }

      if (currentData.event!.allDay) {
        range.end = time(range.end).subtract(1, 'days').toISOString();
      }

      // Current selected event date is the end
      if (moment.utc(rrule!.until).isSame(range.end)) {
        return true;
      }

      betweenDates = generateDateRange({
        date: moment.utc(rrule!.until).toISOString(),
        rrule,
        field: 'start',
      });

      // End: deep validation
      return calendarHelper.isLastEventOfRecurrence(range.start, betweenDates, currentEvent);
    });

    // ----- Calendar events -----
    const onEventClick = (e: EventApi) => {
      setDataOfCurrentEvent(e);
      eventAction.value = 'modal';

      setOldRecurringPosition(getEventOldPosition(e));
      modal.info = true;

      // Select item according disponibility
      nextTick(() => {
        if (isRecurringEvent.value) {
          if (!hideCurrentItem.value) {
            editRecurringType.value = 'current';
          } else if (!hideFollowingCheckItem.value) {
            editRecurringType.value = 'following';
          } else {
            editRecurringType.value = 'all';
          }
        }
      });
    };

    const addEventToView = (event: CalendarEvent) => {
      // If the source is displayed
      if (isSourceDisplayed(event.calendarId)) {
        // Add, doesn't exists
        calendar.addEvent(event as any, event.calendarId);
      }
    };

    const updateEventsTimeInView = (events: EventApi[]) => {
      events.forEach(event => {
        // Convert event date to old timezone selected or a his own's
        const start = moment.tz(event.startStr, event.extendedProps.timezone);

        const end = moment.tz(event.endStr, event.extendedProps.timezone);

        // Convert date to new timezone selected and update event date
        event.setStart(time(start).format(FORMAT_EVENT));
        event.setEnd(time(end).format(FORMAT_EVENT));
      });
    };

    const refreshEventsInView = (...events: CalendarEvent[]) => {
      calendar.batchRendering(() => {
        events.forEach(event => {
          const eventUI = calendar.getEventById(event.id);

          // Exists, remove and replace
          !!eventUI && eventUI.remove();
          addEventToView(event);
        });

        // Update time
        const eventsUI = calendar.getEvents().filter(event => {
          const needUpdate = events.some(item => item.id === event.id);
          const isSameTimezone = event.extendedProps.timezone === settings.timezone;

          return !event.allDay && !isSameTimezone && needUpdate;
        });

        updateEventsTimeInView(eventsUI);
      });
    };

    /** Get previous event end date in the current recurrence according to event selected */
    const getPrevEventEndDate = () => {
      const activeRange = getActiveDatesRange();
      const dates = currentData.oldRecurringPosition!;

      const currentEvent = getCurrentFullEvent();

      const between = {
        start: activeRange.start,
        // Set invalid date to get the last allowed date in recurrence
        end: moment.utc(dates.start).subtract(1, 'days').toISOString(),
      };

      // Subtract time according to event interval and frequency
      switch (currentEvent.rrule!.freq) {
        case 'DAILY':
          between.start = moment
            .utc(between.end)
            .subtract(currentEvent.rrule!.interval * 2, 'days')
            .toISOString();
          break;

        case 'WEEKLY':
          between.start = moment
            .utc(between.end)
            .subtract(currentEvent.rrule!.interval * 2, 'weeks')
            .toISOString();
          break;

        case 'MONTHLY':
          between.start = moment
            .utc(between.end)
            .subtract(currentEvent.rrule!.interval * 2, 'months')
            .toISOString();
          break;

        default:
          between.start = moment
            .utc(between.end)
            .subtract(currentEvent.rrule!.interval * 2, 'years')
            .toISOString();
          break;
      }

      const matches = calendarHelper.getRuleMatches({
        recurringEvent: currentData!.event!,
        betweenDates: between,
        exclude: false,
      });

      let until = matches[matches.length - 1];

      if (currentData.event!.allDay) {
        until = isoDateWithoutTime(until);
      } else {
        until = calendarHelper.updateDateFromDuration({
          date: until,
          duration: currentData.event!.duration!,
          allDay: false,
        });
      }

      return until;
    };

    const getCalendarsToRefeatch = (eventCalendarId: string) => {
      const calendarIds = [currentData.event.calendarId!];
      let oldCalendarId: string | undefined = undefined;

      // Move event to another source
      if (!isCurrentCalendarId(eventCalendarId)) {
        calendarIds.push(eventCalendarId);

        oldCalendarId = currentData.event.calendarId!;
      }

      return { calendarIds, oldCalendarId };
    };

    /** Cut recurrence via modal or drag and drop */
    const cutRecurrence = async (data?: CalendarEvent) => {
      const isEditViaModal = !!data;
      const currentEvent = removeObjectProps<CalendarEvent>(
        getCurrentFullEvent(),
        'createdBy',
        'calendarContext',
      );
      const newEvent =
        data || removeObjectProps(getCurrentFullEvent(), 'id', 'createdBy', 'calendarContext')!;

      const prevEventEnd = getPrevEventEndDate();
      let newCount = undefined as undefined | number;

      let { until } = { ...currentEvent.rrule };
      currentEvent.rrule!.until = prevEventEnd;

      // Current event ends by "count" field, recalc
      if (!isUndefined(currentEvent.rrule!.count)) {
        const dates = calendarHelper.getRuleMatches({
          recurringEvent: {
            ...currentEvent,
            rrule: { ...currentEvent.rrule!, count: undefined },
          },
          exclude: false,
        });

        newCount = currentEvent.rrule!.count! - dates.length;
        // Assing new "count" to old current recurrence
        currentEvent.rrule!.count = dates.length;
      }

      const currentEventStartDate = eventApiDateToEventZone(
        currentEventApi.value.startStr,
        newEvent.timezone,
      );

      // Distance between old and new position
      const distance = calendarHelper.generateEventDuration(
        currentData.oldRecurringPosition!.start,
        currentEventStartDate,
      );

      let splitDate = '';

      if (!currentEvent.allDay) {
        splitDate = calendarHelper.subtractDate({
          date: prevEventEnd,
          duration: currentEvent.duration!,
          allDay: false,
        });
      } else {
        splitDate = moment.utc(prevEventEnd).toISOString();
      }

      // manage exdate property
      if (isArrayAndHasItems(currentEvent.exdate)) {
        const exdate = splitExdate(currentEvent.exdate!, splitDate);

        currentEvent.exdate = exdate.current;
        newEvent.exdate = exdate.new;
      }

      const isEventMoved = distance.daysDiff !== 0;
      const hasByweekday = isArrayAndHasItems(currentEvent.rrule!.byweekday);

      // Date changes, update "byweekday"
      if (!isEditViaModal && isEventMoved && hasByweekday) {
        newEvent.rrule!.byweekday = regenerateByweekday({
          currentByweekday: newEvent.rrule!.byweekday! as Byweekday[],
          daysDiff: distance.daysDiff,
          freq: newEvent.rrule!.freq,
          eventSelectedDate: currentEventApi.value.startStr,
        });
      }

      // Via mouse actions
      if (!isEditViaModal) {
        newEvent.rrule!.dtstart = newEvent.allDay
          ? currentEventApi.value.startStr
          : time(currentEventApi.value.startStr).toISOString();

        // Has "count" field
        if (newCount) {
          newEvent.rrule!.count = newCount;
          const dates = calendarHelper.getRuleMatches({ recurringEvent: newEvent, exclude: false });

          let untilByCount = calendarHelper.formatEventDayByAllDay(
            dates[dates.length - 1],
            newEvent.allDay,
          );

          untilByCount = calendarHelper.updateDateFromDuration({
            date: untilByCount,
            duration: newEvent.duration!,
            allDay: newEvent.allDay,
          });

          // Assign and format "until", required for backend filtering
          newEvent.rrule!.until = untilByCount;
        } else if (until) {
          // Finish with "until" field, update according to moved days
          until = calendarHelper.updateDateFromDuration({
            date: until,
            duration: removeObjectProps(distance, 'daysDiff'),
            allDay: newEvent.allDay,
          });

          newEvent.rrule!.until = calendarHelper.formatEventDayByAllDay(until, newEvent.allDay);
        }
      }

      let childrenToUpdate = [] as UpdateEvent[];
      let children = getRecurrenceChildren(currentEvent.calendarId, currentEvent.id);

      // Children to new recurrence
      if (!isArrayEmpty(children)) {
        // children = children.filter(child => moment(child.start).isSameOrAfter(splitDate));
        children = children.filter(child => moment.utc(child.start!).isAfter(splitDate));
      }

      // Data to update of children
      if (isEditViaModal && !isArrayEmpty(children)) {
        // Manage children
        childrenToUpdate = getChildrenDataToUpdate({ currentEvent, newEvent, children });
      }

      try {
        newEvent.createdBy && delete (newEvent as any).createdBy;

        const [[currentEventBackend], newEventBackend] = await Promise.all([
          // Update current recurrence
          calendarService.updateEvents({
            filter: { id: currentEvent.id, calendarId: currentEvent.calendarId },
            newData: getCurrentEventChanges(currentEvent),
          }),
          // Create new recurrence
          calendarService.createEvent(newEvent),
        ]);

        const eventsToLocalUpdate = [currentEventBackend.localEvent];

        const fullCalendarEvents = [
          currentEventBackend.fullCalendarEvent,
          newEventBackend.fullCalendarEvent,
        ];

        // Update children events
        if (!isArrayEmpty(children)) {
          // Add new "parentId" to generated data for update (via modal)
          if (!isArrayEmpty(childrenToUpdate)) {
            childrenToUpdate = childrenToUpdate.map(child => ({
              ...child,
              newData: { ...child.newData, parentId: newEventBackend.localEvent.id },
            }));
          } else {
            // Generate data for update (via modal (without apply changes) or drag and dropd)
            childrenToUpdate = children.map(child => ({
              filter: { id: child.id, calendarId: child.calendarId },
              newData: { parentId: newEventBackend.localEvent.id },
            }));
          }

          const childrenBackend = await calendarService.updateEvents(...childrenToUpdate);

          childrenBackend.forEach(item => {
            eventsToLocalUpdate.push(item.localEvent);
            fullCalendarEvents.push(item.fullCalendarEvent);
          });
        }

        const { oldCalendarId } = getCalendarsToRefeatch(newEventBackend.localEvent.calendarId);

        // Update events locally
        eventsToLocalUpdate.forEach(eventToUpdate => renewLocalEvent(eventToUpdate, oldCalendarId));
        // Add new recurrence
        // addLocalEvent(newEventBackend.localEvent);
        addNewEventInSource(newEventBackend.localEvent);

        if (!isSourceDisplayed(newEventBackend.localEvent.calendarId)) {
          await onChangeCalendarVisibility({
            calendarId: newEventBackend.localEvent.calendarId,
            checked: true,
          });
        }

        refreshEventsInView(...fullCalendarEvents);
        showAlert('Event updated!');

        modal.visible = false;
        modal.moveRecurrence = false;
        editRecurringType.value = 'current';
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const isCurrentCalendarId = (calendarId: string) => {
      if (currentData.event.calendarId === undefined) {
        return false;
      }

      return currentData.event.calendarId === calendarId;
    };

    const manageUnauthorizedError = (errors: any[]) => {
      const hasUnauthorizedError = errors.some(error => {
        return error.message && error.message.includes(UNAUTHORIZED_ERROR);
      });

      if (hasUnauthorizedError) {
        showAlert(`You don't have permission for this action`, 'warning');
      }

      return { hasUnauthorizedError };
    };

    const updateVisibleRangeOnFilterView = async () => {
      // update filter view range
      if (isModeFilter.value && calendar.view.type === 'filter') {
        const { sourceEvents } = await getFilteredEvents(sources.value);

        const visibleRange = getVisibleRange(sourceEvents);
        calendar.setOption('visibleRange', visibleRange);
      }
    };

    const writeEvent = async () => {
      try {
        await refFormEvent.value.validateForm();
        const _event = refFormEvent.value.getEvent();

        setAwaitLoading(true);
        if (modal.type === 'edit') {
          return await handleUpdateEventFromModal(_event);
        }

        const newEvent = await calendarService.createEvent(_event);

        if (isSourceDisplayed(newEvent.localEvent.calendarId)) {
          // addLocalEvent(newEvent.localEvent);
          addNewEventInSource(newEvent.localEvent);

          // update filter view range
          updateVisibleRangeOnFilterView();

          if (newEvent.localEvent.allDay) {
            addEventToView(newEvent.fullCalendarEvent);
          } else {
            // Add and update the time if is necessary (timezone not equals to global)
            refreshEventsInView(newEvent.fullCalendarEvent);
          }
        } else {
          // Hidden source, display and fetch events
          await onChangeCalendarVisibility({
            calendarId: newEvent.localEvent.calendarId,
            checked: true,
          });
        }

        if (isModeFilter.value && calendar.view.type === 'filter') {
          fetchEvents();
        }

        showAlert('Event created!');
        modal.visible = false;
        editRecurringType.value = 'current';
      } catch (error: any) {
        console.error(error);

        Array.isArray(error) && manageUnauthorizedError(error);
      } finally {
        setAwaitLoading(false);
      }
    };

    /** Select date (or range) to create new event */
    const onSelect = (args: DateSelectArg) => {
      eventAction.value = 'modal';
      setCurrentCalendarId(defaultSource.value.id);
      state.newEventRange = { start: args.startStr, end: args.endStr };

      modal.type = 'create';
      modal.visible = true;
    };

    /** Click on custom button "New Event" */
    const onNewEventFromCustomButton = () => {
      modal.type = 'create';
      const today = moment();

      state.newEventRange = {
        start: today.toISOString(),
        end: today.clone().add(1, 'days').toISOString(),
      };

      modal.visible = true;
      refFormEvent.value && refFormEvent.value.clearValidate();
    };

    const onConfirmEditEvent = () => {
      eventToForm.value = getCurrentFullEvent();

      // Cut recurrence
      if (isRecurringEvent.value && editRecurringType.value === 'following') {
        const temptEvent = getCurrentFullEvent();

        if (typeof temptEvent.rrule!.count !== 'undefined') {
          const end = getPrevEventEndDate();

          temptEvent.rrule!.until = end;
          const dates = calendarHelper.getRuleDates({
            ...temptEvent,
            rrule: { ...temptEvent.rrule!, count: undefined },
          });

          const count = temptEvent.rrule!.count - dates.length;

          eventToForm.value.rrule!.count = count;
          eventToForm.value.rrule!.dtstart = currentEventApi.value.startStr;
        }
      }

      modal.info = false;
      modal.type = 'edit';
      modal.visible = true;
    };

    /** Hook of create/edit modal */
    const onAfterCloseModal = () => {
      editRecurringType.value = 'current';
    };

    const onClickDeleteEvent = () => {
      editRecurringType.value = 'current';
    };

    /** Remove normal event */
    const deleteEvent = async () => {
      const { id, calendarId } = getCurrentFullEvent();

      try {
        // Remove in backend
        await calendarService.deleteEvents({
          field: 'id',
          value: id,
          calendarId,
        });

        // Remove in local data
        // deleteLocalEvent(id, calendarId);
        deleteEventInSource(id, calendarId);
        // Remove from view
        calendar.getEventById(id)?.remove();

        showAlert('Event removed!');
        modal.info = false;
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const deleteEventFromRecurrence = async () => {
      const currentEvent = getCurrentFullEvent();
      const { start } = currentData.oldRecurringPosition!;

      const exdate = [...(currentEvent.exdate || []), start];

      try {
        const [updated] = await calendarService.updateEvents({
          filter: { id: currentEvent.id, calendarId: currentEvent.calendarId },
          newData: { exdate },
        });

        renewLocalEvent(updated.localEvent);
        refreshEventsInView(updated.fullCalendarEvent);

        showAlert('Event updated!');

        modal.info = false;
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const deleteThisAndFollowingEvents = async () => {
      const currentEvent = getCurrentFullEvent();
      const end = getPrevEventEndDate();

      currentEvent.rrule!.until = end;

      // Current event ends by "count" field, recalc
      if (!isUndefined(currentEvent.rrule!.count)) {
        const dates = calendarHelper.getRuleMatches({
          recurringEvent: { ...currentEvent, rrule: { ...currentEvent.rrule!, count: undefined } },
          exclude: false,
        });

        // Assing new "count" to old current recurrence
        currentEvent.rrule!.count = dates.length;
      }

      let splitDate = '';

      if (!currentEvent.allDay) {
        splitDate = calendarHelper.subtractDate({
          date: end,
          duration: currentEvent.duration!,
          allDay: false,
        });
      } else {
        splitDate = moment.utc(end).toISOString();
      }

      // Manage "exdate" property
      if (isArrayAndHasItems(currentEvent.exdate!)) {
        const ex = splitExdate(currentEvent.exdate!, splitDate);

        currentEvent.exdate = ex.current;
      }

      let children = getRecurrenceChildren(currentEvent.calendarId, currentEvent.id);

      // Get children to delete
      if (!isArrayEmpty(children)) {
        children = children.filter(child => moment.utc(child.start!).isAfter(splitDate));
      }

      try {
        const promises: [Promise<ReturnCalendarEvent[]>, Promise<CalendarEvent[]>] = [] as any;

        promises.push(
          calendarService.updateEvents({
            filter: { id: currentEvent.id, calendarId: currentEvent.calendarId },
            newData: { exdate: currentEvent.exdate!, rrule: currentEvent.rrule! },
          }),
        );

        // Remove children
        if (!isArrayEmpty(children)) {
          // Add request
          promises.push(
            // Generate data for children deleting
            calendarService.deleteEvents(
              ...children.map(child => {
                return {
                  field: 'id',
                  value: child.id,
                  calendarId: child.calendarId,
                } as FilterDeleteEvent;
              }),
            ),
          );
        }

        const [[updated], childrenDeleted] = await Promise.all(promises);

        renewLocalEvent(updated.localEvent);
        refreshEventsInView(updated.fullCalendarEvent);

        if (childrenDeleted) {
          const { source } = getSourceById(currentEvent.calendarId);
          // Remove events of local and UI
          calendar.batchRendering(() => {
            childrenDeleted.forEach(deletedEvent => {
              // UI
              calendar.getEventById(deletedEvent.id)?.remove();
              // Local
              deleteEventInSource(deletedEvent.id, source.id);
            });
          });
        }

        modal.info = false;
        showAlert('Event removed!');
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const deleteAllEvents = async () => {
      const event = getCurrentFullEvent();

      const toDelete: FilterDeleteEvent[] = [
        { field: 'id', value: event.id, calendarId: event.calendarId },
      ];

      const children = getRecurrenceChildren(event.calendarId, event.id);

      if (children.length) {
        // Remove children
        toDelete.unshift({
          field: 'parentId',
          value: event.id,
          calendarId: event.calendarId,
        });
      }

      try {
        const { source } = getSourceById(event.calendarId);
        const deletedEvents = await calendarService.deleteEvents(...toDelete);

        // Remove events of local and UI
        calendar.batchRendering(() => {
          deletedEvents.forEach(deletedEvent => {
            // UI
            calendar.getEventById(deletedEvent.id)?.remove();
            // Local
            deleteEventInSource(deletedEvent.id, source.id);
          });
        });

        modal.info = false;
        showAlert(`${children.length ? 'Events' : 'Event'} removed!`);
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const handleDeleteEvents = async () => {
      // Remove normal event
      if (!isRecurringEvent.value) {
        return deleteEvent();
      }

      // Remove only event selected of recurrence
      if (editRecurringType.value === 'current') {
        return deleteEventFromRecurrence();
      }

      // Remove this and following events
      switch (editRecurringType.value) {
        case 'following':
          await deleteThisAndFollowingEvents();
          break;

        default:
          // Remove all events
          await deleteAllEvents();
          break;
      }
    };

    /** Split event from recurrence via modal or drag and drop */
    const splitEventFromRecurrence = async (data?: CalendarEvent) => {
      // Split from mouse events, drag and drop
      if (!data) {
        data = removeObjectProps<CalendarEvent>(
          getCurrentFullEvent(),
          'id',
          'duration',
          'rrule',
          'exdate',
          'createdBy',
          'calendarContext',
        );

        /** Current dates range */
        const { start, end } = getEventDate();

        data.start = start;
        data.end = end;
      }

      const parent = cloneObject(currentData.event);
      const oldPosition = time(currentData.oldRecurringPosition!.start).toISOString();

      // Exclude from parent
      parent.exdate = [...parent.exdate!, oldPosition];

      const { oldCalendarId } = getCalendarsToRefeatch(data.calendarId);

      if (!oldCalendarId) {
        // Assign parent
        data.parentId = parent.id;
      }

      try {
        // Update recurring event and create a new child in backend.
        const [[parentUpdated], newChildEvent] = await Promise.all([
          calendarService.updateEvents({
            filter: { id: parent.id, calendarId: parent.calendarId },
            newData: { exdate: parent.exdate },
          }),
          calendarService.createEvent(data),
        ]);

        addNewEventInSource(newChildEvent.localEvent);
        renewLocalEvent(parentUpdated.localEvent, oldCalendarId);
        refreshEventsInView(parentUpdated.fullCalendarEvent);

        if (isSourceDisplayed(newChildEvent.localEvent.calendarId)) {
          refreshEventsInView(newChildEvent.fullCalendarEvent);
        } else {
          // Hidden source, display and fetch events
          await onChangeCalendarVisibility({
            calendarId: newChildEvent.localEvent.calendarId,
            checked: true,
          });
        }

        showAlert('Event updated!');

        modal.visible = false;

        modal.moveRecurrence = false;
        editRecurringType.value = 'current';
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const getCurrentEventChanges = (newEventData: CalendarEvent) => {
      let compareData = cloneObject(newEventData);

      const currentEvent = removeObjectProps<CalendarEvent>(
        getCurrentFullEvent(),
        'createdBy',
        'calendarContext',
      );

      if (compareData.description === null && currentEvent.description === undefined) {
        (currentEvent as any).description = null;
      }

      // Quit color
      if (currentEvent.color && isUndefined(compareData.color)) {
        (compareData.color as any) = null;
      }

      if (isUndefined(currentEvent.location)) {
        (currentEvent.location as any) = null;
      }

      if (isUndefined(currentEvent.attendees)) {
        (currentEvent.attendees as any) = null;
      }

      return getObjChangedProps<CalendarEvent>(currentEvent, compareData);
    };

    /** Update events from modal */
    const handleUpdateEventFromModal = async (data: CalendarEvent) => {
      // Edit normal event
      if (!isRecurringEvent.value) {
        try {
          let oldCalendarId = undefined as string | undefined;

          if (data.calendarId !== currentData.event.calendarId) {
            oldCalendarId = currentData.event.calendarId!;

            data.parentId && ((data as any).parentId = null);
          }

          const changes = getCurrentEventChanges(data);

          if (isObjectEmpty(changes)) {
            return showAlert('Please apply any change to current event before', 'warning');
          }

          const [newData] = await calendarService.updateEvents({
            filter: { id: data.id, calendarId: currentData.event.calendarId },
            newData: changes,
          });

          if (isSourceDisplayed(newData.localEvent.calendarId)) {
            renewLocalEvent(newData.localEvent, oldCalendarId);

            // update filter view range
            updateVisibleRangeOnFilterView();

            refreshEventsInView(newData.fullCalendarEvent);
          } else {
            calendar.getEventById(data.id)?.remove();
            // Hidden source, display and fetch events
            await onChangeCalendarVisibility({
              calendarId: newData.localEvent.calendarId,
              checked: true,
            });
          }

          showAlert('Event updated!');

          modal.visible = false;
        } catch (error: any) {
          console.error(error);

          manageUnauthorizedError(error);
        }

        return;
      }

      // Split event from recurrence
      if (editRecurringType.value === 'current') {
        return splitEventFromRecurrence({ ...data });
      }

      // Cut current recurrence
      if (editRecurringType.value === 'following') {
        return cutRecurrence({ ...data });
      }

      // ------- Update all recurrence ------
      const currentEvent = getCurrentFullEvent();

      const newData = getCurrentEventChanges(data);

      if (isObjectEmpty(newData)) {
        return showAlert('Please apply any change to current event before', 'warning');
      }

      const { oldCalendarId } = getCalendarsToRefeatch(data.calendarId);

      const children = getRecurrenceChildren(currentEvent.calendarId, currentEvent.id);
      let childrenToUpdate = [] as UpdateEvent[];

      if (isArrayAndHasItems(children)) {
        childrenToUpdate = getChildrenDataToUpdate({
          currentEvent,
          newEvent: data,
          children,
        });
      }

      try {
        const events = await calendarService.updateEvents(
          {
            // Send current calendarId, not new if this is changed
            filter: { id: data.id, calendarId: currentEvent.calendarId },
            newData,
          },
          ...childrenToUpdate,
        );

        const fullCalendarEvents: CalendarEvent[] = [];

        events.forEach(event => {
          renewLocalEvent(event.localEvent, oldCalendarId);
          fullCalendarEvents.push(event.fullCalendarEvent);
        });

        // update filter view range
        updateVisibleRangeOnFilterView();

        refreshEventsInView(...fullCalendarEvents);

        if (!isSourceDisplayed(data.calendarId)) {
          await onChangeCalendarVisibility({
            calendarId: data.calendarId,
            checked: true,
          });
        }

        showAlert('Event updated!');
        modal.visible = false;
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    const updateMonthlyByDay = async () => {
      const newEvent = getCurrentFullEvent();

      const month = calendarHelper.getRecurrenceNewMonth({
        dateAbsulute: newEvent.rrule!.dtstart,
        eventOldPosition: currentData.oldRecurringPosition!.start,
        eventNewPosition: currentEventApi.value.startStr,
      });

      let currentStart = currentEventApi.value.startStr;

      if (!newEvent.allDay) {
        const tmpStart = eventApiDateToEventZone(currentStart, newEvent.timezone);

        currentStart = tmpStart.format(FORMAT_EVENT);
      }

      const position = dateHelper.getDayPositionOfMonth(currentStart);

      const newDtstart = dateHelper.getDateByPositionInMonth({
        month,
        year: new Date(newEvent.rrule!.dtstart).getUTCFullYear(),
        byweekday: position,
      });

      // Update "dtstart" field
      if (!newEvent.allDay) {
        // Add time for to correct formating
        let temp = `${newDtstart.substr(0, 10)}T${currentStart.split('T')[1]}`;

        newEvent.rrule!.dtstart = time(temp, newEvent.timezone).toISOString();
      } else {
        newEvent.rrule!.dtstart = moment.utc(newDtstart).format('YYYY-MM-DD');
      }

      (newEvent.rrule!.byweekday as any) = [position];

      // Has finish
      if (!isUndefined(newEvent.rrule!.until)) {
        // Ends by "count" field
        if (!isUndefined(newEvent.rrule?.count)) {
          const matches = calendarHelper.getRuleMatches({
            recurringEvent: newEvent,
            exclude: false,
          });

          newEvent.rrule!.until = matches[matches.length - 1];

          if (!newEvent.allDay) {
            newEvent.rrule!.until = calendarHelper.updateDateFromDuration({
              date: newEvent.rrule!.until!,
              duration: newEvent.duration!,
              allDay: newEvent.allDay,
            });
          } else {
            newEvent.rrule!.until = isoDateWithoutTime(newEvent.rrule!.until);
          }
        } else {
          // Ends by "until" field
          const monthU = calendarHelper.getRecurrenceNewMonth({
            dateAbsulute: newEvent.rrule!.until!,
            eventOldPosition: currentData.oldRecurringPosition!.start,
            eventNewPosition: currentEventApi.value.startStr,
          });

          const newUntil = dateHelper.getDateByPositionInMonth({
            month: monthU,
            year: new Date(newEvent.rrule!.until!).getUTCFullYear(),
            byweekday: position,
          });

          // Update "dtstart" field
          if (!newEvent.allDay) {
            // Add time for to correct formating
            let temp = `${newUntil.substr(0, 10)}T${newEvent.rrule!.until!.split('T')[1]}`;

            newEvent.rrule!.until = time(temp, newEvent.timezone).toISOString();
          } else {
            newEvent.rrule!.until = moment.utc(newUntil).format('YYYY-MM-DD');
          }
        }
      }

      try {
        const [result] = await calendarService.updateEvents({
          filter: { id: newEvent.id, calendarId: newEvent.calendarId },
          newData: { rrule: newEvent.rrule },
        });

        // Update events locally
        renewLocalEvent(result.localEvent);

        refreshEventsInView(result.fullCalendarEvent);
        showAlert('Event updated!');

        modal.visible = false;
        modal.moveRecurrence = false;
        editRecurringType.value = 'current';
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      }
    };

    /** Update recurring event from resizing and drag and drop */
    const handleUpdateRecurringMouseActions = async () => {
      try {
        console.time('WRITE MOUSE');
        setAwaitLoading(true);
        // Split one event of the recurrence
        if (editRecurringType.value === 'current') {
          await splitEventFromRecurrence();
          return;
        }

        if (editRecurringType.value === 'following') {
          return await cutRecurrence();
        }

        // ----- Move all recurrence ----
        const newEvent = removeObjectProps<CalendarEvent>(getCurrentFullEvent(), 'createdBy');

        // Update monthly by day
        if (newEvent.rrule!.freq === 'MONTHLY' && isArrayAndHasItems(newEvent.rrule!.byweekday)) {
          return updateMonthlyByDay();
        }

        // Rest of cases

        const currentEventStartDate = eventApiDateToEventZone(
          currentEventApi.value.startStr,
          newEvent.timezone,
        );

        // Distance between old and new position
        const distance = calendarHelper.generateEventDuration(
          currentData.oldRecurringPosition!.start,
          currentEventStartDate,
        );

        // New dtstart
        newEvent.rrule!.dtstart = calendarHelper.updateDateFromDuration({
          date: newEvent.rrule!.dtstart,
          duration: removeObjectProps(distance, 'daysDiff'),
          allDay: newEvent.allDay,
        });

        // Update "byweekday" field
        if (isArrayAndHasItems(newEvent.rrule!.byweekday!)) {
          newEvent.rrule!.byweekday = regenerateByweekday({
            currentByweekday: newEvent.rrule!.byweekday! as Byweekday[],
            daysDiff: distance.daysDiff,
            freq: newEvent.rrule!.freq,
            eventSelectedDate: currentEventApi.value.startStr,
          });
        }

        // Recurring event ends by "count" field
        if (!isUndefined(newEvent.rrule!.count)) {
          const dates = calendarHelper.getRuleMatches({ recurringEvent: newEvent, exclude: false });

          newEvent.rrule!.until = dates[dates.length - 1];
        } else if (newEvent.rrule!.until) {
          // Recurring event ends by "until" field
          newEvent.rrule!.until = calendarHelper.updateDateFromDuration({
            date: newEvent.rrule!.until,
            duration: removeObjectProps(distance, 'daysDiff'),
            allDay: newEvent.allDay,
          });
        }

        const [result] = await calendarService.updateEvents({
          filter: { id: newEvent.id, calendarId: newEvent.calendarId },
          newData: { rrule: newEvent.rrule },
        });

        // Update events locally
        renewLocalEvent(result.localEvent);

        refreshEventsInView(result.fullCalendarEvent);
        showAlert('Event updated!');

        modal.visible = false;
        modal.moveRecurrence = false;
        editRecurringType.value = 'current';
      } catch (error: any) {
        console.error(error);

        manageUnauthorizedError(error);
      } finally {
        setAwaitLoading(false);
        console.timeEnd('WRITE MOUSE');
      }
    };

    const setCurrentActiveRange = () => {
      const { start, end } = getActiveDatesRange();
      let areDatesEquals = false;

      if ('start' in currentData.activeDateRange) {
        const active = currentData.activeDateRange;

        areDatesEquals =
          time(start).utc().isSame(active.start) && time(end).utc().isSame(active.end);
      }

      !areDatesEquals && setActiveDateRange({ start, end });
    };

    const getEventOldPosition = (event: EventApi) => {
      if (event.allDay) {
        return {
          start: calendarHelper.convertOffsetTimeToUTC(event.startStr),
          end: calendarHelper.convertOffsetTimeToUTC(event.endStr),
        };
      }

      const eventTimezone = event.extendedProps.timezone;

      // Event and calendar with the same timezone
      if (settings.timezone === eventTimezone) {
        return {
          start: time(event.startStr, eventTimezone).toISOString(),
          end: time(event.endStr, eventTimezone).toISOString(),
        };
      }

      return {
        start: time(event.startStr, settings.timezone).tz(eventTimezone).toISOString(),
        end: time(event.endStr, settings.timezone).tz(eventTimezone).toISOString(),
      };
    };

    /** Revert change of event position */
    let revertEventChangePosition: () => void;

    /** Manage normal event resizing or moving */
    const manageEventOnResizeOrMoving = async (event: EventApi, oldEvent: EventApi) => {
      // It indicates that the event is recurring
      if (isRecurringEvent.value) {
        setOldRecurringPosition(getEventOldPosition(oldEvent));
        modal.moveRecurrence = true;
        return;
      }

      // Update normal event
      try {
        const [{ localEvent }] = await calendarService.updateEvents({
          filter: { id: event.id, calendarId: event.extendedProps.calendarId },
          newData: getEventOldPosition(event),
        });

        renewLocalEvent(localEvent);
        showAlert('Event updated!');
      } catch (error: any) {
        console.error(error);

        const { hasUnauthorizedError } = manageUnauthorizedError(error);

        if (hasUnauthorizedError) {
          revertEventChangePosition();
        }
      }
    };

    const onEventResizeStart = (args: EventResizeStartArg) => {
      setDataOfCurrentEvent(args.event);

      if (permission.value === Permission.Read) {
        return showAlert('This event is read-only for you', 'warning');
      }

      const { rrule } = getCurrentFullEvent();

      if (rrule) {
        return showAlert("Recurring events can't be resized", 'warning');
      }
    };

    const onEventResize = async (args: EventResizeDoneArg) => {
      if (permission.value === Permission.Read || currentData.event.rrule) {
        args.revert();
        return;
      }

      revertEventChangePosition = args.revert;
      eventAction.value = 'resize';

      manageEventOnResizeOrMoving(args.event, args.oldEvent);
    };

    const onEventDragStart = (args: EventDragStartArg) => {
      setDataOfCurrentEvent(args.event);

      if (permission.value === Permission.Read) {
        return showAlert('This event is read-only for you', 'warning');
      }
    };

    const onEventDrop = async (args: EventDropArg) => {
      setDataOfCurrentEvent(args.event);

      if (permission.value === Permission.Read) {
        args.revert();
        return;
      }

      revertEventChangePosition = args.revert;
      eventAction.value = 'dragAndDrop';

      manageEventOnResizeOrMoving(args.event, args.oldEvent);
    };

    const restartRecurringEventPosition = () => {
      revertEventChangePosition();

      editRecurringType.value = 'current';
      // Remove temp position data
      setOldRecurringPosition(null);
    };

    const calendarFormData = reactive({
      name: undefined as string | undefined,
      color: undefined as string | undefined,
    });

    const onCreateNewCalendar = async () => {
      if (!calendarFormData.name || !calendarFormData.color) {
        throw new Error(`Name and color fields are required to create new calendar source`);
      }

      const newCalendar: CalendarCreate = {
        ...(calendarFormData as any),
        isDefault: false,
      };

      try {
        const source = await calendarService.createCalendar(newCalendar);
        // Add source to local data and calendar UI
        addNewCalendarSource(source);
      } catch (error: any) {
        console.error(error);
      }

      calendarFormData.name = undefined;
      calendarFormData.color = undefined;

      showAlert('New calendar created!');
      modal.calendar = false;
    };

    const onCancelNewCalendar = () => {
      calendarFormData.name = undefined;
      calendarFormData.color = undefined;
    };

    /** Update events color */
    const onChangeSource = ({ source, oldSource }: ChangeSource) => {
      if (source.color === oldSource.color) {
        return;
      }

      const fullSource = getSourceById(source.id).source;

      // Change events color
      calendar.batchRendering(() => {
        // Get only events of changed calendar and without color
        const events = calendar.getEvents().filter(event => {
          const belongsToCalendar = event.extendedProps.calendarId === source.id;

          if (!belongsToCalendar) {
            return false;
          }

          const fullEvent = getEventFromSource(event.id, fullSource).event;
          return !fullEvent.color;
          // return belongsToCalendar && !event.backgroundColor && !event.borderColor;
        });

        events.forEach(event => {
          event.setProp('backgroundColor', source.color);
          event.setProp('borderColor', source.color);
        });
      });
    };

    /** Show/hide calendars */
    const onChangeCalendarVisibility = async ({
      calendarId,
      checked,
    }: ToggleCalendarVisibilityParams) => {
      const { source } = getSourceById(calendarId);

      // Hide calendar
      if (!checked) {
        // Update in database, not default calendar
        if (defaultSource.value.id !== calendarId) {
          try {
            await calendarService.updateCalendarVisibility(source.context, calendarId, checked);
          } catch (error: any) {
            console.error(error);
          }
        }

        // Hide and clear local data
        source.show = false;
        source.events = [];

        let removed = false;

        while (!removed) {
          calendar.getEventSourceById(calendarId)?.remove();

          removed = !calendar.getEventSourceById(calendarId);
        }

        return;
      }

      source.show = true;
      source.events = [];

      const { activeDateRange } = currentData;

      const promises: Promise<CalendarWriteShowFor | ReturnCalendarEvent[]>[] = [
        calendarService.getEventsFromOneCalendar(
          calendarId,
          source.context,
          activeDateRange,
          globalFilter.value,
        ),
      ];

      // Update in database, not default
      if (defaultSource.value.id !== calendarId) {
        promises.push(
          calendarService.updateCalendarVisibility(source.context, calendarId, checked),
        );
      }

      try {
        const [events] = await Promise.all(promises);

        if (!isArrayAndHasItems(events)) {
          return;
        }

        const [localEvents, fullCalendarEvents] = (events as ReturnCalendarEvent[]).reduce(
          (acc, item) => {
            acc[0].push(item.localEvent);
            acc[1].push(item.fullCalendarEvent);

            return acc;
          },
          [[] as CalendarEvent[], [] as CalendarEvent[]],
        );

        calendar.batchRendering(() => {
          calendar.addEventSource({ ...source, events: fullCalendarEvents } as any);
          source.events.push(...localEvents);

          // Events to update
          const eventsUI = calendar.getEvents().filter(event => {
            const needUpdate = settings.timezone !== event.extendedProps.timezone;

            return !event.allDay && needUpdate && event.extendedProps.calendarId === calendarId;
          });

          if (isArrayEmpty(eventsUI)) {
            return;
          }

          updateEventsTimeInView(eventsUI);
        });
      } catch (error: any) {
        console.error(error);
      }
    };

    const onRemoveCalendar = async (id: string) => {
      try {
        const calendarSource = calendar.getEventSourceById(id);

        if (!calendarSource) {
          throw new Error('Calendar source not found when trying to remove it');
        }

        // Remove from backend
        const result = await calendarService.removeCalendar(id);
        // Remove from full-calendar api
        calendarSource.remove();
        // Remove from local data
        removeSource(id);

        showAlert(
          `The calendar "${result.name}" and its ${result.eventsRemoved} events were eliminated!`,
          'success',
        );
      } catch (error) {
        console.error(error);
      }
    };

    /** CalendarToolbar component data and events */
    const calendarToolbar = reactive({
      viewTitle: '',
      viewName: '',
      /** Create new event/calendar from custom button */
      onCreate(type: 'event' | 'calendar') {
        if (type === 'event') {
          return onNewEventFromCustomButton();
        }

        modal.calendar = true;
      },
      /** Change view from "CalendarToolbar" component */
      onChangeView(view: string) {
        isModeFilter.value && calendar.removeAllEvents();

        calendar.changeView(view);
        setCurrentActiveRange();
      },
      /** Set custom date in calendar */
      onGotoDate(date: string) {
        calendar.gotoDate(date);
        // Update title in "CalendarToolbar" component
        calendarToolbar.viewTitle = calendar.currentData.viewTitle;

        setCurrentActiveRange();
      },
      /** Moves the calendar to the current date */
      onSetToday() {
        calendar.today();
        // Update title in "CalendarToolbar" component
        calendarToolbar.viewTitle = calendar.currentData.viewTitle;

        setCurrentActiveRange();
      },
      /** Go to next/prev page */
      async onChangeViewPage(type: 'next' | 'prev') {
        calendar[type]();

        setCurrentActiveRange();

        // Update title in "CalendarToolbar" component
        calendarToolbar.viewTitle = calendar.currentData.viewTitle;
      },
      onFetchEvents: () => fetchEvents(),
      onChangeCalendarVisibility,
      onChangeSource,
      onRemoveCalendar,
    });

    const onCreated = async () => {
      provide(CalendarSettingsSymbol, settings);
    };

    onCreated();

    onMounted(async () => {
      const el = document.getElementById('calendar') as HTMLDivElement;

      calendar = new Calendar(el, {
        aspectRatio: 19 / 8,
        plugins: [
          rrulePlugin,
          // momentTimezonePlugin,
          dayGridPlugin,
          timeGridPlugin,
          listGridPlugin,
          interactionPlugin,
        ],
        views: {
          filter: { type: 'list' },
        },
        initialView: isModeFilter.value ? 'filter' : 'dayGridMonth',
        timeZone: settings.timezone,
        headerToolbar: false,
        editable: true,
        selectable: true,
        dayMaxEvents: true, // when too many events in a day, show the popover
        slotDuration: '00:15:00',
        allDayText: 'All day',
        slotLabelFormat: {
          hour: 'numeric',
          minute: '2-digit',
          omitZeroMinute: true,
          meridiem: 'lowercase',
        },
        select: onSelect,
        eventDragStart: onEventDragStart,
        eventDrop: onEventDrop,
        eventResize: onEventResize,
        eventResizeStart: onEventResizeStart,
        eventClick: args => onEventClick(args.event),
        viewClassNames({ view }) {
          calendarToolbar.viewTitle = view.title;
          calendarToolbar.viewName = view.type;

          return [];
        },
      });

      provide(CalendarInstanceSymbol, calendar);
      provide(CalendarCurrentDataSymbol, currentData);
      setCalendarInstance(calendar);

      setCurrentActiveRange();
      calendar.render();

      // Add displayed sources to Fullcalendar
      const _sources = [...sourcesToRender.value.own, ...sourcesToRender.value.shared];

      calendar.batchRendering(() => {
        _sources.forEach(_source => {
          if (_source.show) {
            calendar.addEventSource({ ..._source, events: [] });
          }
        });
      });
    });

    return {
      isEventSelected: computed(() => !!Object.keys(currentData.event as any).length),
      isLoading,
      showRecurrenceSelect,
      isCalendarOwn,
      sources,
      settings,
      currentData,
      calendarToolbar,
      calendarFormData,
      sourcesToRender,
      writeEvent,
      onCreateNewCalendar,
      onCancelNewCalendar,
      eventAction,
      eventToForm,
      editRecurringType,
      selectedSource,
      state,
      onAfterCloseModal,
      refFormEvent,
      modal,
      modalActionTitle,
      isRecurringEvent,
      hideCurrentItem,
      hideFollowingCheckItem,
      handleDeleteEvents,
      onConfirmEditEvent,
      restartRecurringEventPosition,
      handleUpdateRecurringMouseActions,
      onClickDeleteEvent,
    };
  },
});
