import { computed, reactive } from 'vue';

import {
  CalendarBasics,
  CalendarEvent,
  CalendarLocalIndividualPermission,
  CalendarOriginalPermission,
  CalendarOriginalPermissions,
  CalendarOriginalSource,
  CalendarPermissions,
  CalendarPermissionType,
  CalendarSource,
  CalendarUpdate,
} from '@/types/calendar.type';
import { Color } from '@/types/color.type';
import { useUsers } from '.';
import { CalendarColors } from '@/utils/enums/colors.enum';
import { CalendarContext, Permission } from '@/utils/enums/global.enum';
import { cloneObject, isArrayAndHasItems } from '@/utils/functions';
import { User } from '@/types/user.type';
import useModeFilter from './use-mode-filter';

interface State {
  sources: CalendarSource[];
  filteredSources: CalendarSource[];
}

const state = reactive<State>({
  sources: [],
  filteredSources: [],
});

export default function () {
  const { whoami, getUserLocal } = useUsers();
  const { isFilterViewActive, isModeFilter } = useModeFilter();

  const setSources = (sources: CalendarOriginalSource[], cache = false) => {
    state.sources = sources.map(source => transformSource(source));

    if (!cache) {
      return;
    }

    // Set cache, all as visible
    state.filteredSources = cloneObject(sources)
      .filter(source => source.context !== CalendarContext.Google)
      .map(source => transformSource({ ...source, show: true }));
  };

  const sources = computed(() => {
    return isFilterViewActive.value ? state.filteredSources : state.sources;
  });

  /** Currently displayed sources id's */
  const displayedSources = computed(() => {
    return sources.value.filter(source => source.show).map(source => source.id);
  });

  const getPermission = (permission: CalendarOriginalPermission) => {
    return permission.all ? Permission.All : Permission.Read;
  };

  const transformPermission = (permissions: CalendarOriginalPermissions) => {
    const newPermissions = {} as CalendarPermissions;

    permissions.admin && (newPermissions.admin = getPermission(permissions.admin));
    permissions.global && (newPermissions.global = getPermission(permissions.global));

    if (!isArrayAndHasItems(permissions.individual)) {
      return newPermissions;
    }

    newPermissions.individual = permissions.individual!.map(permission => {
      // const user =
      // permission.userId === whoami.value.id ? unref(whoami) : getUserLocal(permission.userId);
      const user = getUserLocal(permission.userId);

      return {
        id: user.id,
        name: user.fullName,
        email: user.email,
        admin: user.admin,
        permission: getPermission(permission),
      } as CalendarLocalIndividualPermission;
    });

    return newPermissions;
  };

  const sourcesToRender = computed(() => {
    return sources.value.reduce(
      (acc, source) => {
        const basics: CalendarBasics = {
          id: source.id,
          user: source.user,
          name: source.name,
          show: source.show,
          isDefault: source.isDefault,
          color: source.color,
          context: source.context,
          permissions: source.permissions,
        };

        // Google calendars
        if (source.context === CalendarContext.Google) {
          return { ...acc, google: [...acc.google, basics] };
        }

        if (source.user!.id === whoami.value.id) {
          return { ...acc, own: [...acc.own, basics] };
        }

        // Sharing calendars
        return { ...acc, shared: [...acc.shared, basics] };
      },
      {
        own: [] as CalendarBasics[],
        shared: [] as CalendarBasics[],
        google: [] as CalendarBasics[],
      },
    );
  });

  /** Get default source */
  const defaultSource = computed(() => {
    const [calendar] = sourcesToRender.value.own.filter(cal => cal.isDefault);

    return calendar;
  });

  const usedColors = computed(() => {
    return sources.value.map(source => source.color);
  });

  const sourceColors = computed<Color[]>(() => {
    return Object.values(CalendarColors).map(color => {
      const isUsedColor = usedColors.value.includes(color);

      return {
        color,
        active: false,
        animate: !isUsedColor,
        disabled: isUsedColor,
      };
    });
  });

  const getSourcePermission = ({
    permissions = null,
    user,
    context,
  }: CalendarBasics): CalendarPermissionType => {
    // Google calendar, readonly
    if (context === CalendarContext.Google || !permissions || !user) {
      return Permission.Read;
    }

    // Own calendar
    if (user.id === whoami.value.id) {
      return Permission.All;
    }

    const permissionTypes = Object.keys(permissions) as (keyof CalendarPermissions)[];

    for (const permissionType of permissionTypes) {
      let permission: CalendarPermissionType;

      // Individual permission, get first item
      if (permissionType === 'individual' && isArrayAndHasItems(permissions.individual)) {
        permission = permissions.individual![0].permission;
      } else {
        permission = permissions[permissionType] as CalendarPermissionType;
      }

      if (permission === Permission.All) {
        return permission;
      }
    }

    return Permission.Read;
  };

  const isSourceDisplayed = (sourceId: string) => {
    return displayedSources.value.includes(sourceId);
  };

  const isCalendarOwn = (calendarId: string) => {
    return sourcesToRender.value.own.some(source => source.id === calendarId);
  };

  const addNewEventInSource = (event: CalendarEvent) => {
    if (isModeFilter.value) {
      for (const source of state.filteredSources) {
        if (source.id !== event.calendarId) {
          continue;
        }

        source.events.push(event);
        break;
      }
    }

    if (isFilterViewActive.value) {
      return;
    }

    for (const source of state.sources) {
      if (source.id !== event.calendarId) {
        continue;
      }

      source.events.push(event);
      break;
    }
  };

  const deleteEventInSource = (eventId: string, calendarId: string) => {
    if (isModeFilter.value) {
      for (const source of state.filteredSources) {
        if (source.id !== calendarId) {
          continue;
        }

        const index = source.events.findIndex(event => event.id === eventId);

        if (index !== -1) {
          source.events.splice(index, 1);
        }
        break;
      }
    }

    if (isFilterViewActive.value) {
      return;
    }

    for (const source of state.sources) {
      if (source.id !== calendarId) {
        continue;
      }

      const index = source.events.findIndex(event => event.id === eventId);

      if (index !== -1) {
        source.events.splice(index, 1);
      }
      break;
    }
  };

  const addNewSource = (source: CalendarSource): CalendarSource => {
    state.sources.push(cloneObject(source));

    if (isModeFilter.value) {
      state.filteredSources.push(cloneObject(source));
    }

    return source;
  };

  const getSourceById = (id: string) => {
    const index = sources.value.findIndex(source => source.id === id);

    if (index === -1) {
      throw new Error(`Source with the ID "${id}" was not found`);
    }

    return { index, source: sources.value[index] };
  };

  const getOppositeSourceById = (id: string) => {
    const opposite = isFilterViewActive.value ? state.sources : state.filteredSources;

    const oppositeIndex = opposite.findIndex(source => source.id === id);
    return { oppositeIndex, oppositeSource: opposite[oppositeIndex] };
  };

  const removeSource = (id: string) => {
    const { index, source } = getSourceById(id);

    if (!source) {
      throw new Error(`Calendar with the ID "${id}" was not found.`);
    }

    return sources.value.splice(index, 1);
  };

  const updateSource = (id: string, data: Partial<CalendarUpdate>) => {
    const { source } = getSourceById(id);

    if (!source) {
      throw new Error(`Calendar with the ID "${id}" was not found.`);
    }

    const keys = Object.keys(data) as (keyof CalendarUpdate)[];

    keys.forEach(key => {
      (source[key] as any) = data[key];
    });

    if (isModeFilter.value) {
      const { oppositeSource } = getOppositeSourceById(id);

      keys.forEach(key => {
        (oppositeSource[key] as any) = data[key];
      });
    }

    return source;
  };

  const transformSource = (source: CalendarOriginalSource): CalendarSource => {
    const { userId, ...newSource } = source;
    let user = null as User | null;

    if (userId !== null) {
      // user = userId === whoami.value.id ? unref(whoami) : getUserLocal(userId);
      user = getUserLocal(userId);
    }

    return {
      ...newSource,
      user,
      permissions: source.permissions ? transformPermission(source.permissions) : null,
    };
  };

  return {
    sources,
    /** Functions */
    getSourcePermission,
    setSources,
    addNewSource,
    addNewEventInSource,
    deleteEventInSource,
    updateSource,
    removeSource,
    getSourceById,
    getOppositeSourceById,
    isSourceDisplayed,
    transformSource,
    transformPermission,
    /** Computed properties */
    isCalendarOwn,
    displayedSources,
    sourcesToRender,
    defaultSource,
    sourceColors,
  };
}
