
import { useCalendarSources, useGlobalProps, useUsers } from '@/composables';
import { computed, defineComponent, nextTick, PropType, reactive, ref, watch } from 'vue';
import {
  UserOutlined,
  PlusOutlined,
  KeyOutlined,
  TeamOutlined,
  CloseOutlined,
  LockOutlined,
} from '@ant-design/icons-vue';
import { CalendarBasics, CalendarPermissionType } from '@/types/calendar.type';
import { Permission, PermissionsType } from '@/utils/enums/global.enum';
import { calendarService } from '@/services';
import { OnChangePermissionArgs, RevokePermissionArgs } from '@/types/services.type';
import { isArrayAndHasItems } from '@/utils/functions';

interface DataItem {
  id: number;
  name: string;
  email: string;
  isOwner: boolean;
  admin: boolean;
  permission: CalendarPermissionType;
}

export default defineComponent({
  components: {
    UserOutlined,
    PlusOutlined,
    KeyOutlined,
    TeamOutlined,
    CloseOutlined,
    LockOutlined,
  },

  props: {
    calendar: {
      type: Object as PropType<CalendarBasics>,
      required: true,
    },
  },

  setup(props) {
    const { isLoading } = useGlobalProps();
    const { whoami, users } = useUsers();
    const { transformPermission, updateSource } = useCalendarSources();

    const modalAddPeople = ref(false);
    const refSelectUser = ref<any>();

    const addPermission = reactive({
      users: [] as number[],
      permission: 'read' as CalendarPermissionType,
    });

    const isOpenSelectUser = ref(false);
    const isVisiblePopConfirm = ref(false);
    const isVisibleTooltipRevokeGlobal = ref(false);

    const sharedWith = ref<DataItem[]>([]);

    const specialPermissions = reactive({
      admin: {
        permission: Permission.Read as CalendarPermissionType,
        label: 'Administrators',
        icon: 'KeyOutlined',
      },
      global: {
        permission: null as CalendarPermissionType | null,
        label: 'All users',
        icon: 'TeamOutlined',
      },
    });

    watch(
      () => props.calendar,
      ({ permissions }) => {
        if (!permissions) {
          return;
        }

        specialPermissions.admin.permission = permissions.admin;

        if (permissions.global) {
          specialPermissions.global.permission = permissions.global;
        } else {
          specialPermissions.global.permission = null;
        }

        if (!permissions.individual) {
          sharedWith.value = [];

          return;
        }

        sharedWith.value = [];

        permissions.individual!.forEach(individual => {
          sharedWith.value.push({ ...individual, isOwner: false });
        });
      },
      { immediate: true },
    );

    watch(modalAddPeople, isOpen => {
      if (!isOpen) return;

      nextTick(() => {
        refSelectUser.value.focus();
      });
    });

    const availableUsers = computed(() => {
      if (
        !props.calendar.permissions ||
        !isArrayAndHasItems(props.calendar.permissions.individual)
      ) {
        return users.value;
      }

      return users.value.filter(user => {
        const ignoreUser = props.calendar.permissions!.individual!.some(
          individual => individual.id === user.id,
        );

        return !ignoreUser;
      });
    });

    const userFilterOption = (input: string, option: any) => {
      input = input.trim();
      const currentUser = users.value.find(item => item.id === option.value);

      if (!currentUser) {
        return false;
      }

      if (currentUser.fullName.toLowerCase().includes(input.toLowerCase())) {
        return true;
      }

      return currentUser.email.toLowerCase().includes(input.toLowerCase());
    };

    const revokePermission = async (type: 'global' | 'individual', userId?: number) => {
      if (type === PermissionsType.Individual && !userId) {
        throw new Error(`Field "userId" is required for revoke permission`);
      }

      const args: RevokePermissionArgs = {
        calendarId: props.calendar.id,
        type,
      };

      if (type === PermissionsType.Individual && userId) {
        args.userId = userId;
      }

      const result = await calendarService.revokePermission(args);

      const newPermissions = transformPermission(result.permissions);
      updateSource(props.calendar.id, { permissions: newPermissions });
    };

    const addUserPermission = async () => {
      const usersToAdd = users.value.filter(user => addPermission.users.includes(user.id));

      await setPermission({
        type: PermissionsType.Individual,
        permission: addPermission.permission,
        userIds: usersToAdd.map(user => user.id),
      });

      modalAddPeople.value = false;

      addPermission.users = [];
      addPermission.permission = 'read';
    };

    const onChangePopConfirm = (visible: boolean) => {
      isVisiblePopConfirm.value = visible;
    };

    const onCancelModalAddPeople = () => {
      addPermission.users = [];
      addPermission.permission = 'read';
    };

    const setPermission = async ({ type, permission, userIds = [] }: OnChangePermissionArgs) => {
      try {
        const result = await calendarService.setPermission({
          calendarId: props.calendar.id,
          type,
          permission,
          userIds,
        });

        const newPermissions = transformPermission(result.permissions);
        updateSource(props.calendar.id, { permissions: newPermissions });
      } catch (error: any) {
        console.error(error);
      }
    };

    const setCalendarAsGlobal = async () => {
      specialPermissions.global.permission = 'read';

      await setPermission({
        type: PermissionsType.Global,
        permission: Permission.Read,
      });
    };

    return {
      whoami,
      sharedWith,
      addPermission,
      refSelectUser,
      isLoading,
      modalAddPeople,
      users,
      availableUsers,
      PermissionsType,
      userFilterOption,
      isOpenSelectUser,
      specialPermissions,
      revokePermission,
      onChangePopConfirm,
      isVisiblePopConfirm,
      isVisibleTooltipRevokeGlobal,
      addUserPermission,
      onCancelModalAddPeople,
      setPermission,
      setCalendarAsGlobal,
    };
  },
});
