
import { computed, defineComponent, nextTick, reactive, ref, toRefs, watch } from 'vue';
import {
  RightOutlined,
  LeftOutlined,
  SettingOutlined,
  PlusOutlined,
  CalendarOutlined,
  DownOutlined,
  AimOutlined,
  FormOutlined,
  GlobalOutlined,
  SearchOutlined,
  ArrowLeftOutlined,
  CloseOutlined,
} from '@ant-design/icons-vue';
import moment from 'moment';
import CalendarSettings from '@/components/CalendarSettings.vue';
import { useCalendarSources, useModeFilter, useTime, useUsers } from '@/composables';
import {
  CalendarBasics,
  ChangeSource,
  ToggleCalendarVisibilityParams,
} from '@/types/calendar.type';
import useGlobal from '@/composables/use-global';
import { createDebounce } from '@/utils/functions';
import { CalendarContext } from '@/utils/enums/global.enum';

interface OnCalendarCheck {
  checked: boolean;
  calendar: {
    id: string;
    name: string;
    color: string;
    show: boolean;
    isDefault: boolean;
  };
}

export default defineComponent({
  components: {
    CalendarSettings,
    RightOutlined,
    LeftOutlined,
    SettingOutlined,
    PlusOutlined,
    CalendarOutlined,
    DownOutlined,
    AimOutlined,
    FormOutlined,
    GlobalOutlined,
    SearchOutlined,
    ArrowLeftOutlined,
    CloseOutlined,
  },
  props: {
    viewTitle: {
      type: String,
      required: true,
    },
    viewName: {
      type: String,
      required: true,
    },
  },
  emits: [
    'changeView',
    'setToday',
    'create',
    'changeViewPage',
    'gotoDate',
    'changeCalendarVisibility',
    'changeSource',
    'fetchEvents',
    'removeCalendar',
  ],
  setup(_, { emit }) {
    const { isLoading } = useGlobal();
    const { whoami } = useUsers();
    const { sourcesToRender, sources } = useCalendarSources();
    const { timezoneLabel } = useTime();
    const {
      isModeFilter,
      isDisplayInputSearch,
      isFilterAppliedWithKeyword,
      paginationStatus,
      keyword,
      setKeyword,
      setVisibilityInputSearch,
      setCalendarsToFilter,
      setPaginationSkip,
      resetPagination,
      setFilterViewAsActive,
    } = useModeFilter();

    const timezoneAutofocus = ref(false);

    const state = reactive({
      showCalendarsDropdown: false,
      viewSelectedLabel: isModeFilter.value ? 'list' : 'month',
      calendars: [] as OnCalendarCheck['calendar'][],
      modalSettings: false,
      popoverCustomDate: moment(),
      createType: 'event' as 'event' | 'calendar',
      sourceToFilter: {} as { [key: string]: boolean },
    });

    const popover = reactive({
      click: false,
      hover: false,
    });

    const debounce = createDebounce();

    const isHideTitle = computed(() => {
      return isModeFilter.value && state.viewSelectedLabel === 'list';
    });

    const isNextDisabled = computed(() => {
      return isHideTitle.value && !paginationStatus.value.next;
    });

    const isPrevDisabled = computed(() => {
      return isHideTitle.value && !paginationStatus.value.prev;
    });

    const paginationLabel = computed(() => {
      if (isHideTitle.value) {
        return { prev: 'Previous page', next: 'Next page' };
      }

      const viewLabel = state.viewSelectedLabel;

      return { prev: `Previous ${viewLabel}`, next: `Next ${viewLabel}` };
    });

    const popoverDateData = computed(() => {
      switch (state.viewSelectedLabel) {
        case 'day':
        case 'week':
        case 'list':
          return { component: 'ADatePicker', format: 'LL' };
        default:
          return { component: 'AMonthPicker', format: 'MMMM YYYY' };
      }
    });

    const sourcesToFilter = computed(() => {
      if (!isDisplayInputSearch) {
        return [];
      }

      const ids: string[] = [];

      for (const source of sources.value) {
        if (!state.sourceToFilter[source.id] && source.context !== CalendarContext.Google) {
          ids.push(source.id);
        }
      }

      return ids;
    });

    watch(
      sourcesToFilter,
      val => {
        setCalendarsToFilter(val);
      },
      { immediate: true },
    );

    const refCalendarSettings = ref({});
    const refInputKeyword = ref();

    const handlePopoverCustomDateChange = (visible: boolean, trigger: 'hover' | 'click') => {
      if (trigger === 'hover' && popover.click) {
        return;
      }

      popover[trigger] = visible;
      popover[trigger === 'hover' ? 'click' : 'hover'] = false;
    };

    /** Current date */
    const today = moment().format('dddd, MMMM D');

    const views = [
      { name: 'timeGridDay', label: 'Day' },
      { name: 'timeGridWeek', label: 'Week' },
      { name: 'dayGridMonth', label: 'Month' },
      { name: isModeFilter.value ? 'filter' : 'listWeek', label: 'List' },
    ];

    const setViewTitle = (viewName: string) => {
      if (viewName === 'listWeek' || viewName === 'filter') {
        state.viewSelectedLabel = 'list';
        return;
      }

      const view = views.find(view => view.name === viewName)!;

      if (!view) {
        console.error(new Error(`View "${viewName}" not found`));
      }

      state.viewSelectedLabel = view.label.toLowerCase();
    };

    const onClickShowSearchBar = () => {
      setVisibilityInputSearch(true);

      nextTick(() => {
        refInputKeyword.value.focus();
      });
    };

    const onClickHideSearchBar = () => {
      const hasFilter = !!keyword.value;
      setVisibilityInputSearch(false);

      hasFilter && emit('fetchEvents');
    };

    const onClickClearKeyword = () => {
      setKeyword('');
      refInputKeyword.value.focus();

      resetPagination();
      emit('fetchEvents');
    };

    const onCheckedCalendarSearch = (checked: boolean, calendar: CalendarBasics) => {
      state.sourceToFilter[calendar.id] = !checked;

      nextTick(() => emit('fetchEvents'));
    };

    const onChangeView = (view: string) => {
      setViewTitle(view);
      emit('changeView', view);

      setFilterViewAsActive(view === 'filter');
    };

    const onSetToday = () => {
      emit('setToday');
    };

    const onGotoCustomDate = () => {
      emit('gotoDate', state.popoverCustomDate.toISOString());

      // Reset date and close popover
      state.popoverCustomDate = moment();
      popover.click = false;
    };

    const onCreate = (type: 'event' | 'calendar') => {
      emit('create', type);
    };

    const onChangeViewPage = (type: 'next' | 'prev') => {
      if (!isHideTitle.value) {
        emit('changeViewPage', type);

        return;
      }

      setPaginationSkip(type);
      emit('fetchEvents');
    };

    const onRemoveCalendar = (id: string) => {
      emit('removeCalendar', id);
    };

    const onClickChangeTimezone = () => {
      state.modalSettings = true;
      timezoneAutofocus.value = true;
    };

    const onClickSettings = () => {
      state.modalSettings = true;
      timezoneAutofocus.value = false;
    };

    const handleMenuClick = ({ item }: any) => {
      state.createType = item.value;
    };

    const onInputSearch = (value: string) => {
      value = value.trimStart();

      if (!value && !keyword.value) {
        return;
      }

      setKeyword(value);

      if (value.length > 0 && value.length < 2) {
        return;
      }

      debounce(() => {
        if (state.viewSelectedLabel !== 'list') {
          onChangeView('filter');
        }

        emit('fetchEvents');
      });
    };

    /** Show/hide calendars */
    const onCalendarChecked = async (calendarId: string, checked: boolean) => {
      emit('changeCalendarVisibility', { calendarId, checked } as ToggleCalendarVisibilityParams);
    };

    const onChangeSource = (data: ChangeSource) => {
      emit('changeSource', data);
    };

    return {
      today,
      views,
      ...toRefs(state),
      isLoading,
      refCalendarSettings,
      refInputKeyword,
      popover,
      whoami,
      keyword,
      setKeyword,
      isModeFilter,
      isHideTitle,
      isDisplayInputSearch,
      isFilterAppliedWithKeyword,
      isNextDisabled,
      isPrevDisabled,
      paginationLabel,
      sourcesToRender,
      sourcesToFilter,
      popoverDateData,
      setVisibilityInputSearch,
      onInputSearch,
      onClickClearKeyword,
      onClickShowSearchBar,
      onClickHideSearchBar,
      onCheckedCalendarSearch,
      onChangeView,
      onSetToday,
      onCreate,
      onChangeViewPage,
      onCalendarChecked,
      onChangeSource,
      handleMenuClick,
      handlePopoverCustomDateChange,
      onGotoCustomDate,
      timezoneLabel,
      timezoneAutofocus,
      onClickChangeTimezone,
      onClickSettings,
      onRemoveCalendar,
    };
  },
});
