
import { computed, defineComponent, PropType, ref } from 'vue';
import Color from '@/components/Color.vue';
import { useCalendarSources } from '@/composables';

export default defineComponent({
  components: { Color },
  props: {
    name: String,
    color: String,
    layout: {
      type: String as PropType<'vertical' | 'horizontal'>,
      default: 'vertical',
      validator: (val: string) => ['vertical', 'horizontal'].includes(val),
    },
    defaultColor: String,
  },
  emits: ['update:name', 'update:color'],
  setup(props, { emit }) {
    const isNameTouched = ref(false);

    const { sourceColors } = useCalendarSources();

    const updateValue = (field: 'name' | 'color', value: string) => {
      emit(`update:${field}` as any, value);

      if (field === 'name' && !isNameTouched.value) {
        isNameTouched.value = true;
      }
    };

    const nameStatus = computed(() => {
      if (isNameTouched.value && !props.name) {
        return {
          help: 'Required field',
          validateStatus: 'error',
        };
      }

      return {
        validateStatus: 'success',
      };
    });

    const formProps = computed(() => {
      if (props.layout === 'vertical') {
        return {
          field: { style: 'width: 89%' },
          formItem: undefined,
        };
      }

      return {
        field: { style: 'width: 85%' },
        formItem: {
          wrapperCol: { span: 18 },
          labelCol: { span: 6 },
        },
      };
    });

    const formItemProps = computed(() => {
      return { ...nameStatus.value, ...formProps.value.formItem };
    });

    const colors = computed(() => {
      const _colors = [...sourceColors.value];

      if (sourceColors.value.every(sourceColor => sourceColor.color !== props.color)) {
        _colors.push({
          color: props.color!,
          disabled: false,
          animate: true,
          isOut: true,
          active: true,
        });
      }

      return _colors;
    });

    const resetFieldName = (name = '') => {
      updateValue('name', name);
      isNameTouched.value = false;
    };

    return {
      updateValue,
      sourceColors,
      nameStatus,
      resetFieldName,
      formProps,
      formItemProps,
      colors,
    };
  },
});
