import { CalendarEvent, EventFrequencyType, WeekDayMinType } from '@/types/calendar.type';

import moment, { Moment } from 'moment';
import { RRule } from 'rrule';
import { notification } from 'ant-design-vue';

interface EventNotification {
  id: string;
  title: string;
  date: string;
  allDay: boolean;
  /** Indicates if this event is a child */
  childId?: string;
}

interface EventScheduler {
  eventId: string;
  timeoutId: number;
  /** Indicates if this event is a child */
  childEventId?: string;
}

interface SaveScheduler {
  delay: number;
  childEventId?: string;
  notification: ShowNotification;
}

interface ShowNotification {
  eventId: string;
  date: Moment | boolean;
  title: string;
  /** Indicates if the scheduler runs ten minutes earlier */
  prev?: boolean;
}

export default class {
  private _today: Moment;

  public notifications: EventNotification[] = [];
  public schedulers: EventScheduler[] = [];

  constructor(private readonly _events: CalendarEvent[]) {
    this._today = moment();
  }

  public run() {
    const events = this.getEventsToNotify();
    this.notifications = events;
    console.log(events);
    events.forEach(event => this.addEventScheduler(event));
    console.log(this.schedulers);
  }

  public addEventScheduler(event: EventNotification) {
    const startAt = moment(event.date);

    // All day even
    if (event.allDay) {
      startAt.add(17, 'hours').add(33, 'minutes');

      // The current time is higher than 8:00 a.m
      if (startAt.isBefore(this._today)) {
        console.log('Past time');
        return;
      }

      // Event notification at 8:00 a.m

      this._registerScheduler({
        delay: startAt.valueOf() - this._today.valueOf(),
        childEventId: event.childId,

        notification: {
          eventId: event.id,
          date: true,
          title: event.title,
        },
      });

      return;
    }

    // Event at an exact time

    const tenMinutesBefore = moment(startAt).subtract(10, 'minutes');

    // Notification 10 minutes before
    if (tenMinutesBefore.isSameOrAfter(this._today)) {
      this._registerScheduler({
        delay: tenMinutesBefore.valueOf() - this._today.valueOf(),
        childEventId: event.childId,

        notification: {
          eventId: event.id,
          date: startAt,
          title: event.title,
          prev: true,
        },
      });
    }

    // Notification at event time
    this._registerScheduler({
      delay: startAt.valueOf() - this._today.valueOf(),
      childEventId: event.childId,

      notification: {
        eventId: event.id,
        date: startAt,
        title: event.title,
      },
    });
  }

  private _registerScheduler(params: SaveScheduler) {
    const timeoutId = setTimeout(
      this._showNotification.bind(this),
      params.delay,

      // Arguments of "_showNotification"
      params.notification,
    );

    // Save to manage later
    this.schedulers.push({
      timeoutId,
      eventId: params.notification.eventId,
      childEventId: params.childEventId,
    });
  }

  private _showNotification(params: ShowNotification) {
    let message = '';

    if (typeof params.date === 'boolean') {
      message = `The event "${params.title}" is now starting`;
    } else {
      message = `At ${params.date.format('H:mm a')} starts the event "${params.title}"`;
    }

    if (params.prev) {
      message = `In ten minutes you have the event "${params.title}"`;
    }

    notification.info({
      message: 'Event notification',
      description: message,
      duration: 5,
      placement: 'bottomRight',
    });

    this._removeNotification(params.eventId);

    console.log('----------------');
    console.log(this.notifications);
    console.log(this.schedulers);
  }

  /** Remove notification and scheduler data */
  private _removeNotification(eventId: string) {
    const notificationIndex = this.notifications.findIndex(notification => {
      return notification.id === eventId;
    });

    if (notification !== -1) {
      this.notifications.splice(notificationIndex, 1);
    }

    const schedulerIndex = this.schedulers.findIndex(scheduler => {
      return scheduler.eventId === eventId;
    });

    if (schedulerIndex !== -1) {
      this.schedulers.splice(notificationIndex, 1);
    }
  }

  /** Get events to notify today */
  public getEventsToNotify(): EventNotification[] {
    const all: EventNotification[] = [];

    for (const event of this._events) {
      // Recurring event
      if (event.rrule) {
        all.push(...this.getEventsToNotifyOfRecurrence(event));
        continue;
      }

      const isActive = this._isActiveEvent(event.start!, event.end!, event.allDay);

      if (!isActive) continue;

      // Event is active
      all.push({
        id: event.id,
        title: event.title,
        date: moment(event.start!).toISOString(),
        allDay: event.allDay,
      });
    }

    return all;
  }

  /** Get events to notify of recurrence today */
  public getEventsToNotifyOfRecurrence(event: CalendarEvent) {
    if (!event.rrule) {
      throw new Error('Event is not recurring');
    }

    const { rrule, exdate } = event;

    const freq = this._getFrequencyNumber(rrule.freq as EventFrequencyType);

    const until = rrule.until ? moment(rrule.until).toDate() : this._today.toDate();

    // Recurrence finished
    if (moment(until).isBefore(this._today)) {
      return [];
    }

    const rule = new RRule({
      freq,
      interval: rrule.interval,
      dtstart: moment(rrule.dtstart).toDate(),
      // Until today, ignoring original until date
      until: this._today.toDate(),
      byweekday: rrule.byweekday as WeekDayMinType[],
    });

    const recurrenceDates = rule.all();

    let eventsActiveToday = recurrenceDates.filter(date =>
      this._isActiveEvent(date, this._today.toDate(), event.allDay),
    );

    // Remove excluded events
    if (exdate && exdate.length) {
      eventsActiveToday = eventsActiveToday.filter(date => {
        const isExcluded = exdate.some(exDate => moment(exDate).isSame(date));

        // Return only events not excluded by user
        return !isExcluded;
      });
    }

    // const { children = [] } = event;
    const children: CalendarEvent[] = [];

    let childId: string | undefined = undefined;

    return eventsActiveToday.map(date => {
      let title = event.title;

      if (children.length) {
        for (const child of children) {
          // Title for this event has been changed by user
          const changeTitle = moment(child.start).isSame(date);

          if (changeTitle) {
            title = child.title;
            childId = child.id;
            break;
          }
        }
      }

      return {
        id: event.id,
        title,
        date: moment(date).toISOString(),
        allDay: event.allDay,
        childId,
      } as EventNotification;
    });
  }

  private _getFrequencyNumber(frequency: EventFrequencyType) {
    return RRule.FREQUENCIES.findIndex(freq => freq === frequency);
  }

  private _isActiveEvent(start: string | Date, end: string | Date, allDay = true) {
    // Event finished
    if (moment(end).isBefore(this._today)) {
      return false;
    }

    const startDate = moment(start);
    const isToday = startDate.isSame(this._today.toDate(), 'days');

    if (allDay) {
      return isToday;
    }

    // The event has not happened
    const isAfter = this._today.diff(startDate) < 0;

    return isToday && isAfter;
  }
}
