
import { CasesService } from '@/services';
import { Case } from '@/types/cases.type';
import { computed, defineComponent, ref, watchEffect } from 'vue';

export default defineComponent({
  props: {
    modelValue: Number,
    contact: Number,
    placeholder: {
      type: String,
      default: 'Select Case',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    style: [String, Object],
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['update:modelValue', 'change', 'hasNotCases'],
  inheritAttrs: false,
  setup(props, { emit }) {
    const cases = ref<Case[]>([]);
    let defaultCases: Case[] = [];
    const disabledField = ref(props.disabled);

    const isDisabled = computed({
      get() {
        return props.disabled || disabledField.value;
      },
      set(val: boolean) {
        disabledField.value = val;
      },
    });

    /** Generate case full name */
    const generateCaseFullName = (currentCase: Case) => {
      const fullname = currentCase.name;

      return currentCase.caseTypeName ? `${fullname} | ${currentCase.caseTypeName}` : fullname;
    };

    /** Select filter option */
    const caseFilterOption = (input: string, option: any) => {
      input = input.trim();
      const currentCase = cases.value.find(item => item.id === option.value);

      if (!currentCase) {
        return false;
      }

      const fullName = generateCaseFullName(currentCase);

      return fullName.toLowerCase().includes(input.toLowerCase());
    };

    /** Update parent value */
    const updateValue = (value?: number) => {
      if (value !== props.modelValue) {
        emit('update:modelValue', value);
        emit('change', value);
      }
    };

    /** Search cases */
    const getCases = async () => {
      // Load from default cases
      if (props.contact === undefined && defaultCases.length) {
        cases.value = defaultCases;
        return;
      }

      // Execute search
      const results = await CasesService.search(props.contact);

      if (!results.length) {
        isDisabled.value = true;
        // Notify to parent
        emit('hasNotCases', true);
        return;
      } else {
        isDisabled.value && (isDisabled.value = false);
      }

      cases.value = results;
      // Set default cases
      if (props.contact === undefined && !defaultCases.length) {
        defaultCases = results;
      }
    };

    const selectProps = computed(() => {
      const _props = {
        placeholder: props.placeholder,
        style: props.style,
        disabled: isDisabled.value,
      } as any;

      if (props.readonly) {
        return {
          ..._props,
          filterOption: false,
          showSearch: false,
          allowClear: false,
          open: false,
        };
      }

      return {
        ..._props,
        filterOption: caseFilterOption,
        showSearch: true,
        allowClear: true,
        onChange: updateValue,
      };
    });

    watchEffect(() => getCases());

    return {
      cases,
      generateCaseFullName,
      selectProps,
    };
  },
});
