<template>
  <AForm ref="refForm" :model="form" layout="vertical">
    <AFormItem
      label="Event Title"
      name="title"
      :rules="{
        required: true,
        message: 'Event title can\'t be empty',
        trigger: 'change',
      }"
    >
      <AInput v-model:value="form.title" placeholder="Type Event Title" autocomplete="off" />
    </AFormItem>

    <ARow :gutter="12">
      <ACol :span="dateCols.date">
        <AFormItem label="Event date">
          <ARangePicker
            v-if="isSplitCurrentEvent || !form.isRecurringEvent"
            v-model:value="dateRange"
            :format="dateFormat"
            :allow-clear="false"
            separator="to"
            style="width: 100%"
          />
          <ADatePicker
            v-else
            v-model:value="form.start"
            :format="dateFormat"
            style="width: 100%"
            :allow-clear="false"
            @change="onChangeDates('start', $event)"
          />
        </AFormItem>
      </ACol>

      <ACol :span="dateCols.time" v-if="!form.allDay">
        <ARow :gutter="12">
          <ACol :span="12">
            <AFormItem label="Starts at">
              <ATimePicker
                v-model:value="form.start"
                use12-hours
                format="h:mm a"
                :allow-clear="false"
                style="width: 100%"
                @change="onChangeDates('start', $event)"
              />
            </AFormItem>
          </ACol>
          <ACol :span="12">
            <AFormItem label="Ends at">
              <ATimePicker
                v-model:value="form.end"
                use12-hours
                format="h:mm a"
                :allow-clear="false"
                style="width: 100%"
                @change="onChangeDates('end', $event)"
              />
            </AFormItem>
          </ACol>
        </ARow>
      </ACol>
    </ARow>

    <AFormItem>
      <ARow :gutter="12">
        <ACol :span="12">
          <ARow>
            <ACol :span="24">
              <VSwitch
                v-model="form.allDay"
                label="All Day Event"
                size="small"
                @change="onChangeAllDay"
              />
            </ACol>
            <ACol v-if="type === 'create'" :span="24">
              <VSwitch
                v-model="includeContact"
                :disabled="!!contactData"
                label="Include Contact"
                size="small"
              />
            </ACol>
          </ARow>
        </ACol>

        <ACol :span="12" v-if="!isHideRecurrenceOptions">
          <VSwitch
            v-model="form.isRecurringEvent"
            label="Is a Recurring Event"
            size="small"
            :disabled="isOriginalEventRecurring"
            @change="onChangeEventType"
          />
        </ACol>
      </ARow>
    </AFormItem>

    <ACard
      v-if="form.isRecurringEvent && !isHideRecurrenceOptions"
      title="Recurrence options"
      body-style="padding-bottom: 0"
      style="margin-bottom: 20px"
    >
      <CalendarRecurrence
        ref="refRecurrence"
        v-model:interval="recurrence.interval"
        v-model:freq="recurrence.freq"
        v-model:byweekday="recurrence.byweekday"
        v-model:ends="recurrence.ends"
        v-model:until="recurrence.until"
        v-model:count="recurrence.count"
        v-model:byDay="recurrence.byDay"
        :dtstart="recurrence.dtstart"
        @change-until="onChangeUntil"
      />
    </ACard>

    <ARow v-if="includeContact || (type === 'edit' && form.contact)" :gutter="12">
      <ACol :span="12">
        <AFormItem
          label="Contact"
          name="contact"
          :rules="{
            required: true,
            message: 'Required field',
            trigger: 'change',
            type: 'number',
          }"
        >
          <SelectContact
            v-model="form.contact"
            :disabled="contactData && !!contactData.id"
            @change="onChangeContact"
          />
        </AFormItem>
      </ACol>

      <ACol :span="12">
        <AFormItem
          label="Case"
          name="case"
          :rules="{
            required: isRequiredCaseField,
            message: 'Required field',
            trigger: 'change',
            type: 'number',
          }"
        >
          <SelectCase
            v-model="form.case"
            :contact="form.contact"
            :disabled="(contactData && !!contactData.caseId) || !form.contact || isLoading"
            @hasNotCases="isRequiredCaseField = false"
          />
        </AFormItem>
      </ACol>
    </ARow>

    <ARow :gutter="12">
      <ACol :span="12">
        <AFormItem label="Timezone">
          <ASelect v-model:value="form.timezone" show-search>
            <ASelectOption
              v-for="(timezoneLabel, timezone) in timezones"
              :key="timezone"
              :value="timezone"
            >
              {{ timezoneLabel }}
            </ASelectOption>
          </ASelect>
        </AFormItem>
      </ACol>
      <ACol :span="10">
        <AFormItem label="Calendar">
          <ASelect
            v-model:value="form.calendarId"
            @change="onChangeCalendar"
            show-search
            :filter-option="onFilterCalendars"
          >
            <ASelectOption
              v-for="calendar of sourcesToRender.own"
              :value="calendar.id"
              :key="calendar.id"
            >
              {{ calendar.name }}
            </ASelectOption>

            <ASelectOption
              v-for="calendar of sharedCalendars"
              :value="calendar.id"
              :key="calendar.id"
            >
              {{ calendar.name }} ({{ calendar.user.fullName }})
            </ASelectOption>
          </ASelect>
        </AFormItem>
      </ACol>
      <ACol :span="2">
        <div style="display: flex; height: 100%; align-items: center">
          <Color v-model="form.color" :colors-data="colors" />
        </div>
      </ACol>
    </ARow>

    <ARow>
      <ACol :span="24">
        <AFormItem label="Location">
          <AInput
            v-model:value="form.location"
            :disabled="isLoading"
            placeholder="610 E 16th St, Holland, MI"
          />
        </AFormItem>
      </ACol>
    </ARow>

    <ARow>
      <ACol :span="24">
        <AFormItem
          label="Attendees"
          name="attendees"
          :rules="{
            trigger: 'change',
            type: 'array',
            validator: attendeesValidator,
          }"
        >
          <ASelect
            v-model:value="form.attendees"
            mode="tags"
            style="width: 100%"
            :token-separators="[',']"
            :disabled="isLoading"
            placeholder="Attendees"
          ></ASelect>
        </AFormItem>
      </ACol>
    </ARow>

    <AFormItem label="Description">
      <Editor v-model="form.description" />
    </AFormItem>
  </AForm>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, provide, reactive, ref, unref, watch } from 'vue';
import CalendarRecurrence from '@/components/CalendarRecurrence.vue';
import VSwitch from '@/components/VSwitch.vue';
import SelectContact from '@/components/SelectContact.vue';
import SelectCase from '@/components/SelectCase.vue';
import Editor from '@/components/Editor.vue';
import Color from '@/components/Color.vue';

import { Moment } from 'moment-timezone';
import {
  CalendarEvent,
  CalendarEventDuration,
  CalendarEventPosition,
  CalendarFormEventNew,
  CalendarRecurrenceNew,
  CalendarRecurringEvent,
  EditRecurringType,
} from '@/types/calendar.type';
import { EventFrequency, Permission } from '@/utils/enums/global.enum';
import {
  formatEventDates,
  getCurrentAvailableTimeRange,
  getRuleDates,
} from '@/helpers/calendar.helper';
import {
  useCalendar,
  useCalendarSources,
  useGlobalProps,
  useModeFilter,
  useTime,
} from '@/composables';
import { CalendarFormSymbol } from '@/utils/symbols';
import { FORMAT_EVENT } from '@/utils/constants';
import { Color as IColor } from '@/types/color.type';
import {
  cloneObject,
  isArrayAndHasItems,
  isoDateWithoutTime,
  isUndefined,
  isValidEmail,
  removeObjectProps,
} from '@/utils/functions';
import { calendarHelper } from '@/helpers';
import timezones from '@/utils/timezones.json';
import moment from 'moment';
import { EventColors } from '@/utils/enums/colors.enum';
import { RuleObject } from 'ant-design-vue/lib/form/interface';

export default defineComponent({
  components: { CalendarRecurrence, VSwitch, Editor, SelectContact, SelectCase, Color },
  props: {
    type: {
      type: String as PropType<'edit' | 'create'>,
      default: 'create',
    },
    data: Object as PropType<CalendarEvent>,
    isDisplay: {
      type: Boolean,
      required: true,
    },
    editRecurringType: String as PropType<EditRecurringType>,
    newEventRange: {
      type: Object as PropType<CalendarEventPosition>,
      required: true,
    },
  },
  setup(props) {
    const { time, timezone } = useTime();
    const { isLoading } = useGlobalProps();
    const { getSourcePermission } = useCalendarSources();
    const {
      selectedSource,
      defaultSource,
      sourcesToRender,
      setCurrentCalendarId,
      currentEventApi,
    } = useCalendar();

    const { isModeFilter, contactData } = useModeFilter();

    const refForm = ref();
    const refRecurrence = ref();
    const isRequiredCaseField = ref(true);

    const form = reactive<CalendarFormEventNew>({
      id: undefined,
      timezone: unref(timezone.value),
      calendarId: '',
      title: undefined,
      start: time(),
      end: time(),
      allDay: false,
      isRecurringEvent: false,
      contact: undefined,
      case: undefined,
      description: undefined,
      color: undefined,
      location: undefined,
      attendees: undefined,
    });

    const recurrence = reactive<CalendarRecurrenceNew>({
      interval: 1,
      freq: EventFrequency.Weekly,
      byweekday: undefined,
      ends: 'never',
      until: undefined,
      dtstart: time(),
      count: undefined,
      byDay: undefined,
    });

    const isEndDateChangedByUser = ref(false);
    const includeContact = ref(true);

    const isOriginalEventRecurring = computed(() => {
      return props.type === 'edit' && !!props.data && !!props.data.rrule;
    });

    const isSplitCurrentEvent = computed(() => {
      return (
        isOriginalEventRecurring.value &&
        props.type === 'edit' &&
        props.editRecurringType === 'current'
      );
    });

    const eventToForm = (event: CalendarEvent) => {
      if (isUndefined(event.contact)) {
        includeContact.value = false;
      }

      let start = {} as Moment;
      let end = null as Moment | null;

      // Recurring event
      if (event.rrule) {
        // Split event from recurrence
        if (isSplitCurrentEvent.value) {
          // Assign dates of selected event
          start = time(currentEventApi.value.startStr, event.timezone);
          end = time(currentEventApi.value.endStr, event.timezone);

          if (end.diff(start, 'day') > 0) {
            end.subtract(1, 'day');
          }
        } else {
          // Edit all recurrence
          const { rrule } = event;

          start = time(event.rrule.dtstart, event.timezone);

          if (rrule.until) {
            end = time(rrule.until, event.timezone);

            // Assign specific time to end date
            if (!event.allDay) {
              let newEnd: string | Moment = calendarHelper.updateDateFromDuration({
                date: event.rrule!.dtstart,
                allDay: false,
                duration: event.duration!,
              });

              newEnd = time(newEnd, event.timezone);

              end.set({
                hours: newEnd.get('hours'),
                minutes: newEnd.get('minutes'),
                seconds: 0,
              });
            }
          }

          // Set other recurrence options
          recurrence.freq = rrule.freq;
          recurrence.interval = rrule.interval;
          rrule.byweekday && (recurrence.byweekday = rrule.byweekday);
          rrule.count && (recurrence.count = rrule.count);

          if (rrule.freq === 'MONTHLY' && rrule.byweekday) {
            recurrence.byDay = true;
          }

          if (rrule.count) {
            recurrence.ends = 'ocurrences';
          } else if (rrule.until) {
            recurrence.ends = 'on';
            recurrence.until = end as Moment;
          } else {
            recurrence.ends = 'never';
          }
        }
      } else {
        // Normal event
        start = time(event.start, event.timezone);
        end = time(event.end, event.timezone);

        if (end.diff(start, 'day') > 0) {
          end.subtract(1, 'day');
        }
      }

      form.id = event.id;
      form.calendarId = event.calendarId;
      form.timezone = event.timezone;
      form.title = event.title;
      form.allDay = event.allDay;
      form.contact = event.contact;
      form.case = event.case;
      form.description = event.description;
      form.color = event.color || selectedSource.value.color;
      form.isRecurringEvent = !!event.rrule;
      form.start = start;
      form.location = event.location;
      form.attendees = event.attendees;

      // Recurring event with until
      if (end) {
        form.end = end;

        return;
      }

      // Recurring event without until date, assign
      form.end = time(
        calendarHelper.updateDateFromDuration({
          date: event.rrule!.dtstart,
          duration: event.duration!,
          allDay: false,
        }),
        event.timezone,
      );
    };

    /** Verify if the dates are in the same day */
    const isDatesSameDay = (start: Moment, end: Moment) => {
      return start.dayOfYear() === end.dayOfYear();
    };

    const setDates = (firstTime = false) => {
      if (props.type === 'create' && firstTime) {
        form.start = time(props.newEventRange.start);
        form.end = time(props.newEventRange.end).subtract(1, 'days');
      }

      if (form.allDay) {
        form.start.set({ hours: 0, minutes: 0, seconds: 0, milliseconds: 0 });
        form.end.set({ hours: 0, minutes: 0, seconds: 0, milliseconds: 0 });

        return;
      }

      let { start, end } = getCurrentAvailableTimeRange(time());

      form.start.set({
        hours: start.get('hours'),
        minutes: start.get('minutes'),
        seconds: start.get('seconds'),
      });

      // Same day
      if (isDatesSameDay(start, end)) {
        form.end.set({
          hours: end.get('hours'),
          minutes: end.get('minutes'),
          seconds: end.get('seconds'),
        });

        return;
      }

      // Different days, non-recurring events
      if (!form.isRecurringEvent) {
        form.end.set({
          hours: start.get('hours') + 1,
          minutes: start.get('minutes'),
          seconds: start.get('seconds'),
        });

        return;
      }

      // Different days, recurring events
      form.end = form.end.clone().endOf('day').set({ milliseconds: 0 });
    };

    watch(
      () => props.isDisplay,
      isDisplay => {
        if (!isDisplay) {
          return resetForm();
        }

        if (props.type === 'create') {
          form.calendarId = defaultSource.value.id;
          form.timezone = timezone.value;
          form.color = defaultSource.value.color;

          if (contactData.value) {
            form.contact = contactData.value.id;

            if (contactData.value.caseId) {
              form.case = contactData.value.caseId;
            }
          }

          setDates(true);
          return;
        }

        // Edit event
        if (!props.data) {
          throw new Error(`For event edition the "data" params is required.`);
        }

        // Set props to form
        eventToForm(props.data);
      },
      { immediate: true },
    );

    watch(
      () => ({ start: form.start, isRecurringEvent: form.isRecurringEvent }),
      ({ start, isRecurringEvent }) => {
        if (!isRecurringEvent) {
          return;
        }

        recurrence.dtstart = start.clone();

        if (props.type === 'edit' || !recurrence.until) {
          return;
        }

        const [dtstart, until] = [
          start.clone().format('YYYY-MM-DD'),
          recurrence.until.clone().format('YYYY-MM-DD'),
        ];

        const isSameOrAfter = moment.utc(dtstart).isSameOrAfter(moment.utc(until));

        if (isSameOrAfter || !isEndDateChangedByUser.value) {
          recurrence.until = start.clone().add(1, 'months');
        }
      },
      { immediate: true },
    );

    // Update time when timezone is changed
    watch(
      () => form.timezone,
      (timezone, oldTimezone) => {
        const start = time(form.start, oldTimezone).format(FORMAT_EVENT);
        const end = time(form.end, oldTimezone).format(FORMAT_EVENT);

        form.start = time(start, timezone);
        form.end = time(end, timezone);
      },
    );
    const dateFormat = 'dddd, MMMM D';

    /** Used in normal events */
    const dateRange = computed<[Moment, Moment]>({
      get() {
        return [form.start, form.end];
      },

      set(dates: [Moment, Moment]) {
        form.start = dates[0];
        form.end = dates[1];
      },
    });

    const dateCols = computed(() => {
      const cols = { date: 24, time: 24 };

      if (!isSplitCurrentEvent.value && form.isRecurringEvent) {
        cols.date = 12;
        cols.time = 12;
      }

      return cols;
    });

    const colors = computed<IColor[]>(() => {
      const _colors = Object.values(EventColors) as string[];

      const appendColor = selectedSource.value
        ? selectedSource.value.color
        : defaultSource.value.color;

      _colors.unshift(appendColor);

      const arrColors: IColor[] = _colors.map(color => ({
        color,
        animate: true,
        disabled: false,
        active: form.color === color,
        fixed: appendColor === color,
      }));

      const _color = (props.data && props.data.color!) || selectedSource.value.color;

      if (
        props.data &&
        props.data.color !== appendColor &&
        _colors.every(color => color !== _color)
      ) {
        arrColors.push({
          color: _color,
          animate: true,
          disabled: false,
          active: _color === form.color,
          isOut: true,
        });
      }

      return arrColors;
    });

    const isSourceColor = computed(() => {
      return sourcesToRender.value.own.some(source => source.color === form.color);
    });

    const isChildEvent = computed(() => {
      if (!props.data) {
        return false;
      }

      return !!props.data.parentId;
    });

    const isHideRecurrenceOptions = computed(() => {
      if (isChildEvent.value) {
        return true;
      }

      if (!isOriginalEventRecurring.value) {
        return false;
      }

      return isSplitCurrentEvent.value;
    });

    const sharedCalendars = computed(() => {
      return sourcesToRender.value.shared.filter(source => {
        const permission = getSourcePermission(source);

        return permission === Permission.All;
      });
    });

    const allCalendars = computed(() => {
      return [...sourcesToRender.value.own, ...sharedCalendars.value];
    });

    const resetForm = () => {
      form.id = undefined;
      form.calendarId = '';
      form.title = undefined;
      form.start = time();
      form.end = time();
      form.allDay = false;
      form.isRecurringEvent = false;
      form.contact = undefined;
      form.case = undefined;
      form.description = undefined;
      form.color = undefined;
      form.location = undefined;
      form.attendees = undefined;

      recurrence.interval = 1;
      recurrence.freq = EventFrequency.Weekly;
      recurrence.byweekday = undefined;
      recurrence.ends = 'never';
      recurrence.until = undefined;
      recurrence.dtstart = time();
      recurrence.count = undefined;
      recurrence.byDay = undefined;

      includeContact.value = true;
      clearValidate('contact');
      clearValidate('case');
    };

    const getEventDuration = () => {
      // Apply time of end date for calculate duration
      const tempStart = form.start.clone().set({
        hours: form.end.get('hours'),
        minutes: form.end.get('minutes'),
        seconds: form.end.get('seconds'),
      });

      return removeObjectProps<CalendarEventDuration>(
        calendarHelper.generateEventDuration(form.start.clone(), tempStart),
        'daysDiff',
      );
    };

    const formToRecurringEvent = () => {
      const rrule: any = {
        dtstart: recurrence.dtstart.toISOString(),
        interval: recurrence.interval,
        freq: recurrence.freq,
      };

      if (recurrence.freq === 'WEEKLY' || (recurrence.freq === 'MONTHLY' && recurrence.byDay)) {
        rrule.byweekday = recurrence.byweekday!;
      }

      if (recurrence.ends === 'on') {
        rrule.until = recurrence.until!.toISOString();
      } else if (recurrence.ends === 'ocurrences') {
        rrule.count = recurrence.count!;

        let dtstart = recurrence.dtstart.toISOString();

        if (!form.allDay) {
          dtstart = recurrence.dtstart.format(FORMAT_EVENT) + '.000Z';
        }

        let tmpUntil = '';

        if (!form.allDay && isArrayAndHasItems(rrule.byweekday)) {
          const offset = recurrence.dtstart.format('Z');

          const dates = calendarHelper.getFormattedRuleDates({
            realStart: dtstart,
            offset,
            event: { rrule } as any,
          });

          const duration = getEventDuration();

          tmpUntil = calendarHelper.updateDateFromDuration({
            date: dates[dates.length - 1],
            duration,
            allDay: form.allDay,
          });
        } else {
          const dates = getRuleDates({ rrule: { ...rrule, dtstart } } as any);

          tmpUntil = isoDateWithoutTime(dates[dates.length - 1]);
        }

        // Assign until date for backend filters
        rrule.until = tmpUntil;
      }

      return rrule as CalendarRecurringEvent;
    };

    const getEvent = (): CalendarEvent => {
      isEndDateChangedByUser.value = false;

      form.start.set({ seconds: 0, milliseconds: 0 });
      form.end.set({ seconds: 0, milliseconds: 0 });

      let event: any = {
        title: form.title!,
        timezone: form.timezone,
        calendarId: form.calendarId!,
        allDay: form.allDay,
        contact: form.contact,
        case: form.case,
        description: form.description || null,
        location: form.location || null,
        attendees: form.attendees || null,
      };

      if (!includeContact.value && props.type === 'create') {
        event = removeObjectProps(event, 'contact', 'case');
      }

      !isSourceColor.value && (event.color = form.color);

      const isUpdateAll = props.editRecurringType === 'all';
      /** Update current normal event, not split from recurrence */
      const isUpdateCurrent = props.editRecurringType === 'current' && !isSplitCurrentEvent.value;

      if (props.type === 'edit' && (isUpdateAll || isUpdateCurrent)) {
        event.id = form.id!;
      }

      // Normal event
      if (!form.isRecurringEvent || isSplitCurrentEvent.value) {
        event.start = form.start.toISOString();

        event.end = !form.allDay
          ? form.end.toISOString()
          : form.end.clone().add(1, 'days').toISOString();

        props.data?.parentId && (event.parentId = props.data!.parentId);
      } else {
        // Recurring event
        event.exdate = props.data?.exdate || [];
        event.rrule = formToRecurringEvent();

        // Default duration, all day event
        event.duration = {
          days: 1,
          hours: 0,
          minutes: 0,
        };

        if (!event.allDay) {
          event.duration = getEventDuration();

          return cloneObject(event) as CalendarEvent;
        }
      }

      if (event.allDay) {
        return formatEventDates(cloneObject(event) as CalendarEvent);
      }

      return cloneObject(event);
    };

    const attendeesValidator = (_: RuleObject, value: string[] = []) => {
      if (!value.length) {
        return Promise.resolve();
      }

      for (const email of value) {
        if (!isValidEmail(email)) {
          return Promise.reject('This field must contain a valid email address list.');
        }
      }

      return Promise.resolve();
    };

    /** Changing between normal/recurring events */
    const onChangeEventType = () => {
      if (!isDatesSameDay(form.start, form.end)) {
        setDates();
      }

      if (form.isRecurringEvent && !isEndDateChangedByUser.value && recurrence.until) {
        recurrence.until = form.end.clone().add(1, 'months');
      }
    };

    const onChangeAllDay = () => {
      if (form.isRecurringEvent && isEndDateChangedByUser.value) {
        return;
      }

      setDates();
      // form.start.set({ hours: 0, minutes: 0, seconds: 0, milliseconds: 0 });
      // form.end.set({ hours: 0, minutes: 0, seconds: 0, milliseconds: 0 });

      if (!recurrence.until) {
        return;
      }

      // Remove time on all day events
      if (form.allDay) {
        recurrence.until.set({ hours: 0, minutes: 0, seconds: 0 });
      } else {
        recurrence.until.set({
          hours: form.end.get('hours'),
          minutes: form.end.get('minutes'),
          seconds: 0,
        });
      }
    };

    const onChangeStartDate = (date: Moment) => {
      if (props.type === 'edit' && recurrence.until) {
        const [dtstart, until] = [
          form.start.clone().format('YYYY-MM-DD'),
          recurrence.until.clone().format('YYYY-MM-DD'),
        ];

        const isSameOrAfter = moment.utc(dtstart).isSameOrAfter(moment.utc(until));

        if (isSameOrAfter) {
          recurrence.until = form.start.clone().add(1, 'months');
        }
      }

      const endTemp = form.start.clone().set({
        hours: form.end.get('hours'),
        minutes: form.end.get('minutes'),
      });

      if (!date.isSameOrAfter(endTemp.clone())) {
        return;
      }

      let newEnd = date.clone().add(1, 'hours');

      // In non-recurring events
      if (!form.isRecurringEvent) {
        form.end = newEnd;
        return;
      }

      // Recurring events
      if (!isDatesSameDay(form.start, newEnd)) {
        newEnd = form.start.clone().endOf('day').set({ milliseconds: 0 });
      }

      form.end = form.end.clone().set({
        hours: newEnd.get('hours'),
        minutes: newEnd.get('minutes'),
      });

      if (form.start.isSameOrAfter(form.end)) {
        form.start = form.start
          .clone()
          .endOf('day')
          .subtract(4, 'minutes')
          .set({ milliseconds: 0 });
      }
    };

    const onChangeEndDate = (date: Moment) => {
      const startTemp = form.end.clone().set({
        hours: form.start.get('hours'),
        minutes: form.start.get('minutes'),
      });

      if (date.isAfter(startTemp)) {
        return;
      }

      let newStart = date.clone().subtract(1, 'hours');

      // In non-recurring events
      if (!form.isRecurringEvent) {
        form.start = newStart;
        return;
      }

      if (!isDatesSameDay(newStart, form.end)) {
        newStart = form.end.clone().startOf('day');
        form.end = form.end.clone().startOf('day').add(5, 'minutes');
      }

      form.start = form.start.clone().set({
        hours: newStart.get('hours'),
        minutes: newStart.get('minutes'),
      });
    };

    const onChangeDates = (field: 'start' | 'end', date: Moment) => {
      field === 'start' ? onChangeStartDate(date) : onChangeEndDate(date);
    };

    const onChangeUntil = () => {
      isEndDateChangedByUser.value = true;
    };

    /** Clear case on contact change */
    const onChangeContact = () => {
      form.case = undefined;
      clearValidate('case');

      isRequiredCaseField.value = true;
    };

    const onChangeCalendar = (calendarId: string) => {
      setCurrentCalendarId(calendarId);

      if (isSourceColor.value) {
        form.color = selectedSource.value.color;
      }
    };

    /** Select filter option */
    const onFilterCalendars = (input: string, option: any) => {
      input = input.trim().toLowerCase();
      const currentCalendar = allCalendars.value.find(calendar => calendar.id === option.value);

      if (!currentCalendar) {
        return false;
      }

      const fullName = `${currentCalendar.name} ${currentCalendar.user?.fullName}`.toLowerCase();

      return fullName.includes(input);
    };

    const clearValidate = (field?: string) => refForm.value.clearValidate(field);

    const validateForm = async () => {
      const promises = [refForm.value.validate()];

      if (form.isRecurringEvent && refRecurrence.value) {
        promises.push(refRecurrence.value.validateForm());
      }

      return Promise.all(promises);
    };

    const onCreated = () => {
      provide(CalendarFormSymbol, form);
    };

    onCreated();

    return {
      form,
      timezones,
      recurrence,
      isRequiredCaseField,
      refForm,
      includeContact,
      isModeFilter,
      contactData,
      refRecurrence,
      selectedSource,
      isLoading,
      isHideRecurrenceOptions,
      isOriginalEventRecurring,
      isChildEvent,
      isSplitCurrentEvent,
      dateRange,
      dateCols,
      dateFormat,
      colors,
      sourcesToRender,
      allCalendars,
      sharedCalendars,
      validateForm,
      clearValidate,
      getEvent,
      attendeesValidator,
      onChangeAllDay,
      onChangeDates,
      onChangeEventType,
      onChangeContact,
      onChangeCalendar,
      onChangeUntil,
      onFilterCalendars,
    };
  },
});
</script>

<style lang="scss" scoped>
.custom-switch {
  cursor: pointer;
  margin-left: 5px;
}
</style>
