import { calendarHelper, dateHelper } from '@/helpers';
import {
  Byweekday,
  CalendarEvent,
  CalendarSource,
  ChildrenToUpdateParams,
  RegenerateByweekdayParams,
  UpdateEvent,
  WeekDayMinType,
} from '@/types/calendar.type';
import { EVENT_EDITABLE_PROPS_OBJ } from '@/utils/constants';
import { cloneObject, isUndefined } from '@/utils/functions';
import moment from 'moment';
import useCalendarSources from './use-calendar-sources';

export default function () {
  const { getSourceById, addNewEventInSource, deleteEventInSource } = useCalendarSources();

  // ----- Getters -----

  const getEventFromSource = (eventId: string, source: CalendarSource) => {
    const index = source.events.findIndex(event => event.id === eventId);

    if (index === -1) {
      throw new Error(`Event ID ${eventId} not found in source ${source.name}`);
    }

    return { eventIndex: index, event: source.events[index] };
  };

  /** Get children events of recurring event */
  const getRecurrenceChildren = (source: string | CalendarSource, eventId: string) => {
    if (typeof source === 'string') {
      const { source: _source } = getSourceById(source);

      source = _source;
    }

    return cloneObject(source.events)
      .filter(event => !!event.parentId && event.parentId === eventId)
      .map(event => {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const { createdBy, ...rest } = event;

        return rest as CalendarEvent;
      });
  };

  const getPropsToUpdateInChildren = (currentEvent: CalendarEvent, newEvent: CalendarEvent) => {
    const props = EVENT_EDITABLE_PROPS_OBJ;

    const changedProps = props.filter(({ prop, optional }) => {
      if (optional) {
        const isAssigned = !isUndefined(newEvent[prop]) && isUndefined(currentEvent[prop]);
        const isRemoved = isUndefined(newEvent[prop]) && !isUndefined(currentEvent[prop]);

        if (isAssigned || isRemoved) {
          return true;
        }
      }

      return currentEvent[prop] !== newEvent[prop];
    });

    return changedProps.map(item => item.prop);
  };

  /** Generate data to update children events via modal  */
  const getChildrenDataToUpdate = ({
    currentEvent,
    newEvent,
    children,
  }: ChildrenToUpdateParams) => {
    const editableProps = getPropsToUpdateInChildren(currentEvent, newEvent);

    if (!editableProps.length) {
      return [];
    }

    // Update data of children
    return children.map(child => {
      /** Properties to modify in children */
      const propsToModify = editableProps.filter(prop => {
        return child[prop] === currentEvent[prop];
      });

      const newData: any = {};

      propsToModify.forEach(prop => {
        // Color: remove
        if (newEvent[prop] === undefined) {
          newData[prop] = null;
        } else {
          // Assign new data
          newData[prop] = newEvent[prop];
        }
      });

      // return data for update this event
      return { filter: { id: child.id, calendarId: child.calendarId }, newData } as UpdateEvent;
    });
  };

  // ----- End getters -----

  // ----- Helpers -----

  const splitExdate = (exdate: string[], splitDate: string) => {
    return exdate.reduce(
      (acc, date) => {
        if (moment.utc(date).isSameOrBefore(splitDate)) {
          return { ...acc, current: [...acc.current, date] };
        }

        return { ...acc, new: [...acc.new, date] };
      },
      {
        current: [] as string[],
        new: [] as string[],
      },
    );
  };

  const regenerateByweekday = ({
    currentByweekday,
    daysDiff,
    freq,
    eventSelectedDate,
  }: RegenerateByweekdayParams) => {
    let byweekday: WeekDayMinType[] | Byweekday[] =
      calendarHelper.byweekdayPropToStr(currentByweekday);

    // Days updated
    byweekday = calendarHelper.updateByweekdayProp(byweekday, daysDiff);
    // Transform to objects: MO -> { weekday: MO }
    byweekday = byweekday.map(item => ({ weekday: item }));

    // Monthly by day: first Monday, last Sunday...
    if (freq === 'MONTHLY') {
      const position = dateHelper.getDayPositionOfMonth(eventSelectedDate);

      // byweekday always have only one item
      byweekday = [{ ...byweekday[0], n: position.n } as Byweekday];
    }

    return byweekday;
  };

  // ----- End helpers -----

  // ----- CRUD -----
  const renewLocalEvent = (event: CalendarEvent, oldCalendarId?: string) => {
    deleteEventInSource(event.id, oldCalendarId || event.calendarId);
    addNewEventInSource(event);

    return true;
  };

  // ----- End CRUD -----

  return {
    // Getters
    getEventFromSource,
    getRecurrenceChildren,
    getChildrenDataToUpdate,
    // Helpers
    splitExdate,
    regenerateByweekday,
    // CRUD
    renewLocalEvent,
  };
}
