
import { defineComponent } from 'vue';

export default defineComponent({
  inheritAttrs: false,
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    label: String,
    disabled: Boolean,
  },
  emits: ['update:modelValue', 'change'],
  setup(props, { emit }) {
    const updateValue = () => {
      if (props.disabled) {
        return;
      }

      emit('update:modelValue', !props.modelValue);
      emit('change', !props.modelValue);
    };

    return { updateValue };
  },
});
