<template>
  <AModal :footer="null" :closable="false">
    <template #title>
      <div style="display: flex; justify-content: flex-end">
        <div>
          <ATooltip v-if="permission === 'all'" title="Edit Event" placement="bottom">
            <APopconfirm
              v-if="isRecurringEvent"
              @confirm="onConfirmEditEvent"
              @visible-change="onChangeVisiblePopConfirm"
            >
              <template #title>
                <div style="display: flex; flex-direction: column">
                  Edit recurring event
                  <ARadioGroup
                    style="margin-top: 0.3rem; margin-left: -22px"
                    :value="$props.editRecurrenceType"
                    @change="updateEditRecurrenceType($event.target.value)"
                  >
                    <ARadio
                      v-if="!$props.hideCurrentCheckItem"
                      value="current"
                      class="radio-vertical"
                    >
                      This event
                    </ARadio>
                    <ARadio
                      v-if="!$props.hideFollowingCheckItem"
                      value="following"
                      class="radio-vertical"
                    >
                      This and following events
                    </ARadio>
                    <ARadio value="all" class="radio-vertical">All events</ARadio>
                  </ARadioGroup>
                </div>
              </template>
              <AButton type="link" style="padding-right: 8px">
                <EditOutlined style="font-size: 18px" />
              </AButton>
            </APopconfirm>

            <AButton v-else @click="onConfirmEditEvent" type="link" style="padding-right: 8px">
              <EditOutlined style="font-size: 18px" />
            </AButton>
          </ATooltip>

          <ATooltip v-if="permission === 'all'" title="Delete Event" placement="bottom">
            <APopconfirm
              ok-text="Yes"
              cancel-text="No"
              @confirm="onConfirmDeleteEvent"
              @visible-change="onChangeVisiblePopConfirm"
            >
              <template #title>
                <div v-if="isRecurringEvent" style="display: flex; flex-direction: column">
                  Delete recurring event
                  <ARadioGroup
                    :value="$props.editRecurrenceType"
                    @change="updateEditRecurrenceType($event.target.value)"
                    style="margin-top: 0.3rem; margin-left: -22px"
                  >
                    <ARadio
                      v-if="!$props.hideCurrentCheckItem"
                      value="current"
                      class="radio-vertical"
                    >
                      This event
                    </ARadio>
                    <ARadio
                      v-if="!$props.hideFollowingCheckItem"
                      value="following"
                      class="radio-vertical"
                    >
                      This and following events
                    </ARadio>
                    <ARadio value="all" class="radio-vertical">All events</ARadio>
                  </ARadioGroup>
                </div>
                <span v-else>Are you sure?</span>
              </template>
              <AButton type="link" style="padding-left: 8px">
                <DeleteOutlined style="font-size: 18px" />
              </AButton>
            </APopconfirm>
          </ATooltip>

          <ATooltip title="Close" placement="bottom">
            <AButton type="link" style="padding-right: 0" @click="closeModal">
              <CloseOutlined style="font-size: 16px" />
            </AButton>
          </ATooltip>
        </div>
      </div>
    </template>
    <h2 style="margin-bottom: 0">{{ event.title }}</h2>

    <div style="display: flex; align-items: center">
      <ColorCircle :color="selectedSource.color" size="extraSmall" :animate="false" />
      <h4 style="color: gray; margin: 0 0 0 0.5rem">
        {{ selectedSource.name }}
      </h4>

      <h4 style="color: gray; margin: 0 0 0 0.8rem">
        <CalendarOutlined style="margin-right: 0.2rem" />
        {{ eventLabel }}
      </h4>

      <ATooltip v-if="!isGoogleEvent" title="Created By" placement="top">
        <h4 style="color: gray; margin: 0 0 0 0.8rem">
          <UserAddOutlined style="margin-right: 0.2rem" />
          {{ createdBy.fullName }}
        </h4>
      </ATooltip>
    </div>
    <h4
      v-if="isRecurringEvent"
      v-html="recurrenceLabel"
      style="color: gray; margin: 0; display: block"
    ></h4>

    <ARow :gutter="12" style="margin-top: 1rem">
      <ACol v-if="!isGoogleEvent && event.contact" :span="12">
        <h4 style="color: grey; margin-bottom: 0.5rem">Contact</h4>
        <SelectContact :model-value="event.contact" style="width: 100%" readonly />
      </ACol>

      <ACol v-if="!isGoogleEvent && event.contact" :span="12">
        <h4 style="color: grey; margin-bottom: 0.5rem">Case</h4>
        <SelectCase
          :model-value="event.case"
          :contact="event.contact"
          :disabled="!event.contact || isLoading"
          readonly
          placeholder="Without Case"
          style="width: 100%"
        />
      </ACol>

      <ACol v-if="showDescription" :span="24" style="margin-top: 0.7rem">
        <h4 style="color: grey; margin-bottom: 0.5rem">Description</h4>
        <Editor :model-value="event.description" disabled></Editor>
      </ACol>
    </ARow>
  </AModal>
</template>

<script lang="ts">
import { useCalendar, useGlobalProps, useTime, useUsers } from '@/composables';
import { computed, defineComponent, nextTick, PropType, ref } from 'vue';
import ColorCircle from '@/components/ColorCircle.vue';
import {
  EditOutlined,
  DeleteOutlined,
  CloseOutlined,
  CalendarOutlined,
  UserAddOutlined,
} from '@ant-design/icons-vue';
import { CustomRecurrenceHelper } from '@/helpers';
import SelectContact from '@/components/SelectContact.vue';
import SelectCase from '@/components/SelectCase.vue';
import Editor from '@/components/Editor.vue';
import { EditRecurringType } from '@/types/calendar.type';
import { CalendarContext } from '@/utils/enums/global.enum';

export default defineComponent({
  components: {
    SelectContact,
    SelectCase,
    Editor,
    ColorCircle,
    EditOutlined,
    DeleteOutlined,
    CloseOutlined,
    CalendarOutlined,
    UserAddOutlined,
  },
  props: {
    editRecurrenceType: {
      type: String as PropType<EditRecurringType>,
      required: true,
    },
    hideFollowingCheckItem: {
      type: Boolean,
      required: false,
    },
    hideCurrentCheckItem: {
      type: Boolean,
      required: false,
    },
  },
  emits: ['update:editRecurrenceType', 'close', 'confirmEditEvent', 'confirmDeleteEvent'],
  setup(_, { emit }) {
    const { selectedSource, currentData, currentEventApi, permission } = useCalendar();
    const { time, timezone, getTimeZoneLabel } = useTime();

    const { isLoading } = useGlobalProps();
    const { users, whoami } = useUsers();

    const isConfirmed = ref(false);
    const event = computed(() => currentData.event);

    const isRecurringEvent = computed(() => !!event.value.rrule);

    const createdBy = computed(() => {
      const createdById = event.value.createdBy;

      if (createdById === whoami.value.id) {
        return whoami.value;
      }

      return users.value.find(user => user.id === event.value.createdBy);
    });

    const recurrenceLabel = computed(() => {
      if (!isRecurringEvent.value) {
        return '';
      }

      const helper = new CustomRecurrenceHelper(event.value.rrule!);

      let label = helper.getLabel();

      if (!event.value.allDay && event.value.timezone !== timezone.value) {
        label += ` <i>${getTimeZoneLabel(event.value.timezone)}</i>`;
      }

      return label;
    });

    const showDescription = computed(() => {
      const description = event.value.description || '';

      return !!description.trim();
    });

    const isGoogleEvent = computed(() => event.value.calendarContext === CalendarContext.Google);

    const eventDate = computed(() => {
      const start = time(currentEventApi.value.startStr);
      const end = time(currentEventApi.value.endStr);

      if (currentEventApi.value.allDay && end.diff(start, 'days')) {
        end.subtract(1, 'days');
      }

      return { start: start.toISOString(), end: end.toISOString() };
    });

    const eventLabel = computed(() => {
      if (currentEventApi.value.allDay) {
        let startStr = time(eventDate.value.start).format('dddd, MMM D');

        // Same day
        if (time(eventDate.value.start).isSame(eventDate.value.end, 'days')) {
          return startStr;
        }

        startStr = time(eventDate.value.start).format('MMMM D');
        const endStr = time(eventDate.value.end).format('MMMM D');

        return `${startStr} - ${endStr}`;
      }

      const startStr = time(eventDate.value.start).format('MMM D, h:mm a');

      const endStr = time(eventDate.value.start).isSame(eventDate.value.end, 'days')
        ? time(eventDate.value.end).format('h:mm a')
        : time(eventDate.value.end).format('MMM D, h:mm a');

      return `${startStr} - ${endStr}`;
    });

    const updateEditRecurrenceType = (value: EditRecurringType) => {
      emit('update:editRecurrenceType', value);
    };

    const onConfirmEditEvent = () => {
      isConfirmed.value = true;
      emit('confirmEditEvent');
      closeModal();
    };

    const onConfirmDeleteEvent = () => {
      isConfirmed.value = true;
      emit('confirmDeleteEvent');
      closeModal();
    };

    const closeModal = () => {
      emit('close');
    };

    const onChangeVisiblePopConfirm = (visible: boolean) => {
      nextTick(() => {
        if (!visible && !isConfirmed.value) {
          updateEditRecurrenceType('current');
          isConfirmed.value = false;
        }
      });
    };

    return {
      event,
      selectedSource,
      permission,
      isRecurringEvent,
      isGoogleEvent,
      showDescription,
      createdBy,
      eventLabel,
      recurrenceLabel,
      isLoading,
      updateEditRecurrenceType,
      closeModal,
      onChangeVisiblePopConfirm,
      onConfirmEditEvent,
      onConfirmDeleteEvent,
    };
  },
});
</script>
