
import { computed, defineComponent, inject, reactive, ref, toRefs, watch } from 'vue';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons-vue';
import Color from '@/components/Color.vue';
import { useCalendarSources, useGlobalProps, useUsers } from '@/composables';
import Permissions from './Permissions.vue';
import { Color as IColor } from '@/types/color.type';
import { ModalSettingsSymbol } from '@/utils/symbols';
import { CalendarCreate, ChangeCalendarData } from '@/types/calendar.type';
import { cloneObject } from '@/utils/functions';

export default defineComponent({
  components: {
    Color,
    Permissions,
    PlusOutlined,
    DeleteOutlined,
  },
  emits: ['readyForUpdate', 'changeSource', 'change', 'createCalendar', 'removeCalendar'],
  setup(_, { emit }) {
    const { isLoading } = useGlobalProps();
    const { sourceColors, sourcesToRender, defaultSource } = useCalendarSources();
    const { whoami, getUserFullName } = useUsers();

    const state = reactive({
      name: '',
      color: '',
    });

    const modalData = inject(ModalSettingsSymbol)!;
    const refNewCalendarName = ref();

    const isNameTouched = ref(false);
    const calendarId = ref(defaultSource.value.id);

    const owner = computed(() => {
      const fullName = getUserFullName(whoami.value);

      return { ...whoami, fullName };
    });

    const calendar = computed(() => {
      return sourcesToRender.value.own.find(item => item.id === calendarId.value)!;
    });

    const isDefaultCalendar = computed(() => {
      return calendar.value.id === defaultSource.value.id;
    });

    const nameStatus = computed(() => {
      if (isNameTouched.value && !state.name) {
        return {
          help: 'Required field',
          validateStatus: 'error',
        };
      }

      return {
        validateStatus: 'success',
      };
    });

    const colors = computed(() => {
      const _colors: IColor[] = [];
      let hasOutColor = true;

      for (const _color of sourceColors.value) {
        if (hasOutColor && _color.color === calendar.value.color) {
          hasOutColor = false;
        }

        _colors.push({ ..._color, active: _color.color === state.color });
      }

      if (hasOutColor) {
        _colors.push({
          color: calendar.value.color,
          disabled: false,
          animate: true,
          isOut: true,
          active: calendar.value.color === state.color,
        });
      }

      return _colors;
    });

    const isReadyForUpdate = computed(() => {
      if (nameStatus.value.validateStatus === 'error') {
        return false;
      }

      if (state.name !== calendar.value.name || state.color !== calendar.value.color) {
        return true;
      }

      return false;
    });

    watch(
      calendar,
      calendarSelected => {
        // Render data of default calendar (removed calendar)
        if (!calendarSelected) {
          calendarId.value = defaultSource.value.id;
          return;
        }

        state.name = calendarSelected.name;
        state.color = calendarSelected.color;
      },
      { immediate: true },
    );

    watch(modalData.activeTab, tab => {
      if (tab === 'calendars') {
        isNameTouched.value = false;

        state.name = calendar.value.name;
        state.color = calendar.value.color;

        calendarId.value = defaultSource.value.id;
      }
    });

    const onChangeName = () => {
      if (!isNameTouched.value) {
        isNameTouched.value = true;
      }
    };

    const onChangeCalendar = () => {
      refNewCalendarName.value && refNewCalendarName.value.focus();
    };

    const onClickRemoveCalendar = () => {
      emit('removeCalendar', calendar.value.id);
    };

    const onClickSave = async () => {
      const keys = Object.keys(state) as ('name' | 'color')[];
      /** Only data updated */
      const updated = keys.reduce<Partial<CalendarCreate>>((data, key) => {
        if (calendar.value[key] !== state[key]) {
          return { ...data, [key]: state[key] };
        }
        return data;
      }, {} as CalendarCreate);

      emit('change', {
        data: updated,
        source: { ...calendar.value, ...updated },
        oldSource: cloneObject(calendar.value),
      } as ChangeCalendarData);
    };

    return {
      ...toRefs(state),
      defaultSource,
      refNewCalendarName,
      sourceColors,
      sources: sourcesToRender,
      nameStatus,
      calendar,
      colors,
      isLoading,
      isReadyForUpdate,
      isDefaultCalendar,
      owner,
      onChangeName,
      onCreateCalendar: () => emit('createCalendar'),
      calendarId,
      onChangeCalendar,
      onClickSave,
      onClickRemoveCalendar,
    };
  },
});
