import { ContactPrima, ContactSearch } from '@/types/contacts.type';
import gql from 'graphql-tag';
import { primaApi } from '@/api';

export default class {
  /** Search contacts */
  public static async search(search?: ContactSearch, getCases = false) {
    try {
      let casesStr = '';

      if (getCases) {
        casesStr = `
          cases {
            id
            name
            caseTypeName
            contactId
          }
        `;
      }

      const { data } = await primaApi.graphql<{ contacts: ContactPrima[] }>({
        query: gql`
          query getContacts($search: searchContact) {
            contacts(search: $search) {
              id
              fullName
              email
              createdAt
              birthDate
              alienNumber
              isSubcontact
              socialSecurityNumber
              citizenship
              accessLink
              ${casesStr}
            }
          }
        `,
        variables: { search },
      });

      return Promise.resolve(data.data.contacts);
    } catch (error: any) {
      return Promise.reject(error);
    }
  }

  /** Search contacts */
  public static async getAll() {
    try {
      const { data } = await primaApi.graphql<{ contacts: ContactPrima[] }>({
        query: gql`
          query {
            contacts {
              id
              fullName
              email
              createdAt
              birthDate
              alienNumber
              isSubcontact
              socialSecurityNumber
              citizenship
              accessLink
            }
          }
        `,
      });

      return Promise.resolve(data.data.contacts);
    } catch (error: any) {
      return Promise.reject(error);
    }
  }
}
