import { Byweekday, CalendarRecurringEvent, WeekDayMinType } from '@/types/calendar.type';
import { DAYS } from '@/utils/constants';
import { DayPosition, EventFrequency, WeekDayMin } from '@/utils/enums/global.enum';
import moment from 'moment';

export default class {
  constructor(private readonly _rrule: CalendarRecurringEvent) {}

  private _getByweekday() {
    const { byweekday } = this._rrule;

    if (!byweekday) {
      throw new Error(`Field "byweekday" is not found`);
    }

    return byweekday;
  }

  private _matchByweekday(item: WeekDayMinType | Byweekday, minDay: WeekDayMinType): boolean {
    if (typeof item === 'string') {
      return item === minDay;
    }

    return item.weekday === minDay;
  }

  /** Convert SU -> Sunday, Mo -> Monday */
  private _generateWeeklyLabel(): string {
    const byweekday = this._getByweekday();

    // const labels = DAYS.filter(day => byweekday.includes(day.min)).map(day => day.full);
    const labels = DAYS.filter(day => {
      return byweekday.some((item: WeekDayMinType | Byweekday) =>
        this._matchByweekday(item, day.min),
      );
    }).map(day => day.full);

    return labels.join(', ');
  }

  /** Verify if the selection days are: Monday, Tuesday, Wednesday, Thursday, Friday */
  private _areSeletionDaysWeekdays(): boolean {
    const byweekday = this._getByweekday();

    const weekdays = [WeekDayMin.Mo, WeekDayMin.Tu, WeekDayMin.We, WeekDayMin.Th, WeekDayMin.Fr];

    if (byweekday.length !== 5) {
      return false;
    }

    // return weekdays.every(day => byweekday.includes(day));
    return weekdays.every(day => {
      return byweekday.some((item: WeekDayMinType | Byweekday) => this._matchByweekday(item, day));
    });
  }

  private _gerateMonthlyByweekday(byweekday: Byweekday) {
    const day = DAYS.find(item => item.min === byweekday.weekday)!;
    let position = '';

    switch (byweekday.n!) {
      case DayPosition.Last:
        position = 'last';
        break;

      case DayPosition.First:
        position = 'first';
        break;

      case DayPosition.Second:
        position = 'second';
        break;

      case DayPosition.Third:
        position = 'third';
        break;

      case DayPosition.Fourth:
        position = 'fourth';
        break;

      default:
        position = '5th';
        break;
    }

    // example: last Friday
    return `${position} ${day.full}`;
  }

  private _generateSelectionAsString() {
    const today = moment(this._rrule.dtstart);

    switch (this._rrule.freq) {
      case EventFrequency.Daily:
        if (this._rrule.interval === 1) {
          return 'Daily';
        }

        return `Every ${this._rrule.interval} days`;

      case EventFrequency.Weekly: {
        const isWeekdays = this._areSeletionDaysWeekdays();

        const rest = isWeekdays ? 'weekdays' : this._generateWeeklyLabel();

        if (this._rrule.interval === 1) {
          return `Weekly on ${rest}`;
        }

        return `Every ${this._rrule.interval} weeks on ${rest}`;
      }

      case EventFrequency.Monthly: {
        let day = today.format('Do'); // Friday

        if (this._rrule.byweekday) {
          // the last Friday
          day = `the ${this._gerateMonthlyByweekday(this._rrule.byweekday[0] as Byweekday)}`;
        }

        if (this._rrule.interval === 1) {
          return `Monthly on ${day}`;
        }

        return `Every ${this._rrule.interval} months on ${day}`;
      }

      // Yearly
      default:
        if (this._rrule.interval === 1) {
          return `Yearly on ${today.format('MMMM D')}`;
        }

        return `Every ${this._rrule.interval} years on ${today.format('MMMM D')}`;
    }
  }

  /** Transform ['MO'] to [false, true, false, false, false, false, false] */
  public getCustomSelectedDays(): boolean[] {
    const byweekday = this._getByweekday();

    // return DAYS.map(day => byweekday.some(minDay => minDay === day.min));
    return DAYS.map(day =>
      byweekday.some((item: WeekDayMinType | Byweekday) => this._matchByweekday(item, day.min)),
    );
  }

  public getLabel() {
    let label = this._generateSelectionAsString();

    if (typeof this._rrule.count !== 'undefined') {
      label += ` for ${this._rrule.count} times`;
    } else {
      if (this._rrule.until) {
        label += ` until ${moment(this._rrule.until).format('MMM D, YYYY')}`;
      }
    }

    return label;
  }

  /** Know if the rule is a custom recurrence */
  public isCustom() {
    const { byweekday = [] } = this._rrule;

    if (this._rrule.interval > 1 || byweekday.length > 1 || this._rrule.until) {
      return true;
    }

    if (this._rrule.freq === 'WEEKLY') {
      const todayIndex = moment(this._rrule.dtstart).day();
      const todayMin = DAYS[todayIndex].min;

      return (byweekday as any[]).every((item: WeekDayMinType | Byweekday) =>
        this._matchByweekday(item, todayMin),
      );
      // return !byweekday.includes(todayMin);
    }

    return false;
  }
}
