import { primaApi } from '@/api';
import {
  CalendarContextType,
  CalendarCreate,
  CalendarEdit,
  CalendarEvent,
  CalendarEventCreate,
  CalendarEventPosition,
  CalendarOriginalSource,
  CalendarSimple,
  CalendarSource,
  CalendarWriteShowFor,
  FilterDeleteEvent,
  GetEventsFilterArgs,
  ReturnCalendarEvent,
  UpdateEvent,
} from '@/types/calendar.type';
import gql from 'graphql-tag';
import { CalendarGQL, EventGQL, UserGQL } from '@/graphql';
import { formatBackendEvent, formatEventToFullCalendar } from '@/helpers/calendar.helper';
import { isArrayAndHasItems, isArrayEmpty } from '@/utils/functions';
import { User } from '@/types/user.type';
import {
  AddPermissionArgs,
  FetchEventsMap,
  RevokePermissionArgs,
  SearchEventsArgs,
  WritePermissionResult,
} from '@/types/services.type';
import { generateBackendNotation } from '@/helpers/permissions.helper';

function returnEvent(event: CalendarEvent): ReturnCalendarEvent {
  const localEvent = formatBackendEvent(event);
  const fullCalendarEvent = formatEventToFullCalendar({ ...localEvent });

  return { localEvent, fullCalendarEvent };
}

export async function searchEvents(search: SearchEventsArgs) {
  const { calendars, ...events } = search;

  const { data } = await primaApi.graphql<any>({
    query: gql`
      query getEvents($events: filterCalendarEvents, $calendars: [String]!) {
        calendarSearch(search: { events: $events, calendars: $calendars }) {
          events {
            ...FullEventFragment
          }
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables: { calendars, events },
  });

  if (data.errors) {
    throw data.errors;
  }
  const calendarEvents = data.data.calendarSearch.events as CalendarEvent[];

  if (!calendarEvents) {
    return [];
  }

  const calendarsResult = calendarEvents.reduce<CalendarSimple[]>((sources, event) => {
    if (!sources.length) {
      const source = {
        id: event.calendarId,
        events: [event],
      };

      return [source];
    }

    const sourceIndex = sources.findIndex(source => source.id === event.calendarId);

    if (sourceIndex !== -1) {
      sources[sourceIndex].events.push(event);

      return sources;
    }

    const source = {
      id: event.calendarId,
      events: [event],
    };

    return [...sources, source];
  }, [] as CalendarSource[]);

  const sources = calendarsResult
    .filter(calendar => !isArrayEmpty(calendar.events))
    .map(calendar => {
      const id = calendar.events[0].calendarId;

      const events = calendar.events.map(event => formatBackendEvent(event));
      const fullCalendarEvents = events.map(event => formatEventToFullCalendar({ ...event }));

      return { id, events, fullCalendarEvents };
    }) as FetchEventsMap[];

  return sources;
}

export async function getCalendars() {
  const { data } = await primaApi.graphql<any>({
    query: gql`
      {
        calendar {
          ...CalendarFullFragment
        }
        users {
          ...UserBasicsFragment
        }
        whoAmI {
          ...UserBasicsFragment
        }
      }
      ${CalendarGQL.fragments.CalendarFullFragment},
      ${UserGQL.fragments.UserBasicsFragment}
    `,
  });

  if (data.errors) {
    throw data.errors;
  }

  const whoami = data.data.whoAmI as User;
  const users = data.data.users as User[];
  let calendars = data.data.calendar as CalendarOriginalSource[];

  // Sanitize calendars and sort
  calendars = calendars
    .map(calendar => {
      return {
        ...calendar,
        /** Omit others default calendars */
        isDefault: calendar.isDefault && calendar.userId === whoami.id,
        events: [],
      };
    })
    .sort(calendar => {
      if (calendar.isDefault) {
        return -1;
      }

      if (calendar.userId === whoami.id) {
        return 0;
      }

      return 1;
    }) as CalendarOriginalSource[];

  return {
    calendars,
    whoami,
    users,
  };
}

export async function getEvents(
  position: CalendarEventPosition,
  filter: GetEventsFilterArgs | null,
): Promise<FetchEventsMap[]> {
  const params = {
    func: '$start: String!, $end: String!',
    events: 'start: $start, end: $end',
  };

  const variables: any = { ...position };

  if (filter) {
    params.func += `, $${filter.field}: Int`;
    params.events += `, ${filter.field}: $${filter.field}`;

    variables[filter.field] = filter.value;
  }

  const { data } = await primaApi.graphql<any>({
    query: gql`
      query getEvents(${params.func}) {
        calendar(filter: { calendar: { show: true }, events: { ${params.events} } }) {
          events {
            ...FullEventFragment
          }
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables,
  });

  if (data.errors) {
    throw data.errors;
  }

  const calendars = data.data.calendar as CalendarSource[];

  const sources = calendars
    .filter(calendar => !isArrayEmpty(calendar.events))
    .map(calendar => {
      const id = calendar.events[0].calendarId;

      const events = calendar.events.map(event => formatBackendEvent(event));
      const fullCalendarEvents = events.map(event => formatEventToFullCalendar({ ...event }));

      return { id, events, fullCalendarEvents };
    });

  return sources;
}

export async function getEventsFromOneCalendar(
  calendarId: string,
  context: CalendarContextType,
  position: CalendarEventPosition,
  filter: GetEventsFilterArgs | null,
) {
  const params = {
    func: '$calendarId: String!, $context: enumCalendarContext!, $start: String!, $end: String!',
    events: 'start: $start, end: $end',
  };

  const variables: any = { calendarId, context, ...position };

  if (filter && context === 'LOCAL') {
    params.func += `, $${filter.field}: Int`;
    params.events += `, ${filter.field}: $${filter.field}`;

    variables[filter.field] = filter.value;
  }

  const { data } = await primaApi.graphql<any>({
    query: gql`
      query getEvents(${params.func}) {
        calendar(
          filter: {
            calendar: { entity: { id: $calendarId, context: $context } }
            events: { ${params.events} }
          }
        ) {
          events {
            ...FullEventFragment
          }
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables,
  });

  if (data.errors) {
    throw data.errors;
  }

  const events = isArrayAndHasItems(data.data.calendar)
    ? (data.data.calendar[0].events as CalendarEvent[])
    : ([] as CalendarEvent[]);

  return events.map(event => returnEvent(event));
}

export async function updateCalendarVisibility(
  context: CalendarContextType,
  id: string,
  show: boolean,
) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation updateCalendar($id: String!, $context: enumCalendarContext!, $show: Boolean!) {
        calendarWriteShowFor(type: $context, calendarId: $id, show: $show) {
          calendarId
          show
          userId
          type
        }
      }
    `,
    variables: { id, context, show },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarWriteShowFor as CalendarWriteShowFor;
}

export async function updateCalendar(id: string, calendar: Partial<CalendarEdit>) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation updateCalendar($id: String!, $editCalendar: editCalendar!) {
        calendarEdit(_id: $id, editCalendar: $editCalendar) {
          ...CalendarWithoutPermissionsFragment
        }
      }
      ${CalendarGQL.fragments.CalendarWithoutPermissionsFragment}
    `,
    variables: { id, editCalendar: calendar },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarEdit as CalendarCreate;
}

export async function createCalendar(calendar: CalendarCreate) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation createCalendar($calendar: newCalendar!) {
        calendarAdd(newCalendar: $calendar) {
          ...CalendarFullFragment
        }
      }
      ${CalendarGQL.fragments.CalendarFullFragment}
    `,
    variables: { calendar },
  });

  if (data.errors) {
    throw data.errors;
  }

  const newCalendar: CalendarOriginalSource = {
    ...data.data.calendarAdd,
    events: [],
  };

  return newCalendar;
}

/** Create event and return the new data */
export async function createEvent(event: CalendarEventCreate) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation createEvent($event: newCalendarEvent!) {
        calendarEventAdd(newCalendarEvent: $event) {
          ...FullEventFragment
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables: { event },
  });

  if (data.errors) {
    throw data.errors;
  }

  return returnEvent(data.data.calendarEventAdd);
}

/** Update events and return the updated data */
export async function updateEvents(...criteria: UpdateEvent[]) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation updateEvents($criteria: [calendarEventCriteriaEdit!]!) {
        calendarEventsEdit(criteria: $criteria) {
          ...FullEventFragment
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables: { criteria },
  });

  if (data.errors) {
    throw data.errors;
  }

  const events = data.data.calendarEventsEdit as CalendarEvent[];

  return events.map(event => returnEvent(event));
}

export async function deleteEvents(...filters: FilterDeleteEvent[]): Promise<CalendarEvent[]> {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation deleteEvents($filters: [calendarEventFilterDelete!]!) {
        calendarEventsDelete(filters: $filters) {
          ...FullEventFragment
        }
      }
      ${EventGQL.fragments.FullEventFragment}
    `,
    variables: { filters },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarEventsDelete.map((event: CalendarEvent) => formatBackendEvent(event));
}

export async function setPermission(args: AddPermissionArgs) {
  const permission = generateBackendNotation(args.permission, ...(args.userIds ?? []));

  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation addPermission(
        $id: String!
        $type: enumTypePermission!
        $permission: newCalendarPermission!
      ) {
        calendarPermissionWrite(id: $id, type: $type, permission: $permission) {
          id
          userId
          permissions {
            admin {
              all
              read
            }
            global {
              all
              read
            }
            individual {
              userId
              all
              read
            }
          }
        }
      }
    `,
    variables: { id: args.calendarId, type: args.type, permission },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarPermissionWrite as WritePermissionResult;
}

export async function revokePermission(args: RevokePermissionArgs) {
  const permission = generateBackendNotation('quit', ...(args.userId ? [args.userId] : []));

  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation revokePermission(
        $id: String!
        $type: enumTypePermission!
        $permission: newCalendarPermission!
      ) {
        calendarPermissionWrite(id: $id, type: $type, permission: $permission) {
          id
          userId
          permissions {
            admin {
              all
              read
            }
            global {
              all
              read
            }
            individual {
              userId
              all
              read
            }
          }
        }
      }
    `,
    variables: { id: args.calendarId, type: args.type, permission },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarPermissionWrite as WritePermissionResult;
}

export async function removeCalendar(id: string) {
  const { data } = await primaApi.graphql<any>({
    mutation: gql`
      mutation removeCalendar($id: String!) {
        calendarRemove(id: $id) {
          id
          name
          eventsRemoved
        }
      }
    `,
    variables: { id },
  });

  if (data.errors) {
    throw data.errors;
  }

  return data.data.calendarRemove as { id: string; name: string; eventsRemoved: number };
}
