<template>
  <AForm ref="refForm" :model="$props" layout="vertical" class="CustomRecurrence">
    <ARow :gutter="12">
      <ACol :span="24">
        <AFormItem label="Repeat every">
          <AInputGroup compact>
            <AInputNumber
              :value="$props.interval"
              type="number"
              :max="365"
              :min="1"
              style="width: 30%"
              @change="$emit('update:interval', $event)"
            />
            <ASelect :value="$props.freq" @change="onChangeFreq" style="width: 70%">
              <ASelectOption :value="frequency.Daily">
                {{ periodLabel('Day') }}
              </ASelectOption>
              <ASelectOption :value="frequency.Weekly">
                {{ periodLabel('Week') }}
              </ASelectOption>
              <ASelectOption :value="frequency.Monthly">
                {{ periodLabel('Month') }}
              </ASelectOption>
              <ASelectOption :value="frequency.Yearly">
                {{ periodLabel('Year') }}
              </ASelectOption>
            </ASelect>
          </AInputGroup>
        </AFormItem>
      </ACol>
    </ARow>

    <ARow v-if="$props.freq === frequency.Monthly" :gutter="12" style="margin-bottom: 20px">
      <ACol :span="7" style="line-height: 30px">
        <VSwitch :modelValue="$props.byDay" label="By day" size="small" @change="onChangeByDay" />
      </ACol>

      <ACol :span="17" v-if="$props.byDay">
        <AInputGroup compact>
          <ASelect
            v-model:value="internalByWeekday.monthly.n"
            style="width: 35%"
            @change="onChangeMonthlyWeekday('n', $event)"
          >
            <ASelectOption :value="1">1st</ASelectOption>
            <ASelectOption :value="2">2nd</ASelectOption>
            <ASelectOption :value="3">3rd</ASelectOption>
            <ASelectOption :value="4">4th</ASelectOption>
            <ASelectOption :value="5">5th</ASelectOption>
            <ASelectOption :value="-1">Last</ASelectOption>
          </ASelect>
          <ASelect
            v-model:value="internalByWeekday.monthly.weekday"
            style="width: 65%"
            @change="onChangeMonthlyWeekday('weekday', $event)"
          >
            <ASelectOption v-for="day of allDays" :key="day.min" :value="day.min">
              {{ day.full }}
            </ASelectOption>
          </ASelect>
        </AInputGroup>
      </ACol>
    </ARow>

    <AFormItem
      v-if="$props.freq === frequency.Weekly"
      label="Repeat on"
      name="byweekday"
      :rules="{
        required: true,
        type: 'array',
        message: 'Please select the days to repeat',
      }"
    >
      <div class="tags-days-container">
        <ATooltip v-for="day of allDays" :key="day.short" :title="day.full">
          <ACheckableTag
            :checked="isDayChecked(day.min)"
            @change="onChangeByweekday(day.min, $event)"
          >
            {{ day.short }}
          </ACheckableTag>
        </ATooltip>
      </div>
    </AFormItem>

    <AFormItem label="Ends" style="margin-bottom: 10px">
      <ARadioGroup
        :value="$props.ends"
        @change="onChangeEnds($event.target.value)"
        style="width: 100%"
      >
        <div style="display: flex; justify-content: space-between">
          <ARadio value="never" style="display: block; line-height: 30px">Never</ARadio>
        </div>
        <div style="display: flex; justify-content: space-between">
          <ARadio value="on" style="display: block; line-height: 30px">On</ARadio>
          <ADatePicker
            v-if="$props.ends === 'on'"
            :value="$props.until"
            :allow-clear="false"
            :disabled-date="disabledDate"
            @change="onChangeUntil"
          >
            <span class="display-date">{{ untilDateLabel }}</span>
          </ADatePicker>
        </div>
        <div style="display: flex; justify-content: space-between">
          <ARadio value="ocurrences" style="display: block; line-height: 30px">Ocurrences</ARadio>
          <div v-if="$props.ends === 'ocurrences'">
            <AInputNumber
              type="number"
              :value="$props.count"
              :min="1"
              class="custom input-text-center"
              style="width: 60px; margin-right: 5px"
              @change="$emit('update:count', $event)"
            />
            times
          </div>
        </div>
      </ARadioGroup>
    </AFormItem>
  </AForm>
</template>

<script lang="ts">
import {
  computed,
  defineComponent,
  inject,
  nextTick,
  PropType,
  reactive,
  ref,
  watch,
  watchEffect,
} from 'vue';
import VSwitch from '@/components/VSwitch.vue';
import {
  Byweekday,
  CalendarRecurrenceEndsType,
  EventFrequencyType,
  WeekDayMinType,
} from '@/types/calendar.type';
import { Moment } from 'moment';
import { EventFrequency } from '@/utils/enums/global.enum';
import { DAYS } from '@/utils/constants';
import { isArrayEmpty, isObjectEmpty, valueIs } from '@/utils/functions';
import { CalendarFormSymbol } from '@/utils/symbols';
import { useTime } from '@/composables';
import { dateHelper } from '@/helpers';

export default defineComponent({
  components: { VSwitch },
  props: {
    interval: {
      type: Number,
      default: 1,
    },
    freq: {
      type: String as PropType<EventFrequencyType>,
      default: EventFrequency.Weekly,
    },
    byweekday: Array as PropType<Byweekday[]>,
    ends: {
      type: String as PropType<CalendarRecurrenceEndsType>,
      default: 'never',
    },
    until: Object as PropType<Moment>,
    count: Number,
    dtstart: {
      type: Object as PropType<Moment>,
      required: true,
    },
    byDay: Boolean,
  },

  emits: [
    'update:interval',
    'update:freq',
    'update:byweekday',
    'update:ends',
    'update:until',
    'update:count',
    'update:byDay',
    'changeUntil',
  ],

  setup(props, { emit }) {
    const { time } = useTime();
    const parentForm = inject(CalendarFormSymbol)!;

    const refForm = ref();
    const allDays = DAYS;

    const internalByWeekday = reactive({
      weekly: [] as Byweekday[],
      monthly: {} as Byweekday,
    });

    /** Generate period label */
    const periodLabel = (label: string) => {
      return props.interval > 1 ? `${label}s` : label;
    };

    const setWeekday = (firstTime = false) => {
      // Not reseat passed values from parent
      if (firstTime && !isArrayEmpty(props.byweekday || [])) {
        return;
      }

      // Set selected day as checked in select days
      const day = props.dtstart.day();
      const itemDay = allDays.find(item => item.numeric.fullcalendar === day)!;

      // Not cached
      if (firstTime) {
        // Monthly "byDay"
        internalByWeekday.monthly = {
          weekday: itemDay.min,
          n: 1,
        };
      }

      if (props.freq === 'WEEKLY') {
        internalByWeekday.weekly = [{ weekday: itemDay.min }];
        return emit('update:byweekday', [{ weekday: itemDay.min }]);
      }

      if (!props.byDay) {
        return;
      }

      emit('update:byweekday', [internalByWeekday.monthly]);
    };

    // Validate "byweekday" prop in real time
    watch(
      () => props.byweekday,
      async () => {
        if (!refForm.value) return;

        try {
          await refForm.value.validateField('byweekday');
        } catch (error: any) {
          error.errorFields.length && console.error(error);
        }
      },
      { deep: true },
    );

    watchEffect(() => {
      if (!props.interval) {
        emit('update:interval', 1);
      }
    });

    watch(
      () => props.dtstart,
      dtstart => {
        if (
          props.byweekday?.length !== 1 ||
          !valueIs(props.freq, EventFrequency.Monthly, EventFrequency.Weekly)
        ) {
          return;
        }

        const day = dateHelper.getWeekday(dtstart);
        const weekday = { weekday: day.min } as Byweekday;

        const byweekday = dateHelper.getDayPositionOfMonth(dtstart);
        internalByWeekday.monthly = byweekday;

        // Monthly
        if (props.freq === 'MONTHLY') {
          if (!props.byDay) {
            return;
          }

          emit('update:byweekday', [byweekday]);
          return;
        }

        internalByWeekday.weekly = [weekday];
        // Weekly
        emit('update:byweekday', [weekday]);
      },
    );

    const untilDateLabel = computed(() => {
      if (!props.until) {
        return null;
      }

      return time(props.until).format('dddd, MMMM D, YYYY');
    });

    const disabledDate = (current: Moment) => {
      // Can not select days before today and today
      return current && current <= props.dtstart.clone().add(1, 'days').startOf('day');
    };

    const isDayChecked = (day: WeekDayMinType) => {
      return props.byweekday && props.byweekday.some(item => item.weekday === day);
    };

    const sortDays = (days: Byweekday[]): Byweekday[] => {
      return allDays
        .filter(item => days.some(day => day.weekday === item.min))
        .map(item => ({ weekday: item.min }));
    };

    const onChangeFreq = (freq: EventFrequencyType) => {
      emit('update:freq', freq);

      // Monthly "byDay"
      if (freq === 'MONTHLY' && props.byDay) {
        return emit('update:byweekday', [internalByWeekday.monthly]);
      }

      if (freq !== 'WEEKLY') {
        return;
      }

      if (isArrayEmpty(internalByWeekday.weekly)) {
        nextTick(setWeekday);
      } else {
        emit('update:byweekday', internalByWeekday.weekly);
      }
    };

    const onChangeByweekday = (day: WeekDayMinType, checked: boolean) => {
      let newDays = props.byweekday || [];

      if (!checked) {
        newDays = newDays.filter(v => v.weekday !== day);
      } else {
        newDays.push({ weekday: day });
      }

      const sorted = sortDays(newDays);

      internalByWeekday.weekly = sorted;
      emit('update:byweekday', sorted);
    };

    const onChangeByDay = (checked: boolean) => {
      emit('update:byDay', checked);

      nextTick(() => {
        // Set today as selected
        if (checked) {
          if (isObjectEmpty(internalByWeekday.monthly)) {
            const byweekday = dateHelper.getDayPositionOfMonth(props.dtstart);

            internalByWeekday.monthly = byweekday;
          }

          return setWeekday();
        }

        emit('update:byweekday', undefined);
      });
    };

    const onChangeEnds = (ends: 'never' | 'on' | 'ocurrences') => {
      emit('update:ends', ends);

      if (ends === 'on' && !props.until) {
        emit('update:until', parentForm.end.clone().add(1, 'months'));
        return;
      }

      if (ends === 'ocurrences' && !props.count) {
        emit('update:count', 10);
      }
    };

    const onChangeMonthlyWeekday = (field: 'n' | 'weekday', value: WeekDayMinType | number) => {
      const byweekday = { ...internalByWeekday.monthly, [field]: value };
      emit('update:byweekday', [byweekday]);
    };

    const onChangeUntil = (date: string) => {
      emit('update:until', date);

      emit('changeUntil', date);
    };

    const validateForm = () => refForm.value.validate();

    const onCreated = () => {
      setWeekday(true);
      const isEmptyByweekday = isArrayEmpty(props.byweekday || []);

      if (isEmptyByweekday) {
        return;
      }

      if (props.freq === 'WEEKLY') {
        internalByWeekday.weekly = props.byweekday!;
        return;
      }

      // Monthly by day
      if (props.byDay || props.freq === 'MONTHLY') {
        internalByWeekday.monthly = props.byweekday![0];
        return;
      }
    };

    onCreated();

    return {
      parentForm,
      internalByWeekday,
      periodLabel,
      untilDateLabel,
      validateForm,
      refForm,
      allDays,
      frequency: EventFrequency,
      disabledDate,
      isDayChecked,
      onChangeFreq,
      onChangeByweekday,
      onChangeByDay,
      onChangeEnds,
      onChangeMonthlyWeekday,
      onChangeUntil,
    };
  },
});
</script>

