<template>
  <section class="CalendarToolbar">
    <template v-if="!isDisplayInputSearch">
      <!-- Next/Prev buttons -->
      <div class="CalendarToolbar__view-info">
        <ATooltip v-if="!isHideTitle" :title="today">
          <AButton @click="onSetToday">Today</AButton>
        </ATooltip>

        <template v-if="!isHideTitle">
          <ATooltip :title="paginationLabel.prev">
            <AButton
              :disabled="isPrevDisabled"
              type="link"
              style="padding-right: 0.5rem"
              @click="onChangeViewPage('prev')"
            >
              <LeftOutlined />
            </AButton>
          </ATooltip>

          <ATooltip :title="paginationLabel.next">
            <AButton
              :disabled="isNextDisabled"
              type="link"
              style="padding-left: 0.5rem"
              @click="onChangeViewPage('next')"
            >
              <RightOutlined />
            </AButton>
          </ATooltip>
        </template>

        <!-- Display calendar view title -->
        <APopover
          content="Go to custom date"
          trigger="hover"
          :visible="popover.hover"
          @visibleChange="handlePopoverCustomDateChange($event, 'hover')"
          overlayClassName="popover-to-tooltip"
        >
          <APopover
            trigger="click"
            title="Go to custom date"
            :visible="popover.click"
            @visibleChange="handlePopoverCustomDateChange($event, 'click')"
          >
            <template #content>
              <div style="margin-bottom: 3px">
                <component
                  :is="popoverDateData.component"
                  v-model:value="popoverCustomDate"
                  :format="popoverDateData.format"
                  placeholder="Select date"
                  :allow-clear="false"
                />
                <AButton style="margin-left: 1rem" @click="onGotoCustomDate"> Go </AButton>
              </div>
            </template>
            <AButton v-if="!isHideTitle" type="link" style="margin-right: -10px; padding: 0">
              <AimOutlined />
              <span class="CalendarToolbar__title">
                {{ $props.viewTitle }}
              </span>
            </AButton>
          </APopover>
        </APopover>

        <ATooltip :title="timezoneLabel">
          <AButton
            type="link"
            @click="onClickChangeTimezone"
            style="display: flex; align-items: center"
          >
            <GlobalOutlined />
            <span class="label-timezone">
              {{ timezoneLabel }}
            </span>
          </AButton>
        </ATooltip>
      </div>
      <div class="CalendarToolbar__options">
        <ATooltip v-if="isModeFilter" title="Search events">
          <AButton type="link" style="margin-right: -10px" @click="onClickShowSearchBar">
            <SearchOutlined style="font-size: 18px" />
          </AButton>
        </ATooltip>

        <ATooltip title="Calendar settings">
          <AButton type="link" @click="onClickSettings">
            <SettingOutlined style="font-size: 18px" />
          </AButton>
        </ATooltip>

        <ATooltip title="Calendars">
          <ADropdown
            v-if="!isHideTitle"
            v-model:visible="showCalendarsDropdown"
            :trigger="['click']"
          >
            <template #overlay>
              <AMenu class="dropdown-calendar">
                <AMenuItemGroup key="own" title="My Calendars">
                  <AMenuItem
                    v-for="calendar of sourcesToRender.own"
                    :key="calendar.id"
                    style="display: flex; align-items: center; justify-content: space-between"
                  >
                    <ACheckbox
                      :checked="calendar.show"
                      @change="onCalendarChecked(calendar.id, !calendar.show)"
                    >
                      {{ calendar.name }}
                    </ACheckbox>
                    <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                  </AMenuItem>
                </AMenuItemGroup>

                <AMenuItemGroup
                  v-if="sourcesToRender.shared.length"
                  key="shared"
                  title="Shared Calendars"
                  style="margin: 0"
                >
                  <AMenuItem
                    v-for="calendar of sourcesToRender.shared"
                    :key="calendar.id"
                    style="display: flex; align-items: center; justify-content: space-between"
                  >
                    <ACheckbox
                      :checked="calendar.show"
                      @change="onCalendarChecked(calendar.id, !calendar.show)"
                    >
                      {{ calendar.name }} ({{ calendar.user.fullName }})
                    </ACheckbox>
                    <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                  </AMenuItem>
                </AMenuItemGroup>

                <AMenuItemGroup
                  v-if="sourcesToRender.google.length"
                  key="google"
                  title="Google Calendars"
                  style="margin: 0"
                >
                  <AMenuItem
                    v-for="calendar of sourcesToRender.google"
                    :key="calendar.id"
                    style="display: flex; align-items: center; justify-content: space-between"
                  >
                    <ACheckbox
                      :checked="calendar.show"
                      @change="onCalendarChecked(calendar.id, !calendar.show)"
                    >
                      {{ calendar.name }}
                    </ACheckbox>
                    <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                  </AMenuItem>
                </AMenuItemGroup>
              </AMenu>
            </template>
            <AButton style="margin-right: 0.5rem">
              <CalendarOutlined />
              <span class="label-calendars">Calendars</span>
            </AButton>
          </ADropdown>

          <ADropdown v-else v-model:visible="showCalendarsDropdown" :trigger="['click']">
            <template #overlay>
              <AMenu class="dropdown-calendar">
                <AMenuItemGroup key="own" title="My Calendars">
                  <AMenuItem
                    v-for="calendar of sourcesToRender.own"
                    :key="calendar.id"
                    style="display: flex; align-items: center; justify-content: space-between"
                  >
                    <ACheckbox
                      :disabled="sourcesToFilter.length === 1 && !sourceToFilter[calendar.id]"
                      :checked="!sourceToFilter[calendar.id]"
                      @change="onCheckedCalendarSearch($event.target.checked, calendar)"
                    >
                      {{ calendar.name }}
                    </ACheckbox>
                    <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                  </AMenuItem>
                </AMenuItemGroup>

                <AMenuItemGroup
                  v-if="sourcesToRender.shared.length"
                  key="shared"
                  title="Shared Calendars"
                  style="margin: 0"
                >
                  <AMenuItem
                    v-for="calendar of sourcesToRender.shared"
                    :key="calendar.id"
                    style="display: flex; align-items: center; justify-content: space-between"
                  >
                    <ACheckbox
                      :disabled="sourcesToFilter.length === 1 && !sourceToFilter[calendar.id]"
                      :checked="!sourceToFilter[calendar.id]"
                      @change="onCheckedCalendarSearch($event.target.checked, calendar)"
                    >
                      {{ calendar.name }} ({{ calendar.user.fullName }})
                    </ACheckbox>
                    <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                  </AMenuItem>
                </AMenuItemGroup>
              </AMenu>
            </template>
            <AButton>
              <CalendarOutlined />
              <span class="label-calendars">Calendars</span>
            </AButton>
          </ADropdown>
        </ATooltip>

        <ATooltip title="Change view">
          <ASelect
            :value="$props.viewName"
            style="width: 90px; margin: 0 0.5rem"
            @change="onChangeView"
          >
            <ASelectOption v-for="view of views" :value="view.name" :key="view.name">
              {{ view.label }}
            </ASelectOption>
          </ASelect>
        </ATooltip>

        <ATooltip title="Create new event">
          <AButton type="primary" @click="onCreate('event')" style="margin-left: 0.5rem">
            <PlusOutlined />
            <span class="label-create-event">Event</span>
          </AButton>
        </ATooltip>
      </div>
    </template>

    <template v-else>
      <section style="display: flex; width: 100%">
        <div class="CalendarToolbar__search-back">
          <AButton type="link" @click="onClickHideSearchBar">
            <ArrowLeftOutlined />
          </AButton>
          <h3>Search</h3>
        </div>

        <AInput
          ref="refInputKeyword"
          :value="keyword"
          placeholder="Search"
          @input="onInputSearch($event.target.value)"
          style="max-width: 450px; margin-right: 1.5rem"
        >
          <template #prefix>
            <SearchOutlined style="margin-right: 5px; color: rgba(0, 0, 0, 0.4)" />
          </template>

          <template #suffix>
            <ATooltip v-if="keyword.length" title="Clear input">
              <AButton type="link" style="padding: 0" @click="onClickClearKeyword">
                <CloseOutlined style="font-size: 10px; color: rgba(0, 0, 0, 0.4)" />
              </AButton>
            </ATooltip>
          </template>
        </AInput>

        <ADropdown v-model:visible="showCalendarsDropdown" :trigger="['click']">
          <template #overlay>
            <AMenu class="dropdown-calendar">
              <AMenuItemGroup key="own" title="My Calendars">
                <AMenuItem
                  v-for="calendar of sourcesToRender.own"
                  :key="calendar.id"
                  style="display: flex; align-items: center; justify-content: space-between"
                >
                  <ACheckbox
                    :disabled="sourcesToFilter.length === 1 && !sourceToFilter[calendar.id]"
                    :checked="!sourceToFilter[calendar.id]"
                    @change="onCheckedCalendarSearch($event.target.checked, calendar)"
                  >
                    {{ calendar.name }}
                  </ACheckbox>
                  <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                </AMenuItem>
              </AMenuItemGroup>

              <AMenuItemGroup
                v-if="sourcesToRender.shared.length"
                key="shared"
                title="Shared Calendars"
                style="margin: 0"
              >
                <AMenuItem
                  v-for="calendar of sourcesToRender.shared"
                  :key="calendar.id"
                  style="display: flex; align-items: center; justify-content: space-between"
                >
                  <ACheckbox
                    :disabled="sourcesToFilter.length === 1 && !sourceToFilter[calendar.id]"
                    :checked="!sourceToFilter[calendar.id]"
                    @change="onCheckedCalendarSearch($event.target.checked, calendar)"
                  >
                    {{ calendar.name }} ({{ calendar.user.fullName }})
                  </ACheckbox>
                  <div class="show-color" :style="{ backgroundColor: calendar.color }" />
                </AMenuItem>
              </AMenuItemGroup>
            </AMenu>
          </template>
          <AButton style="margin-right: 0.5rem">
            Calendars
            <CalendarOutlined />
          </AButton>
        </ADropdown>

        <!-- <template v-if="isFilterAppliedWithKeyword">
          <ATooltip :title="paginationLabel.prev">
            <AButton
              :disabled="isPrevDisabled"
              type="link"
              style="margin-left: 0.5rem"
              @click="onChangeViewPage('prev')"
            >
              <LeftOutlined />
            </AButton>
          </ATooltip>

          <ATooltip :title="paginationLabel.next">
            <AButton
              :disabled="isNextDisabled"
              type="link"
              style="margin-right: 0.5rem"
              @click="onChangeViewPage('next')"
            >
              <RightOutlined />
            </AButton>
          </ATooltip>
        </template> -->
      </section>
    </template>
  </section>

  <AModal
    v-model:visible="modalSettings"
    title="Settings"
    width="650px"
    :footer="null"
    :after-close="() => (refCalendarSettings.tab = 'general')"
  >
    <CalendarSettings
      ref="refCalendarSettings"
      :is-display="modalSettings"
      :timezone-autofocus="timezoneAutofocus"
      @change-source="onChangeSource"
      @create-calendar="onCreate('calendar')"
      @removeCalendar="onRemoveCalendar"
    />
  </AModal>
</template>

<script lang="ts">
import { computed, defineComponent, nextTick, reactive, ref, toRefs, watch } from 'vue';
import {
  RightOutlined,
  LeftOutlined,
  SettingOutlined,
  PlusOutlined,
  CalendarOutlined,
  DownOutlined,
  AimOutlined,
  FormOutlined,
  GlobalOutlined,
  SearchOutlined,
  ArrowLeftOutlined,
  CloseOutlined,
} from '@ant-design/icons-vue';
import moment from 'moment';
import CalendarSettings from '@/components/CalendarSettings.vue';
import { useCalendarSources, useModeFilter, useTime, useUsers } from '@/composables';
import {
  CalendarBasics,
  ChangeSource,
  ToggleCalendarVisibilityParams,
} from '@/types/calendar.type';
import useGlobal from '@/composables/use-global';
import { createDebounce } from '@/utils/functions';
import { CalendarContext } from '@/utils/enums/global.enum';

interface OnCalendarCheck {
  checked: boolean;
  calendar: {
    id: string;
    name: string;
    color: string;
    show: boolean;
    isDefault: boolean;
  };
}

export default defineComponent({
  components: {
    CalendarSettings,
    RightOutlined,
    LeftOutlined,
    SettingOutlined,
    PlusOutlined,
    CalendarOutlined,
    DownOutlined,
    AimOutlined,
    FormOutlined,
    GlobalOutlined,
    SearchOutlined,
    ArrowLeftOutlined,
    CloseOutlined,
  },
  props: {
    viewTitle: {
      type: String,
      required: true,
    },
    viewName: {
      type: String,
      required: true,
    },
  },
  emits: [
    'changeView',
    'setToday',
    'create',
    'changeViewPage',
    'gotoDate',
    'changeCalendarVisibility',
    'changeSource',
    'fetchEvents',
    'removeCalendar',
  ],
  setup(_, { emit }) {
    const { isLoading } = useGlobal();
    const { whoami } = useUsers();
    const { sourcesToRender, sources } = useCalendarSources();
    const { timezoneLabel } = useTime();
    const {
      isModeFilter,
      isDisplayInputSearch,
      isFilterAppliedWithKeyword,
      paginationStatus,
      keyword,
      setKeyword,
      setVisibilityInputSearch,
      setCalendarsToFilter,
      setPaginationSkip,
      resetPagination,
      setFilterViewAsActive,
    } = useModeFilter();

    const timezoneAutofocus = ref(false);

    const state = reactive({
      showCalendarsDropdown: false,
      viewSelectedLabel: isModeFilter.value ? 'list' : 'month',
      calendars: [] as OnCalendarCheck['calendar'][],
      modalSettings: false,
      popoverCustomDate: moment(),
      createType: 'event' as 'event' | 'calendar',
      sourceToFilter: {} as { [key: string]: boolean },
    });

    const popover = reactive({
      click: false,
      hover: false,
    });

    const debounce = createDebounce();

    const isHideTitle = computed(() => {
      return isModeFilter.value && state.viewSelectedLabel === 'list';
    });

    const isNextDisabled = computed(() => {
      return isHideTitle.value && !paginationStatus.value.next;
    });

    const isPrevDisabled = computed(() => {
      return isHideTitle.value && !paginationStatus.value.prev;
    });

    const paginationLabel = computed(() => {
      if (isHideTitle.value) {
        return { prev: 'Previous page', next: 'Next page' };
      }

      const viewLabel = state.viewSelectedLabel;

      return { prev: `Previous ${viewLabel}`, next: `Next ${viewLabel}` };
    });

    const popoverDateData = computed(() => {
      switch (state.viewSelectedLabel) {
        case 'day':
        case 'week':
        case 'list':
          return { component: 'ADatePicker', format: 'LL' };
        default:
          return { component: 'AMonthPicker', format: 'MMMM YYYY' };
      }
    });

    const sourcesToFilter = computed(() => {
      if (!isDisplayInputSearch) {
        return [];
      }

      const ids: string[] = [];

      for (const source of sources.value) {
        if (!state.sourceToFilter[source.id] && source.context !== CalendarContext.Google) {
          ids.push(source.id);
        }
      }

      return ids;
    });

    watch(
      sourcesToFilter,
      val => {
        setCalendarsToFilter(val);
      },
      { immediate: true },
    );

    const refCalendarSettings = ref({});
    const refInputKeyword = ref();

    const handlePopoverCustomDateChange = (visible: boolean, trigger: 'hover' | 'click') => {
      if (trigger === 'hover' && popover.click) {
        return;
      }

      popover[trigger] = visible;
      popover[trigger === 'hover' ? 'click' : 'hover'] = false;
    };

    /** Current date */
    const today = moment().format('dddd, MMMM D');

    const views = [
      { name: 'timeGridDay', label: 'Day' },
      { name: 'timeGridWeek', label: 'Week' },
      { name: 'dayGridMonth', label: 'Month' },
      { name: isModeFilter.value ? 'filter' : 'listWeek', label: 'List' },
    ];

    const setViewTitle = (viewName: string) => {
      if (viewName === 'listWeek' || viewName === 'filter') {
        state.viewSelectedLabel = 'list';
        return;
      }

      const view = views.find(view => view.name === viewName)!;

      if (!view) {
        console.error(new Error(`View "${viewName}" not found`));
      }

      state.viewSelectedLabel = view.label.toLowerCase();
    };

    const onClickShowSearchBar = () => {
      setVisibilityInputSearch(true);

      nextTick(() => {
        refInputKeyword.value.focus();
      });
    };

    const onClickHideSearchBar = () => {
      const hasFilter = !!keyword.value;
      setVisibilityInputSearch(false);

      hasFilter && emit('fetchEvents');
    };

    const onClickClearKeyword = () => {
      setKeyword('');
      refInputKeyword.value.focus();

      resetPagination();
      emit('fetchEvents');
    };

    const onCheckedCalendarSearch = (checked: boolean, calendar: CalendarBasics) => {
      state.sourceToFilter[calendar.id] = !checked;

      nextTick(() => emit('fetchEvents'));
    };

    const onChangeView = (view: string) => {
      setViewTitle(view);
      emit('changeView', view);

      setFilterViewAsActive(view === 'filter');
    };

    const onSetToday = () => {
      emit('setToday');
    };

    const onGotoCustomDate = () => {
      emit('gotoDate', state.popoverCustomDate.toISOString());

      // Reset date and close popover
      state.popoverCustomDate = moment();
      popover.click = false;
    };

    const onCreate = (type: 'event' | 'calendar') => {
      emit('create', type);
    };

    const onChangeViewPage = (type: 'next' | 'prev') => {
      if (!isHideTitle.value) {
        emit('changeViewPage', type);

        return;
      }

      setPaginationSkip(type);
      emit('fetchEvents');
    };

    const onRemoveCalendar = (id: string) => {
      emit('removeCalendar', id);
    };

    const onClickChangeTimezone = () => {
      state.modalSettings = true;
      timezoneAutofocus.value = true;
    };

    const onClickSettings = () => {
      state.modalSettings = true;
      timezoneAutofocus.value = false;
    };

    const handleMenuClick = ({ item }: any) => {
      state.createType = item.value;
    };

    const onInputSearch = (value: string) => {
      value = value.trimStart();

      if (!value && !keyword.value) {
        return;
      }

      setKeyword(value);

      if (value.length > 0 && value.length < 2) {
        return;
      }

      debounce(() => {
        if (state.viewSelectedLabel !== 'list') {
          onChangeView('filter');
        }

        emit('fetchEvents');
      });
    };

    /** Show/hide calendars */
    const onCalendarChecked = async (calendarId: string, checked: boolean) => {
      emit('changeCalendarVisibility', { calendarId, checked } as ToggleCalendarVisibilityParams);
    };

    const onChangeSource = (data: ChangeSource) => {
      emit('changeSource', data);
    };

    return {
      today,
      views,
      ...toRefs(state),
      isLoading,
      refCalendarSettings,
      refInputKeyword,
      popover,
      whoami,
      keyword,
      setKeyword,
      isModeFilter,
      isHideTitle,
      isDisplayInputSearch,
      isFilterAppliedWithKeyword,
      isNextDisabled,
      isPrevDisabled,
      paginationLabel,
      sourcesToRender,
      sourcesToFilter,
      popoverDateData,
      setVisibilityInputSearch,
      onInputSearch,
      onClickClearKeyword,
      onClickShowSearchBar,
      onClickHideSearchBar,
      onCheckedCalendarSearch,
      onChangeView,
      onSetToday,
      onCreate,
      onChangeViewPage,
      onCalendarChecked,
      onChangeSource,
      handleMenuClick,
      handlePopoverCustomDateChange,
      onGotoCustomDate,
      timezoneLabel,
      timezoneAutofocus,
      onClickChangeTimezone,
      onClickSettings,
      onRemoveCalendar,
    };
  },
});
</script>

<style lang="scss" scoped>
