.CalendarToolbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  height: $calendar-toolbar-height;

  &__view-info {
    display: flex;
  }

  &__title {
    line-height: $calendar-toolbar-height;
    font-weight: 600;
    cursor: pointer;
    margin-right: 0.5rem;
  }

  &__search-back {
    display: flex;
    height: 100%;
    align-items: center;

    h3 {
      margin-bottom: 0;
      margin: 0 1.5rem 0 1rem;
      line-height: $calendar-toolbar-height;
    }
  }
}

.show-color {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
