/* eslint-disable max-len */
import { Debounce, StringNumber } from '@/types/types';
import moment from 'moment';
import { Moment } from 'moment';

/** Clone object */
export function cloneObject<T>(obj: T): T {
  return JSON.parse(JSON.stringify(obj));
}

/** Verify if the value is empty (null, undefined empty string) */
export function isEmpty(value: string | undefined | null) {
  return value === '' || value === undefined || value === null;
}

/** Create custom debounce function */
export function createDebounce(): Debounce {
  let timeout: any = null;

  return function (callback: () => void, delay = 500) {
    clearTimeout(timeout);

    timeout = setTimeout(() => callback(), delay);
  };
}

/** Generate array of numbers */
export function genArrayNumbers({ from = 0, to = 10, step = 1 }) {
  const values = [];
  let inc = from;

  while (inc <= to) {
    values.push(inc);

    inc += step;
  }

  return values;
}

/** capitalize text */
export function capitalize(text: string) {
  return `${text.charAt(0).toUpperCase()}${text.slice(1)}`;
}

/** Generate random token string */
export function randomTokenString(length = 4): string {
  let randomString = '';

  const charts = 'aAbB0cCdDeE1fFgGh2HiIjJ3kKl4LmM5nNoOp6PqQrR7sStT8uUv_VwW9xXyYzZ';

  while (randomString.length < length) {
    randomString += charts.charAt(Math.round(Math.random() * charts.length));
  }

  return randomString;
}

/** Format date to format YYYY-MM-DD */
export function isoDateWithoutTime(date: string | Moment) {
  const instance = typeof date === 'string' ? moment(date) : date;

  return instance.utc().format('YYYY-MM-DD');
}

export function removeObjectProps<T>(obj: any, ...propsToRemove: string[]): T {
  if (!propsToRemove.length) return obj;

  return Object.keys(obj).reduce((newObj, key) => {
    if (propsToRemove.includes(key)) {
      return newObj;
    }

    return { ...newObj, [key]: obj[key] };
  }, {} as any);
}

export function isObject(obj: any) {
  return Object.prototype.toString.call(obj) === '[object Object]';
}

export function arraysAreEquals(arr1: any[], arr2: any[]): boolean {
  if (arr1.length !== arr2.length) {
    return false;
  }

  return arr1.every((item, index) => {
    if (isObject(item) && isObject(arr2[index])) {
      return objectsAreEquals(item, arr2[index]);
    }

    if (Array.isArray(item) && Array.isArray(arr2[index])) {
      return arraysAreEquals(item, arr2[index]);
    }

    return item === arr2[index];
  });
}

export function objectsAreEquals(obj1: any, obj2: any): boolean {
  const obj1Keys = Object.keys(obj1);
  const obj2Keys = Object.keys(obj2);

  if (obj1Keys.length !== obj2Keys.length) {
    return false;
  }

  return obj1Keys.every(key => {
    if (isObject(obj1[key]) && isObject(obj2[key])) {
      return objectsAreEquals(obj1[key], obj2[key]);
    }

    if (Array.isArray(obj1[key]) && Array.isArray(obj2[key])) {
      return arraysAreEquals(obj1[key], obj2[key]);
    }

    return obj1[key] === obj2[key];
  });
}

export function getObjChangedProps<T>(oldObj: any, newObj: any): Partial<T> {
  const oldKeys = Object.keys(oldObj);
  const newKeys = Object.keys(newObj);

  const evaluateKeys = oldKeys.length > newKeys.length ? oldKeys : newKeys;

  return evaluateKeys.reduce((changes, key) => {
    // Properties are objects, use recursivity
    if (isObject(oldObj[key]) && isObject(newObj[key])) {
      if (!objectsAreEquals(oldObj[key], newObj[key])) {
        return {
          ...changes,
          [key]: newObj[key],
        };
      }

      return changes;
    }

    if (Array.isArray(oldObj[key]) && Array.isArray(newObj[key])) {
      if (!arraysAreEquals(oldObj[key], newObj[key])) {
        return { ...changes, [key]: newObj[key] };
      }

      return changes;
    }

    if (oldObj[key] !== newObj[key]) {
      return { ...changes, [key]: newObj[key] };
    }

    return changes;
  }, {} as any);
}

/** Remove nullable properties and return sanitized object */
export function removeNullableProps(obj: any, ...exclude: string[]) {
  if (!isObject(obj)) {
    throw new Error(`The param "obj" must be an object`);
  }

  const propsToRemove = Object.keys(obj).filter(prop => {
    if (exclude && exclude.includes(prop)) {
      return false;
    }

    return obj[prop] === null;
  });

  if (!propsToRemove.length) {
    return obj;
  }

  return removeObjectProps(obj, ...propsToRemove);
}

export function isArrayEmpty(array: any[]): boolean {
  return !array.length;
}

export function isArrayAndHasItems(array: any): boolean {
  return Array.isArray(array) && !isArrayEmpty(array);
}

export function isNull(value: any): boolean {
  return value === null;
}

export const isUndefined = (value: any): boolean => {
  return typeof value === 'undefined';
};

export function isObjectEmpty(obj: any) {
  return isArrayEmpty(Object.keys(obj));
}

export function valueIs(value: StringNumber, ...options: StringNumber[]) {
  return options.includes(value);
}

// validate email
export function isValidEmail(email: string): boolean {
  // eslint-disable-next-line prettier/prettier
  const re =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  return re.test(email);
}
