import {
  CalendarEvent,
  CalendarEventPosition,
  CalendarOriginalSource,
  CalendarPermissionType,
  CalendarSource,
  RegeranteRecurrence,
  WeekDayMinType,
} from '@/types/calendar.type';
import { Calendar, EventApi } from '@fullcalendar/core';
import { computed, reactive, readonly, Ref, ref } from 'vue';
import { calendarHelper } from '@/helpers';
import { cloneObject, isoDateWithoutTime } from '@/utils/functions';
import useCalendarSources from './use-calendar-sources';
import { byweekdayFromUIToBackend, formatEventDates } from '@/helpers/calendar.helper';
import { time } from './use-time';

/** Current calendar instance */
const calendar = ref({}) as Ref<Calendar>;

export interface CalendarCurrentData {
  // event: EventApi | null;
  event: CalendarEvent;
  calendarId: string | null;
  /** Previous dates of the recurring event moved */
  oldRecurringPosition: {
    start: string;
    end: string;
  } | null;
  activeDateRange: CalendarEventPosition;
}

const currentData = reactive<CalendarCurrentData>({
  // event: null,
  event: {} as CalendarEvent,
  calendarId: null,
  oldRecurringPosition: null,
  activeDateRange: {} as CalendarEventPosition,
});

const currentEventApi = ref({}) as Ref<EventApi>;

export default function () {
  const {
    sources,
    getSourcePermission,
    setSources,
    addNewSource,
    displayedSources,
    sourcesToRender,
    defaultSource,
    getSourceById,
    transformSource,
  } = useCalendarSources();

  const isSourcesLoaded = ref(false);

  /** Know if the current selected event is a recurring */
  const isRecurringEvent = computed(() => {
    if (!currentData.event) {
      return false;
    }

    return !!currentData.event.rrule;
  });

  /** Know if the current selected event is a custom child */
  const isChildEvent = computed(() => {
    if (!currentData.event) {
      return false;
    }

    return !!currentData.event.parentId;
  });

  /** Calendar data of event selected */
  const selectedSource = computed(() => {
    if (!currentData.calendarId) {
      return defaultSource.value;
    }

    const all = [
      ...sourcesToRender.value.own,
      ...sourcesToRender.value.shared,
      ...sourcesToRender.value.google,
    ];

    return all.find(source => source.id === currentData.calendarId)!;
  });

  const permission = computed<CalendarPermissionType>(() => {
    return getSourcePermission(selectedSource.value);
  });

  const getCurrentSource = () => {
    return sources.value.find(source => source.id === currentData.calendarId)!;
  };

  const getCurrentEventDataFromSources = (source?: CalendarSource) => {
    const id = currentData.event?.id;
    source = source || getCurrentSource();

    const index = source.events.findIndex(event => event.id === id);
    const event = source.events[index];

    return { event, index };
  };

  const addRecurrenceExclusion = (eventId: string, startDate: string) => {
    const source = getCurrentSource();
    const parent = source.events.find(item => item.id === eventId);

    if (!parent) {
      throw new Error(`Event with the ID "${eventId}" was not found.`);
    }

    if (!parent.rrule) {
      throw new Error(`Event with the ID "${eventId}" is not a recurring event`);
    }

    parent.exdate!.push(parent.allDay ? isoDateWithoutTime(startDate) : startDate);
    return [...parent.exdate!];
  };

  const getCurrentFullEvent = (): CalendarEvent => {
    return cloneObject(currentData.event!);
  };

  /** Get event by ID with the full data (like as comes from backend api) */
  const getFullEventById = (id: string, calendarId?: string): CalendarEvent => {
    const source = !calendarId ? getCurrentSource() : getSourceById(calendarId).source;

    const event = cloneObject(source.events.find(event => event.id === id));

    if (!event) {
      throw new Error(`Event with the ID: ${id} was not found`);
    }

    event.rrule && (event.rrule = byweekdayFromUIToBackend(event.rrule));
    return event;
  };

  /** Get dates (UTC) of current selected event */
  const getEventDate = (eventApi?: EventApi) => {
    const { startStr, endStr } = eventApi || currentEventApi.value!;

    return {
      start: time(startStr).toISOString(),
      end: time(endStr).toISOString(),
    };
  };

  const getActiveDatesRange = () => {
    const { activeRange } = calendar.value.currentData.dateProfile!;

    return {
      start: activeRange!.start.toISOString(),
      end: activeRange!.end.toISOString(),
    };
  };

  /** Upate recurrence duration via drag and drop or modal */
  const updateEventRuleAndDuration = ({ event, action, duration }: RegeranteRecurrence) => {
    if (action === 'resize') {
      event.duration = duration;
      event = formatEventDates(event);
      return event;
    }

    const currentEvent = getCurrentFullEvent();
    let byweekday = currentEvent.rrule!.byweekday || [];

    // Assign new star date
    event.rrule!.dtstart = calendarHelper.updateDateFromDuration({
      date: event.rrule!.dtstart,
      duration,
      allDay: event.allDay,
    });

    // Drag and drop, update byweekday property
    if (action === 'dragAndDrop' && byweekday.length) {
      byweekday = (byweekday as WeekDayMinType[]).map(weekday => {
        return calendarHelper.updateWeekday(weekday, duration.days);
      });

      event.rrule!.byweekday = byweekday;

      return event;
    }

    // action from modal
    // let isByWeekdayDifferent = false;
    const isByWeekdayDifferent = false;

    if (byweekday.length) {
      // changing only "byweekday" property
      // isByWeekdayDifferent = !byweekday.every(item => event.rrule?.byweekday?.includes(item));
    }

    // Update byweekday when recurrence has been moved
    if (duration.daysDiff !== 0 && byweekday && !isByWeekdayDifferent) {
      event.rrule!.byweekday = (byweekday as WeekDayMinType[]).map(weekday => {
        return calendarHelper.updateWeekday(weekday, duration.daysDiff);
      });
    }

    if (event.rrule?.until) {
      // Assing new until date
      event.rrule.until = calendarHelper.updateDateFromDuration({
        date: event.rrule.until,
        duration,
        allDay: event.allDay,
      });
    }

    return event;
  };

  const setCalendarInstance = (instance: Calendar) => {
    calendar.value = instance;
  };

  const setCurrentCalendarId = (calendarId: string) => {
    currentData.calendarId = calendarId;
  };

  const setActiveDateRange = (dates: CalendarEventPosition) => {
    currentData.activeDateRange = dates;
  };

  const setOldRecurringPosition = (value: CalendarCurrentData['oldRecurringPosition'] | null) => {
    currentData.oldRecurringPosition = value;
  };

  /** Save data of the event selected */
  const setDataOfCurrentEvent = (event: EventApi) => {
    setCurrentCalendarId(event.source!.id);

    currentEventApi.value = event;
    currentData.event = getFullEventById(event.id, event.source!.id);
  };

  const refetchSources = (ids?: string[]) => {
    const _sources = ids ? sources.value.filter(item => ids.includes(item.id))! : sources.value;

    _sources.forEach(source => {
      if (source.show) {
        const calendarSource = calendar.value.getEventSourceById(source.id);

        calendar.value.batchRendering(() => {
          if (!calendarSource) {
            calendar.value.addEventSource(source as any);
          } else {
            // Remove
            calendarSource.remove();
            // Add again
            calendar.value.addEventSource(source as any);
          }
        });
      }
    });
  };

  /** Add source to UI */
  const addCalendarSource = (source: CalendarSource) => {
    calendar.value.addEventSource(source as any);
  };

  /** Create new source: save locally and show in UI */
  const addNewCalendarSource = (source: CalendarOriginalSource) => {
    const newSource = transformSource(source);

    // Save locally
    addNewSource(newSource);
    // Add in UI
    addCalendarSource(newSource);
  };

  return {
    calendar,
    sources,
    currentData,
    currentEventApi,
    permission,
    getCurrentEventDataFromSources,
    getFullEventById,
    getCurrentFullEvent,
    getEventDate,
    getActiveDatesRange,
    isSourcesLoaded: readonly(isSourcesLoaded),
    isRecurringEvent,
    isChildEvent,
    displayedSources,
    sourcesToRender,
    defaultSource,
    selectedSource,
    addNewCalendarSource,
    refetchSources,
    addRecurrenceExclusion,
    updateEventRuleAndDuration,
    setCalendarInstance,
    setSources,
    setDataOfCurrentEvent,
    setActiveDateRange,
    setCurrentCalendarId,
    setOldRecurringPosition,
  };
}
