import { App } from 'vue';

// Ant design components
import {
  Button,
  Input,
  InputNumber,
  Menu,
  Switch,
  Dropdown,
  Checkbox,
  Modal,
  Radio,
  Select,
  Form,
  Col,
  Row,
  Tooltip,
  TimePicker,
  DatePicker,
  Popconfirm,
  Popover,
  Card,
  Tabs,
  Tag,
  Result,
  Spin,
  List,
  Avatar,
} from 'ant-design-vue';

const components = [
  Button,
  Input,
  InputNumber,
  Menu,
  Switch,
  Dropdown,
  Checkbox,
  Modal,
  Radio,
  Select,
  Form,
  Col,
  Row,
  Tooltip,
  TimePicker,
  DatePicker,
  Popconfirm,
  Popover,
  Card,
  Tabs,
  Tag,
  Result,
  Spin,
  List,
  Avatar,
];

export default (app: App) => {
  components.forEach(component => app.use(component));
};
