import { formatEventToFullCalendar } from '@/helpers/calendar.helper';
import { searchEvents } from '@/services/calendar.service';
import { CalendarEvent, CalendarSource } from '@/types/calendar.type';
import { Case } from '@/types/cases.type';
import { ContactPrima } from '@/types/contacts.type';
import { SearchEventsArgs } from '@/types/services.type';
import { cloneObject } from '@/utils/functions';
import { computed, reactive } from 'vue';
import useUsers from './use-users';

export type FilterField = 'contactId' | 'caseId';

export interface Filter {
  field: FilterField;
  value: number;
}

interface ContactData {
  id: number;
  caseId: number;
}

const state = reactive({
  enable: false,
  isActiveView: false,
  loaded: false,
  filter: null as Filter | null,
  displayInputSearch: false,
  keyword: '',
  calendars: [] as string[],
  filterAppliedWithKeyword: false,
  contact: null as ContactData | null,
  pagination: {
    size: 1000,
    skip: 0,
    total: 0,
  },
  cache: {
    contact: {} as ContactPrima,
    cases: [] as Case[],
  },
});

export default function () {
  const { getUserLocal } = useUsers();

  const activeModeFilter = (val: boolean) => {
    state.enable = val;

    val && setFilterViewAsActive(true);
  };

  const setFilterViewAsActive = (active: boolean) => {
    state.isActiveView = active;
  };

  const setCalendarsToFilter = (calendars: string[]) => {
    state.calendars = calendars;
  };

  const setFilterData = (field: FilterField, value: number) => {
    if (!state.enable) {
      throw new Error(`The mode filter is disabled, enable first and set the filter data`);
    }

    state.filter = { field, value };
  };

  const setCasesInCache = (cases: Case[]) => {
    state.cache.cases = cases;
  };

  const setContactInCache = (contact: ContactPrima) => {
    state.cache.contact = contact;
  };

  const setVisibilityInputSearch = (value: boolean) => {
    state.displayInputSearch = value;

    if (!value && state.keyword) {
      state.keyword = '';
    }
  };

  const setKeyword = (value: string) => {
    state.keyword = value;
  };

  const setContactData = (data: ContactData) => {
    state.contact = data;
  };

  const setPaginationSkip = (type: 'next' | 'prev') => {
    if (type === 'next') {
      state.pagination.skip += state.pagination.size;

      return;
    }

    state.pagination.skip -= state.pagination.size;
  };

  const resetPagination = () => {
    state.pagination.skip = 0;
  };

  const paginationStatus = computed(() => {
    const status = { prev: false, next: false };
    const pag = state.pagination;

    if (pag.size + pag.skip < pag.total) {
      status.next = true;
    }

    if (pag.skip >= pag.size) {
      status.prev = true;
    }

    return status;
  });

  const filterEvent = (event: CalendarEvent) => {
    if (!state.keyword) {
      return true;
    }

    const keyword = state.keyword.toLowerCase();

    // Search by title
    if (event.title.toLowerCase().includes(keyword)) {
      return true;
    }

    // Search by description
    if (event.description && event.description.toLowerCase().includes(keyword)) {
      return true;
    }

    // Search by user
    const user = getUserLocal(event.createdBy);

    if (user.fullName.toLowerCase().includes(keyword)) {
      return true;
    }

    // Search by cases
    if (state.filter?.field === 'contactId') {
      const casesMatch = state.cache.cases.some(primaCase => {
        return (
          primaCase.name.toLowerCase().includes(keyword!) ||
          primaCase.caseTypeName.toLowerCase().includes(keyword)
        );
      });

      if (casesMatch) {
        return true;
      }
    }

    return false;
  };

  const mapSources = (sources: CalendarSource[]) => {
    return cloneObject(sources).map(source => {
      let events = source.events;

      if (state.keyword) {
        events = source.events.filter(event => filterEvent(event));
      }

      const fullCalendarEvents = events.map(event => formatEventToFullCalendar({ ...event }));
      return { id: source.id, events, fullCalendarEvents };
    });
  };

  const getFilteredSources = (sources: CalendarSource[]) => {
    const filtered = sources.filter(source => state.calendars.includes(source.id));

    return mapSources(filtered).filter(source => source.events.length);
  };

  const getFilterArgs = () => {
    if (!state.filter) {
      throw new Error(`Nothing to filter`);
    }

    const args: SearchEventsArgs = {
      [state.filter.field]: state.filter.value,
      calendars: state.calendars,
    };

    if (state.keyword) {
      args.keyword = state.keyword;
    }

    return args;
  };

  const getFilteredEvents = async (sources?: CalendarSource[]) => {
    if (sources && state.loaded) {
      return { from: 'local', sourceEvents: getFilteredSources(sources) };
    }

    const args = getFilterArgs();
    const results = await searchEvents(args);

    state.filterAppliedWithKeyword = !!state.keyword;

    if (!state.loaded) {
      state.loaded = true;
    }

    return { from: 'backend', sourceEvents: results };
  };

  return {
    isModeFilter: computed(() => state.enable),
    globalFilter: computed(() => state.filter),
    isDisplayInputSearch: computed(() => state.displayInputSearch),
    keyword: computed(() => state.keyword),
    isFilterAppliedWithKeyword: computed(() => state.filterAppliedWithKeyword),
    contactData: computed(() => state.contact),
    isFilterViewActive: computed(() => state.isActiveView),
    activeModeFilter,
    setFilterData,
    setContactData,
    setCasesInCache,
    setContactInCache,
    setVisibilityInputSearch,
    setPaginationSkip,
    setKeyword,
    setCalendarsToFilter,
    getFilteredEvents,
    paginationStatus,
    resetPagination,
    setFilterViewAsActive,
    getFilterArgs,
  };
}
