import { reactive, readonly, toRefs } from 'vue';

const state = reactive({
  isLoading: false,
  awaitLoading: false,
});

const mutations = {
  setLoading: (val: boolean) => (state.isLoading = val),
  toggleLoading: () => (state.isLoading = !state.isLoading),
  setAwaitLoading: (val: boolean) => {
    state.awaitLoading = val;

    if (!val && state.isLoading) {
      state.isLoading = false;
    }
  },
};

/** Export such as Vuex store for usage in external files to the Vue components */
export const globalStore = {
  ...toRefs(readonly(state)),
  ...mutations,
};

/** Vue composable */
export default function () {
  return globalStore;
}
