import { ref, readonly, computed } from 'vue';
import moment, { Moment } from 'moment-timezone';
import timezones from '@/utils/timezones.json';

const _timezone = ref<string>('');
export const timezone = readonly(_timezone);

export const time = (date?: string | Date | Moment, timezone?: string) => {
  return moment.tz(date || moment(), timezone || _timezone.value);
};

export default function (timezone?: string) {
  const timezoneLabel = computed(() => getTimeZoneLabel(_timezone.value));

  const setTimezone = (zone: string) => (_timezone.value = zone);

  const getTimeZoneLabel = (zone: string) => {
    if (zone === 'UTC') {
      zone = 'Etc/GMT';
    }

    return (timezones as any)[zone] as string;
  };

  const onCreated = () => {
    if (!_timezone.value) {
      _timezone.value = timezone || moment.tz.guess();
    }
  };

  onCreated();

  return {
    timezone: readonly(_timezone),
    time,
    timezoneLabel,
    setTimezone,
    getTimeZoneLabel,
  };
}
