<template>
  <!-- Share with specific people -->
  <AModal
    v-model:visible="modalAddPeople"
    title="Share with specific people"
    :ok-button-props="{ disabled: !addPermission.users.length }"
    :confirm-loading="isLoading"
    @ok="addUserPermission"
    @cancel="onCancelModalAddPeople"
  >
    <AForm layout="vertical">
      <AFormItem label="Add email or name">
        <ASelect
          ref="refSelectUser"
          v-model:value="addPermission.users"
          show-search
          mode="multiple"
          :filter-option="userFilterOption"
          option-label-prop="label"
          placeholder="Add email or name"
          style="width: 100%"
        >
          <ASelectOption v-for="user of availableUsers" :key="user.id" :label="user.fullName">
            {{ user.fullName }} ({{ user.email }})
          </ASelectOption>
        </ASelect>
      </AFormItem>

      <AFormItem label="Permission">
        <div class="Permissions__list-permission">
          <ASelect v-model:value="addPermission.permission" style="width: 100%">
            <ASelectOption value="read">See all event details</ASelectOption>
            <ASelectOption value="all">Make changes to events</ASelectOption>
          </ASelect>
        </div>
      </AFormItem>
    </AForm>
  </AModal>

  <section class="Permissions">
    <h2 class="Permissions__label">Special permissions</h2>

    <div
      v-for="(permission, permissionType) of specialPermissions"
      :key="permissionType"
      class="Permissions__list"
    >
      <div class="Permissions__list-item">
        <div class="Permissions__list-user">
          <div class="Permissions__list-avatar">
            <component :is="permission.icon" style="font-size: 16px" />
          </div>
          <div class="Permissions__list-title">{{ permission.label }}</div>
        </div>

        <div v-if="permission.permission" class="Permissions__list-permission">
          <ASelect
            v-model:value="permission.permission"
            size="small"
            style="width: 188.77px"
            :disabled="isLoading"
            @change="setPermission({ type: permissionType, permission: permission.permission })"
          >
            <ASelectOption value="read">See all event details</ASelectOption>
            <ASelectOption value="all">Make changes to events</ASelectOption>
          </ASelect>

          <ATooltip v-if="permissionType === 'global'" title="Revoke permission">
            <AButton
              :disabled="isLoading"
              type="link"
              size="small"
              @click="revokePermission(permissionType)"
            >
              <CloseOutlined />
            </AButton>
          </ATooltip>

          <LockOutlined v-if="permissionType === 'admin'" style="padding: 0 8px" />
        </div>

        <div v-else class="Permissions__list-permission">
          <APopconfirm
            title="Are you sure you want to set the calendar as global?"
            ok-text="Yes"
            cancel-text="No"
            @confirm="setCalendarAsGlobal"
            @visible-change="onChangePopConfirm"
          >
            <ATooltip
              v-if="permissionType === 'global'"
              :visible="isVisibleTooltipRevokeGlobal && !isVisiblePopConfirm"
              title="Set the calendar as global"
            >
              <AButton
                size="small"
                type="link"
                :disabled="isLoading"
                @mouseenter="isVisibleTooltipRevokeGlobal = true"
                @mouseleave="isVisibleTooltipRevokeGlobal = false"
              >
                <PlusOutlined />
              </AButton>
            </ATooltip>
          </APopconfirm>
        </div>
      </div>
    </div>

    <h2 class="Permissions__label" style="margin-top: 1.5rem">Share with specific people</h2>

    <div class="Permissions__list">
      <div class="Permissions__list-item">
        <div class="Permissions__list-user">
          <div class="Permissions__list-avatar">
            <UserOutlined style="font-size: 16px" />
          </div>

          <ATooltip
            :title="`${whoami.fullName} (${whoami.email})`"
            :overlay-style="{ maxWidth: '420px' }"
          >
            <div class="Permissions__list-title">
              {{ whoami.fullName }} {{ `(Owner${whoami.admin && ', Admin'})` }}
            </div>
          </ATooltip>
        </div>
      </div>
      <div v-for="user of sharedWith" :key="user.id" class="Permissions__list-item">
        <div class="Permissions__list-user">
          <div class="Permissions__list-avatar">
            <UserOutlined style="font-size: 16px" />
          </div>

          <ATooltip :title="`${user.name} (${user.email})`" :overlay-style="{ maxWidth: '420px' }">
            <div class="Permissions__list-title">
              {{ user.name }} {{ user.admin ? '(Admin)' : '' }}
            </div>
          </ATooltip>
        </div>

        <div v-if="!user.isOwner" class="Permissions__list-permission">
          <ASelect
            v-model:value="user.permission"
            :disabled="isLoading"
            size="small"
            style="width: 188.77px"
            @change="
              setPermission({
                type: 'individual',
                permission: user.permission,
                userIds: [user.id],
              })
            "
          >
            <ASelectOption value="read">See all event details</ASelectOption>
            <ASelectOption value="all">Make changes to events</ASelectOption>
          </ASelect>

          <ATooltip
            title="Revoke permission"
            @click="revokePermission(PermissionsType.Individual, user.id)"
          >
            <AButton :disabled="isLoading" type="link" size="small">
              <CloseOutlined />
            </AButton>
          </ATooltip>
        </div>
      </div>
    </div>

    <div class="Permissions__plus-btn">
      <AButton :disabled="isLoading" @click="modalAddPeople = true">
        <PlusOutlined />
        Add people
      </AButton>
    </div>
  </section>
</template>

<script lang="ts">
import { useCalendarSources, useGlobalProps, useUsers } from '@/composables';
import { computed, defineComponent, nextTick, PropType, reactive, ref, watch } from 'vue';
import {
  UserOutlined,
  PlusOutlined,
  KeyOutlined,
  TeamOutlined,
  CloseOutlined,
  LockOutlined,
} from '@ant-design/icons-vue';
import { CalendarBasics, CalendarPermissionType } from '@/types/calendar.type';
import { Permission, PermissionsType } from '@/utils/enums/global.enum';
import { calendarService } from '@/services';
import { OnChangePermissionArgs, RevokePermissionArgs } from '@/types/services.type';
import { isArrayAndHasItems } from '@/utils/functions';

interface DataItem {
  id: number;
  name: string;
  email: string;
  isOwner: boolean;
  admin: boolean;
  permission: CalendarPermissionType;
}

export default defineComponent({
  components: {
    UserOutlined,
    PlusOutlined,
    KeyOutlined,
    TeamOutlined,
    CloseOutlined,
    LockOutlined,
  },

  props: {
    calendar: {
      type: Object as PropType<CalendarBasics>,
      required: true,
    },
  },

  setup(props) {
    const { isLoading } = useGlobalProps();
    const { whoami, users } = useUsers();
    const { transformPermission, updateSource } = useCalendarSources();

    const modalAddPeople = ref(false);
    const refSelectUser = ref<any>();

    const addPermission = reactive({
      users: [] as number[],
      permission: 'read' as CalendarPermissionType,
    });

    const isOpenSelectUser = ref(false);
    const isVisiblePopConfirm = ref(false);
    const isVisibleTooltipRevokeGlobal = ref(false);

    const sharedWith = ref<DataItem[]>([]);

    const specialPermissions = reactive({
      admin: {
        permission: Permission.Read as CalendarPermissionType,
        label: 'Administrators',
        icon: 'KeyOutlined',
      },
      global: {
        permission: null as CalendarPermissionType | null,
        label: 'All users',
        icon: 'TeamOutlined',
      },
    });

    watch(
      () => props.calendar,
      ({ permissions }) => {
        if (!permissions) {
          return;
        }

        specialPermissions.admin.permission = permissions.admin;

        if (permissions.global) {
          specialPermissions.global.permission = permissions.global;
        } else {
          specialPermissions.global.permission = null;
        }

        if (!permissions.individual) {
          sharedWith.value = [];

          return;
        }

        sharedWith.value = [];

        permissions.individual!.forEach(individual => {
          sharedWith.value.push({ ...individual, isOwner: false });
        });
      },
      { immediate: true },
    );

    watch(modalAddPeople, isOpen => {
      if (!isOpen) return;

      nextTick(() => {
        refSelectUser.value.focus();
      });
    });

    const availableUsers = computed(() => {
      if (
        !props.calendar.permissions ||
        !isArrayAndHasItems(props.calendar.permissions.individual)
      ) {
        return users.value;
      }

      return users.value.filter(user => {
        const ignoreUser = props.calendar.permissions!.individual!.some(
          individual => individual.id === user.id,
        );

        return !ignoreUser;
      });
    });

    const userFilterOption = (input: string, option: any) => {
      input = input.trim();
      const currentUser = users.value.find(item => item.id === option.value);

      if (!currentUser) {
        return false;
      }

      if (currentUser.fullName.toLowerCase().includes(input.toLowerCase())) {
        return true;
      }

      return currentUser.email.toLowerCase().includes(input.toLowerCase());
    };

    const revokePermission = async (type: 'global' | 'individual', userId?: number) => {
      if (type === PermissionsType.Individual && !userId) {
        throw new Error(`Field "userId" is required for revoke permission`);
      }

      const args: RevokePermissionArgs = {
        calendarId: props.calendar.id,
        type,
      };

      if (type === PermissionsType.Individual && userId) {
        args.userId = userId;
      }

      const result = await calendarService.revokePermission(args);

      const newPermissions = transformPermission(result.permissions);
      updateSource(props.calendar.id, { permissions: newPermissions });
    };

    const addUserPermission = async () => {
      const usersToAdd = users.value.filter(user => addPermission.users.includes(user.id));

      await setPermission({
        type: PermissionsType.Individual,
        permission: addPermission.permission,
        userIds: usersToAdd.map(user => user.id),
      });

      modalAddPeople.value = false;

      addPermission.users = [];
      addPermission.permission = 'read';
    };

    const onChangePopConfirm = (visible: boolean) => {
      isVisiblePopConfirm.value = visible;
    };

    const onCancelModalAddPeople = () => {
      addPermission.users = [];
      addPermission.permission = 'read';
    };

    const setPermission = async ({ type, permission, userIds = [] }: OnChangePermissionArgs) => {
      try {
        const result = await calendarService.setPermission({
          calendarId: props.calendar.id,
          type,
          permission,
          userIds,
        });

        const newPermissions = transformPermission(result.permissions);
        updateSource(props.calendar.id, { permissions: newPermissions });
      } catch (error: any) {
        console.error(error);
      }
    };

    const setCalendarAsGlobal = async () => {
      specialPermissions.global.permission = 'read';

      await setPermission({
        type: PermissionsType.Global,
        permission: Permission.Read,
      });
    };

    return {
      whoami,
      sharedWith,
      addPermission,
      refSelectUser,
      isLoading,
      modalAddPeople,
      users,
      availableUsers,
      PermissionsType,
      userFilterOption,
      isOpenSelectUser,
      specialPermissions,
      revokePermission,
      onChangePopConfirm,
      isVisiblePopConfirm,
      isVisibleTooltipRevokeGlobal,
      addUserPermission,
      onCancelModalAddPeople,
      setPermission,
      setCalendarAsGlobal,
    };
  },
});
</script>

<style lang="scss" scoped>
.Permissions {
  &__label {
    font-size: 16px;
    color: $color-grey;
  }

  &__list {
    &-user {
      display: flex;
    }

    &-item {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid rgba($color: #000000, $alpha: 0.1);
      padding: 0.5rem 0;
    }

    &-avatar {
      margin-right: 5px;
    }

    &-permission {
      display: flex;
      align-items: center;
    }
  }

  &__plus-btn {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
  }
}
</style>
