<template>
  <div class="VSwitch">
    <ASwitch
      :checked="$props.modelValue"
      @change="updateValue"
      v-bind="$attrs"
      :disabled="$props.disabled"
    />
    <span
      @click="updateValue"
      class="VSwitch__label"
      :class="{ 'VSwitch--disabled': $props.disabled }"
    >
      {{ $props.label }}
    </span>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
  inheritAttrs: false,
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    label: String,
    disabled: Boolean,
  },
  emits: ['update:modelValue', 'change'],
  setup(props, { emit }) {
    const updateValue = () => {
      if (props.disabled) {
        return;
      }

      emit('update:modelValue', !props.modelValue);
      emit('change', !props.modelValue);
    };

    return { updateValue };
  },
});
</script>

<style lang="scss" scoped>
.VSwitch {
  display: flex;
  align-items: center;
  line-height: 30px;

  &__label {
    cursor: pointer;
    margin-left: 5px;
  }

  &--disabled {
    cursor: not-allowed;
  }
}
</style>
