<template>
  <ADropdown :trigger="['click']">
    <ATooltip title="Choose color" :color="selectedColor">
      <a class="ant-dropdown-link" @click.prevent>
        <ColorCircle :color="selectedColor" />
      </a>
    </ATooltip>
    <template #overlay>
      <div class="Color">
        <div v-if="fixedColor || outColor" class="Color__default">
          <div v-if="fixedColor" style="display: flex">
            <a
              class="ant-dropdown-link Color__link"
              @click.prevent="onClickFixedColor($event, fixedColor)"
            >
              <ColorCircle v-bind="fixedColor" />
            </a>
            <span class="Color__default-label" @click.stop>Calendar</span>
          </div>

          <div v-if="outColor" style="display: flex">
            <a
              class="ant-dropdown-link Color__link"
              style="margin-left: 5px"
              @click.prevent="onClickFixedColor($event, outColor)"
            >
              <ColorCircle v-bind="outColor" />
            </a>
            <span class="Color__default-label" @click.stop>Out list</span>
          </div>
        </div>
        <a
          v-for="(color, index) of colors"
          :key="color.color"
          class="ant-dropdown-link Color__link"
          @click.prevent="onClickColor($event, index, color.color)"
        >
          <ColorCircle
            v-if="isColorAllowed(color)"
            v-bind="{ ...color, disabled: false, animate: true }"
          />
          <ATooltip v-else title="Already used!" :color="color.color">
            <ColorCircle v-bind="color" />
          </ATooltip>
        </a>
      </div>
    </template>
  </ADropdown>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref, watch } from 'vue';
import ColorCircle from '@/components/ColorCircle.vue';
import { Color } from '@/types/color.type';

export default defineComponent({
  components: { ColorCircle },
  props: {
    modelValue: String,
    default: String,
    fixed: {
      type: Boolean,
      default: false,
    },
    colorsData: {
      type: Array as PropType<Color[]>,
      default: () => {
        return ['orange', 'blue', 'red'].map(color => ({
          color,
          disabled: false,
          animate: true,
          actve: false,
        }));
      },
    },
  },
  emits: ['update:modelValue', 'change'],
  setup(props, { emit }) {
    const colors = ref<Color[]>([]);
    const fixedColor = ref<Color>();
    /** Out list color */
    const outColor = ref<Color>();

    const updateModelValue = (color: string) => {
      emit('update:modelValue', color);
      emit('change', color);
    };

    const setActiveColor = (color: string) => {
      colors.value = colors.value.map(item => {
        if (item.color === color) {
          return { ...item, active: true };
        }

        return { ...item, active: false };
      });
    };

    // Update colors from outside
    watch(
      () => props.colorsData,
      newColors => {
        outColor.value = undefined;
        fixedColor.value = undefined;

        const listColors: Color[] = [];

        for (let i = 0; i < newColors.length; i++) {
          const _color = newColors[i];

          if (_color.fixed) {
            fixedColor.value = _color;
            continue;
          }

          if (_color.isOut) {
            outColor.value = _color;
            continue;
          }

          listColors.push(_color);
        }

        colors.value = listColors;
      },
      { deep: true, immediate: true },
    );

    const selectedColor = computed(() => props.modelValue || props.colorsData[0].color);

    watch(
      () => selectedColor.value,
      val => {
        setActiveColor(val);
      },
      { immediate: true },
    );

    const onClickFixedColor = (e: any, color: Color) => {
      const isDefaultColor = color.color !== props.default;
      const isCurrentColorSelected = color.color === props.modelValue;
      const isColorDisabled = color.disabled;

      if (isCurrentColorSelected || (isDefaultColor && isColorDisabled)) {
        return e.stopPropagation();
      }

      updateModelValue(color.color);
    };

    const onClickColor = (e: any, index: number, color: string) => {
      const isDefaultColor = colors.value[index].color !== props.default;
      const isCurrentColorSelected = color === props.modelValue;
      const isColorDisabled = colors.value[index].disabled;

      if (isCurrentColorSelected || (isDefaultColor && isColorDisabled)) {
        return e.stopPropagation();
      }

      updateModelValue(color);
    };

    const getFirstAllowedColor = () => {
      let _color = '';

      for (const color of colors.value) {
        if (!color.disabled) {
          _color = color.color;
          break;
        }
      }

      if (!_color) {
        throw new Error('All colors are disabled!');
      }

      return _color;
    };

    const isColorAllowed = (color: Color) => {
      if (!props.default) {
        return !color.disabled;
      }

      return !color.disabled || (props.default && props.default === color.color);
    };

    const onCreated = () => {
      if (!props.modelValue) {
        updateModelValue(getFirstAllowedColor());
        return;
      }

      setActiveColor(props.modelValue);
    };

    onCreated();

    return {
      selectedColor,
      outColor,
      colors,
      fixedColor,
      isColorAllowed,
      setActiveColor,
      onClickColor,
      onClickFixedColor,
    };
  },
});
</script>

