  &__default {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    width: 100%;
    display: flex;
    padding-bottom: 5px;

    &-label {
      // margin-left: 2px;
      display: flex;
      align-items: center;
      margin-bottom: -5px;
      color: grey;
    }
  }
}
</style>
