
import { computed, defineComponent } from 'vue';
import CKEditor from '@ckeditor/ckeditor5-vue';
import InlineEditor from '@ckeditor/ckeditor5-build-inline';

export default defineComponent({
  components: {
    CKEditor: CKEditor.component,
  },
  props: {
    modelValue: {
      type: String,
      default: '',
    },
    config: {
      type: Object,
      default: () => ({} as any),
    },
    placeholder: String,
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const defaultToolbar = [
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'blockQuote',
      'undo',
      'redo',
    ];

    const editorConfig = computed(() => {
      const _data: any = {};

      _data.toolbar = !props.config?.toolbar ? defaultToolbar : props.config.toolbar;

      _data.placeholder = !props.placeholder ? 'Type here...' : props.placeholder;

      _data.link = {
        addTargetToExternalLinks: true,
        defaultProtocol: 'https://',
      };

      return _data;
    });

    /** Typing in editor, update parent value */
    const onInput = (value: string | any) => {
      if (typeof value !== 'string') return;

      emit('update:modelValue', value);
    };

    return {
      editor: InlineEditor,
      editorConfig,
      onInput,
    };
  },
});
