import gql from 'graphql-tag';

export const CalendarGQL = {
  fragments: {
    CalendarFullFragment: gql`
      fragment CalendarFullFragment on Calendar {
        id
        userId
        name
        show
        color
        isDefault
        context
        permissions {
          admin {
            all
            read
          }
          global {
            all
            read
          }
          individual {
            userId
            all
            read
          }
        }
      }
    `,
    CalendarWithoutPermissionsFragment: gql`
      fragment CalendarWithoutPermissionsFragment on Calendar {
        id
        userId
        name
        show
        color
        isDefault
        context
      }
    `,
  },
};
