import { isArrayAndHasItems } from '@/utils/functions';

type Action = 'read' | 'all' | 'quit';

interface BackendPermission {
  read: boolean;
  create: boolean;
  edit: boolean;
  delete: boolean;
  all: boolean;
  userIds: readonly number[];
}

interface BackendPermissions {
  read: Partial<BackendPermission>;
  all: Partial<BackendPermission>;
  quit: Partial<BackendPermission>;
}

export function generateBackendNotation(action: Action, ...userIds: number[]) {
  const permissions: BackendPermissions = {
    read: {
      read: true,
      create: false,
      edit: false,
      delete: false,
    },
    all: { all: true },
    quit: { all: false },
  };

  const permission = permissions[action];

  if (isArrayAndHasItems(userIds)) {
    permission.userIds = Object.freeze([...userIds!]);
  }

  return Object.freeze(permission);
}
