import axios, { AxiosRequestConfig } from 'axios';
import { print } from 'graphql/language/printer';
import { globalStore } from '@/composables/use-global';

function createInstance(config: AxiosRequestConfig) {
  /** Axios instance */
  const _axios = axios.create(config);
  const { isLoading, setLoading, awaitLoading } = globalStore;

  const GRAPHQL_PROPS = ['query', 'mutation'];

  _axios.setBaseURL = url => {
    _axios.defaults.baseURL = url;
  };

  // Define custom functions
  _axios.setToken = value => {
    // Set Axios headers
    Object.keys(value).forEach(header => {
      _axios.defaults.headers.common[header] = value[header];
    });
  };

  _axios.getToken = (...headers) => {
    if (headers.length === 1) {
      return _axios.defaults.headers.common[headers[0]] || null;
    }

    const headersObject = headers.reduce((obj, header) => {
      if (!_axios.defaults.headers.common[header]) {
        return obj;
      }

      return { ...obj, [header]: _axios.defaults.headers.common[header] };
    }, {});

    if (!Object.keys(headersObject).length) {
      return null;
    }

    return headersObject;
  };

  _axios.graphql = data => _axios.post('/graphql', { ...data, isGraphql: true });

  /** Axios intercept request */
  _axios.interceptors.request.use(
    function (config) {
      // Show loading
      !isLoading.value && setLoading(true);

      // Using "graphql" method
      if (config.data.isGraphql) {
        delete config.data.isGraphql;

        for (const prop in config.data) {
          // Format query and mutation
          if (GRAPHQL_PROPS.includes(prop)) {
            config.data['query'] = print(config.data[prop]);
          }
        }
      }
      // Do something before request is sent
      return config;
    },
    function (error) {
      // Hide loading
      isLoading.value && setLoading(false);
      // Do something with request error
      return Promise.reject(error);
    },
  );

  // Add a response interceptor
  _axios.interceptors.response.use(
    function (response) {
      // Hide loading
      if (isLoading.value && !awaitLoading.value) {
        setLoading(false);
      }
      // isLoading.value && setLoading(false);
      // Do something with response data
      return response;
    },
    function (error) {
      // Hide loading
      isLoading.value && setLoading(false);
      // Do something with response error
      return Promise.reject(error);
    },
  );

  return _axios;
}

export default createInstance;
